import axios from 'axios';
import { config } from '../../../config/config';

const baseUrl = config.baseUrl;

const API = axios.create({ baseURL: config.baseUrl });

API.interceptors.request.use((req) => {

    if (sessionStorage.getItem('token')) {
        req.headers.authorization = `Bearer ${sessionStorage.getItem('token')}`
    }

    return req;
})

export const handleStatus = async (changeStatus) => {

    try {
        return await API.patch(`/account/${sessionStorage.getItem('accountname')}/agent/${sessionStorage.getItem('agentId')}/status`,
            { status: changeStatus }
        )
    } catch (err) {
        console.log(err);
    }
}

export const conversationList = async (id) => {

    try {
        return await API.get(`/account/${sessionStorage.getItem('accountname')}/agent/${id}/conversations?status=open`,

        )
    } catch (err) {
        console.log(err);
    }
}

export const messageList = async (conversationId) => {

    const convoDetails = {
        agentid: sessionStorage.getItem('agentId'),
        conversationid: conversationId,
        accountname: sessionStorage.getItem('accountname'),
    }
    try {
        return await axios.post(`/account/conversations/messages`,
            convoDetails,
        )
    } catch (err) {
        console.log(err);
    }
}

export const newSentMessage = async (newMsg, conversationId) => {

    const sendingDetails = {
        conversationid: conversationId,
        message: newMsg,
        accountname: sessionStorage.getItem('accountname'),
    }
    try {
        return await API.post(`/sendAgentMessage`,
            sendingDetails,

        )
    } catch (err) {
        console.log(err);
    }
}

export const signIn = async (loginData) => {

    try {
        return await API.post(`/signin`,
            {
                email: loginData.email,
                password: loginData.password,
                accountname: loginData.accountname,
            },

        )
    } catch (err) {
        if (err.response !== undefined) {
            if (err.response.status === 500 && err.response !== undefined) {
                alert('account does not exist')
            } else if (err.response.status === 400 && err.response) {
                alert(err.response.data.error)
            }
        }
        console.log(err);
    }
}

