export const agentStatus = async (agentId, resolve) => {

    const body = {
        status: 'offline',
        isSignOut: true,
        resolveConversation: resolve
    }
    try {

        return await API.post(`/changeAgentStatus/${agentId}`,
            body,

        )
    } catch (err) {
        console.log(err);
    }
}

