const startDeviceContext = React.createContext()

export function useStartDevice() {
    return useContext(startDeviceContext)
}

export function DeviceProvider({ children }) {
    const toggleTime = useTime();
    const outbound = useOutboundCall();
    const [startDevice, setStartDevice] = useState(false);
    const [token, setToken] = useState('');
    const [clientName, setClientName] = useState('');
    const [callerNmber, setCallerNmber] = useState(0);
    const [workerSid, setWorkerSid] = useState("");
    const [workerToken, setWorkerToken] = useState("");
    const [clientToken, setClientToken] = useState("");
    const [pickCall, setPickCall] = useState(false);
    const [number, setNumber] = useState("");
    const [newDevice, setNewDevice] = useState(null);
    const [conn, setConn] = useState();
    const [incommingNumber, setIncommingNumber] = useState();
    const [taskSid, setTaskSid] = useState();
    const [isDeviceReady, setIsDeviceReady] = useState(false);
    const [ticketID, setTicketID] = useState('');
    const [category, setCategory] = useState('');
    const [subCategory, setSubCategory] = useState('');
    const [reservationSid, setReservationSid] = useState('');
    const [show, setShow] = useState(false);
    const [showMod, setShowMod] = useState(false);
    const [newWorkerClient, setNewWorkerClient] = useState();
    const [clearTicket, setClearTicket] = useState(false);
    const [connected, setConnected] = useState(false);
    const [open, setOpen] = useState(false);
    const [openResolve, setOpenResolve] = useState(false);
    const [disableOutbound, setDisableOutbound] = useState(false);
    const [disableInbound, setDisableInbound] = useState(false);
    const [renderWorker, setRenderWorker] = useState();
    const [workerOfflineSid, setWorkerOfflineSid] = useState();
    const [workerOnlineSid, setWorkerOnlineSid] = useState();
    const [isWrapping, setIsWrapping] = useState(false);
    const [isAnswered, setIsAnswered] = useState(false);


    // const [isTaskCompleted, setIsTaskCompleted] = useState(false);

    let device;
    const inputData = useRef();
    let workerClient;
    let worker;
    let workerActivities = {};
    let acceptBtn;
    const corporate = sessionStorage.getItem('corporate')


    const startupClient = () => {
        axios.get('http://localhost:3000/token').then((res) => {
            console.log(res.data.token);
            setToken(res.data.token);
            intitializeDevice();


        }).catch((err) => console.log(err))
        //   setClientName(data.identity);
    }
    // Instantiate a new Twilio.Device
    // const intitializeDevice = () => {

    const handleClose = () => setShow(false);
    const handleCloseMod = () => setShowMod(false);
    const handleShowMod = () => {
        setShowMod(true);
    }
    const handleShow = () => {
        setShow(true);
    }

    function fetchActivities() {
        workerClient.activities.fetch(function (error, activityList) {
            console.log(activityList, 'activity list');
            for (let activity of activityList.data) {
                if (activity.friendlyName === "Offline") {
                    workerActivities.offline = activity.sid;
                    setWorkerOfflineSid(workerActivities.offline)
                    console.log(workerActivities.offline, 'worker ofline');
                } else if (activity.friendlyName === "Available")
                    workerActivities.available = activity.sid;
                setWorkerOnlineSid(workerActivities.available)
                console.log(workerActivities.available, 'worker available');
            }
        });
    }


    function toggleWorkerAvailability() {
        console.log(renderWorker.available, 'worker available');
        console.log(workerActivities, 'inside toggle worker activity');
        let newStatus = {
            ActivitySid: renderWorker.available
                ? workerOfflineSid
                : workerOnlineSid,
        };
        console.log(newStatus, 'newStatus');
        newWorkerClient.update(newStatus, (error, workerInfo) => {
            worker = workerInfo;
            setRenderWorker(workerInfo);
        });
        if (renderWorker.available) {
            setConnected(false)
        } else {
            setConnected(true)
        }
    }


    const acceptReservation = (reservationSid) => {
        workerClient.fetchReservations(function (error, reservations) {
            for (let reservation of reservations.data) {
                if (reservation.sid === reservationSid) {
                    if (reservation.task.attributes.conference) {
                        // There is already a conference, so it's likely a transfer
                        console.log(
                            "Joining conference " +
                            reservation.task.attributes.conference.room_name
                        );
                        reservation.call(
                            null,
                            "https://amber-ibis-1382.twil.io/join-conference?conferenceRoomName=" +
                            reservation.task.attributes.conference.room_name,
                            null,
                            true
                        );
                    } else {
                        reservation.conference();
                    }
                    break;
                }
            }
        });
    }

    const rejectReservation = (reservationSid) => {
        console.log(reservationSid, 'reservationSid');
        newWorkerClient.fetchReservations(function (error, reservations) {
            for (let reservation of reservations.data) {
                if (reservation.sid === reservationSid) {
                    reservation.reject();
                    setStartDevice(false);
                    toggleTime.setCurrentStatus('');
                    setIncommingNumber(undefined);
                    setTicketID('')
                    setCategory('')
                    setSubCategory('')
                    break;
                }
            }
        });
    }

    const transferCall = (taskSid, workspaceSid) => {
        fetch(`/transfer?task_sid=${taskSid}&workspace=${workspaceSid}&workerName=${result.value}`)
            .then(() => {
                updateReservations();
            });
    }

    const renderReservation = (reservation) => {
        let result = "";
        switch (reservation.reservationStatus) {
            case "pending":
                console.log('pending');
                // result = `<button type="button" class="btn btn-primary float-right" onclick="this.disabled = true; this.textContent = 'Accepting...'; acceptReservation('${reservation.sid}')">Accept</button>`;
                // acceptReservation(reservation.sid);
                setReservationSid(reservation.sid)
                // if (reservation.sid) {
                // }

                break;
            case "accepted":
                toggleTime.setIsResAccepted(true);
                console.log('accepted');
                break;
            case "rejected":

                // setTicketID('');
                // setCategory('');
                console.log('rejected');
                break;
            case "canceled":
                console.log('cancled');
                break;
        }
        switch (reservation.task.assignmentStatus) {
            case "assigned":
            // result += `<button type="button" class="btn btn-info float-right ml-2" onclick="this.disabled = true; transferCall('${reservation.task.sid}', '${reservation.task.workspaceSid}')">Transfer Call</button>`;
            case "wrapping":
                console.log(reservation.task.sid, 'reservation task sid');
                setTaskSid(reservation.task.sid)
                break;
            case "completed":
                result = "";
                break;
        }
        return result;
    }

    const completeTask = async (taskSid) => {
        // debugger
        console.log('inside complete task', taskSid);
        const tskComp = await taskComplete(taskSid)
        // fetch(`http://localhost:8000/complete-task?taskSid=${taskSid}`).then(() => {
        //     console.log("inside fetch");

        //     updateReservations();
        // });
        updateReservations();
        console.log("complete task initiated", tskComp);
    }

    const onInputData = (e) => {
        setWorkerSid(e.target.value)
    }

    const registerWorker = async (workerToken) => {
        console.log(Twilio, 'Twilio');
        console.log('workerToken', workerToken);
        // const workerRes = await register(workerToken);
        // console.log(workerRes.data);
        // workerClient = new Twilio.TaskRouter.Worker(workerRes.data);
        workerClient = new Twilio.TaskRouter.Worker(workerToken);
        console.log(workerClient);
        setNewWorkerClient(workerClient)
        workerClient.on("ready", function (workerInfo) {
            fetchActivities();
            worker = workerInfo;
            setRenderWorker(workerInfo);
            console.log(worker, 'after ready worker info');
            if (
                workerInfo.attributes.contact_uri &&
                workerInfo.attributes.contact_uri.startsWith("client:")
            ) {
                console.log('client token', workerInfo.attributes.contact_uri.slice(7));
                // registerTwilioDevice(workerInfo.attributes.contact_uri.slice(7));
                registerTwilioDevice(sessionStorage.getItem('clientToken'))
            } else {
                console.warn(
                    'Twilio Audio device not enabled. The worker "contact_uri" is either missing or not in the form "client:xxxx"'
                );
            }
        });
        workerClient.on("reservation.created", function (reservation) {
            console.log("reservation created");
            handleShow()
            setStartDevice(!startDevice);
            updateReservations();
        });

        workerClient.on("reservation.rejected", function (reservation) {
            console.log("reservation rejected");
            // updateReservations()
            setClearTicket(true);
            setTicketID('');
            setCategory('');
            setSubCategory('')
            console.log(clearTicket);
        })

        workerClient.on("reservation.accepted", function (reservation) {
            console.log("reservation accepted");
            updateReservations();
        });

        workerClient.on("reservation.canceled", function (reservation) {
            console.log("reservation cancled");
            // toggleTime.resetState()
            setStartDevice(false);
            toggleTime.setCurrentStatus('');
            setIncommingNumber(undefined);
            handleClose()
            setTicketID('')
            setCategory('')
            setSubCategory('')
        });
        workerClient.on("disconnected", function (reservation) {
            setConnected(false);
            console.log(connected, 'reservation disconnected');
        });
        workerClient.on("connected", function (reservation) {
            setConnected(true);
            console.log(connected, 'reservation connected');
        })
    }

    const registerTwilioDevice = (clientToken) => {
        // const clientRes = await clientRegister(clientToken);
        // setClientToken(clientRes.data.token)
        // console.log("client token", clientRes.data.token);
        // device = new Twilio.Device(clientRes.data.token, {
        console.log('clientToken', clientToken);
        device = new Twilio.Device(clientToken, {
            codecPreferences: ["opus", "pcmu"],
            enableRingingState: true,
            answerOnBridge: true,
            debug: true
        });
        console.log(device);
        setNewDevice(device)
        device.on("ready", function (device) {
            setOpen(true);
            console.log("Twilio.Device Ready!");
            if (device) {
                console.log('loading');
                setIsDeviceReady(true);
            }
            // document.getElementById("button-hangup").onclick = function () {
            //     console.log("Hanging up...");
            //     if (device) {
            //         device.disconnectAll();
            //     }
            // };
        });

        device.on("error", function (error) {
            console.log("Twilio.Device Error: " + error.message);
            if (error.message === "JWT Token Expired" || "Cannot register. Token not validated") {
                sessionStorage.clear();
                window.location.href = `/${corporate}`;
            }
        });

        device.on("connect", function (conn) {
            console.log(conn, "Successfully established call!");
            // setCallSid(conn.parameters.CallSid)
            // setStartDevice(!startDevice);
            // console.log(conn);

        });
        device.on("incoming", function (conn) {
            // setStartDevice(!startDevice);
            console.log("Incoming connection from " + conn.parameters.From);
            console.log("Incoming connection from " + conn);
            setConn(conn);
            toggleTime.setCurrentStatus('incommingCall')
            if (conn) {
                console.log(conn, "conn answered call");
                conn.accept();
                toggleTime.acceptIncomming();
            }
            // document.getElementById("button-answer").onclick = () => {
            //     conn.accept();
            //     toggleTime.toggle();
            //     setPickCall(true);
            // };
            // callAccept(conn);

        });
        device.on("disconnect", function (conn) {
            console.log(conn, 'connection');
            toggleTime.reset();
            console.log(toggleTime.isInbound, 'is inbound in disconnect');
            if (conn._direction === "OUTGOING") {
                handleShowMod()
                setDisableInbound(false);
            }
            setIsWrapping(true)
            console.log('disconnected');
        })


        device.on("reject", function () {
            console.log('rejected');
        })
    }

    // if (toggleTime.isInbound && newDevice !== null) {
    //     newDevice.on("disconnect", async function (conn) {
    //         console.log(toggleTime.isInbound, 'isInbound');
    //         toggleTime.reset();
    //         console.log('disconnected');
    //     });
    // } else if (toggleTime.isInbound === false && newDevice !== null) {
    //     newDevice.on("disconnect", async function (conn) {
    //         toggleTime.reset();
    //         handleShowMod()
    //         console.log('disconnected');
    //     });
    // }

    const handleCall = async () => {
        var params = {
            To: number,
        };
        console.log(`Attempting to call ${params.To} ...`);
        console.log(newDevice);
        console.log(device);
        // toggleTime.outgoingCall();
        if (newDevice) {
            console.log("Numb||", number)
            // debugger
            console.log(newDevice);
            const call = await newDevice.connect({ To: number });
            console.log(call);
            handeloutgoingCall()
        } else {
            console.log('unable to make call');
            console.log(toggleTime.currentStatus, 'current status');
        }
    };


    const handleSubmit = async (e) => {
        e.preventDefault();
        // registerTwilioDevice("WK56051f17ce6b9b284f8e1ecfd8b47a01");
        registerWorker("WK1a5f3e3e24b716c13867281f74815ee8");
    }

    const openInboundCall = () => {
        toggleTime.setCurrentStatus('incommingCall');
        toggleTime.setIsInbound(true)
        outbound.setSelectedNumberId('')

    }


    const updateReservations = () => {
        workerClient.fetchReservations(function (error, reservations) {
            reservations.data.forEach((reservation) => {
                if (reservation.reservationStatus !== "timeout") {
                    console.log("reservation sid", reservation.sid);
                    console.log("reservation status", reservation.reservationStatus);
                    console.log("task status", reservation.task.assignmentStatus);
                    console.log("task", reservation.task);
                    renderReservation(reservation);
                    setIncommingNumber(reservation.task.attributes.caller);
                    setTicketID(reservation.task.attributes.ticket_id);
                    const categoryAndSubCategory = reservation.task.attributes.category.split(':');
                    setCategory(categoryAndSubCategory[0]);
                    setSubCategory(categoryAndSubCategory[1])
                }
            });
        });
    }

    const hangup = () => {
        if (newDevice) {
            newDevice.disconnectAll();
            handleDialPad()
        }
    }
    console.log("ticket id", ticketID, category, subCategory)

    const handleHangup = async () => {

        if (newDevice) {
            newDevice.disconnectAll()
            toggleTime.reset()
            setIsWrapping(true)
            setIsAnswered(false)

        }
    }

   

    const value = {
        startDevice,
        conn,
        newDevice,
        incommingNumber,
        taskSid,
        isDeviceReady,
        ticketID,
        category,
        subCategory,
        reservationSid,
        disableInbound,
        newWorkerClient,
        renderWorker,
        clearTicket,
        disableOutbound,
        openResolve,
        connected,
        isWrapping,
        isAnswered,
        // openBotConvo,
        open,
        setIsWrapping,
        openInboundCall,
        setIsAnswered,
        // setOpenBotConvo,
        handleHangup,
        // getPreviousCallHistory,
        setDisableOutbound,
        setDisableInbound,
        setOpenResolve,
        setConnected,
        setIncommingNumber,
        toggleWorkerAvailability,
        setTicketID,
        setCategory,
        setSubCategory,
        setStartDevice,
        registerWorker,
        hangup,
        updateReservations
    }

    return (
        <div>
            <startDeviceContext.Provider value={value}>
                {children}
            </startDeviceContext.Provider>
            {/* <Phone /> */}
            {toggleTime.isInbound && <Reservations setIsAnswered={setIsAnswered} setDisableOutbound={setDisableOutbound} show={show} openInboundCall={openInboundCall} handleShow={handleShow} handleClose={handleClose} rejectReservation={rejectReservation} reservationSid={reservationSid} newWorkerClient={newWorkerClient} />
            }
        </div>
    )
}

