import React, { useState, useEffect } from 'react'
import { BrowserRouter as Router, Link, Route, Switch } from 'react-router-dom';
import { Navbar, Container, Nav, NavDropdown, Row, Col, Tab, Tabs } from 'react-bootstrap'
import { useHistory } from 'react-router';
import './TwilioCall.css'
import InboundCall from './InboundCall';
import TwilioHeader from './header/TwilioHeader';
import { useStartDevice } from '../../context/startDevice/startDeviceContext';
import AdditionalDetails from './additionalDetails/AdditionalDetails';
import OutboundCall from './OutboundCall';
import OutboundCallList from './callList/OutboundCallList';
import ResolveNavbar from './resolveNavbar/ResolveNavbar';
import { useTime } from '../../context/callTime/calltimeContext';
import InboundcallList from './callList/InboundcallList';
import { ToastContainer } from 'react-toastify';
import { useOutboundCall } from '../../context/outboundCall/outboundCallContext';
import BackDrop from '../../chatComponents/backDrop/BackDrop';
import { useMediaQuery } from 'react-responsive';
import NoCallsToLoad from '../../chatComponents/extras/NoCallsToLoad';
// import InboundCall from './InboundCall';

const TwilioCall = () => {
    const [corporate, setCorporate] = useState();
    const currentPage = useTime();
    const isDesktopOrLaptop = useMediaQuery({
        minWidth: "800px"
    });
    const outboundDetails = useOutboundCall();
    const History = useHistory()
    const [isWrapping, setIsWrapping] = useState(false);
    const [assign, setAssign] = useState(true)
    const INBOUND_Call = '/inbound'
    const OUTBOUND_CALL = '/outbound'
    const [activeKey, setActiveKey] = useState()
    const startTwilio = useStartDevice();
    const [openDrawer, setOpenDrawer] = useState(false);
    const [openProps, setOpenProps] = useState(false);



    let drawerClasses = 'd-flex flex-column side-call-drawer';
    if (openDrawer) {
        drawerClasses = 'd-flex flex-column side-call-drawer open'
    }

    const drawerClickToggleHandler = () => {
        setOpenDrawer(!openDrawer)
    }
    const propDrawerToggle = () => {
        setOpenProps(!openProps)
    }
    const backDropClickHandler = () => {
        setOpenDrawer(false);
        setOpenProps(false);
    }

    useEffect(() => {
        setCorporate(sessionStorage.getItem('corporate'))
    }, [])



    return (
        <div>
            <Router>
                <div>
                    <Route render={({ location, history }) => (
                        <div className="layout">
                            {/* <aside> */}
                            <div className={isDesktopOrLaptop ? 'd-flex flex-column' : drawerClasses}>
                                <TwilioHeader />
                                <Tab.Container
                                    defaultActiveKey={INBOUND_Call}
                                    activeKey={activeKey}
                                    onSelect={(selected) => {
                                        const to = `/${corporate}/twilioDashboard` + selected;
                                        if (location.pathname !== to) {
                                            history.push({
                                                pathname: to,
                                                isInbound: true
                                            });
                                        }
                                        setActiveKey()
                                    }}
                                >

                                    <Nav variant="" className="justify-content-left nav-change">
                                        <Nav.Item className="nav-change" >
                                            {startTwilio.disableInbound || startTwilio.disableOutbound ? <Nav.Link className="nav-text-change" disabled eventKey={INBOUND_Call} onClick={startTwilio.openInboundCall}>Inbound Call</Nav.Link>
                                                :
                                                <Nav.Link className="nav-text-change" eventKey={INBOUND_Call} onClick={startTwilio.openInboundCall}>Inbound Call</Nav.Link>
                                            }
                                        </Nav.Item>
                                        <Nav.Item className="nav-change" >
                                            {startTwilio.disableOutbound || startTwilio.disableInbound ? <Nav.Link className="nav-text-change" disabled eventKey={OUTBOUND_CALL} onClick={outboundDetails.openOutboundCall}>
                                                Outbound Call
                                                {/* <FontAwesomeIcon icon={faRedo} /> */}
                                            </Nav.Link>
                                                :
                                                <Nav.Link className="nav-text-change" eventKey={OUTBOUND_CALL} onClick={outboundDetails.openOutboundCall}>
                                                    Outbound Call
                                                    {/* <FontAwesomeIcon icon={faRedo} /> */}
                                                </Nav.Link>
                                            }
                                        </Nav.Item>
                                    </Nav>
                                    <Tab.Content className="border-right overflow-auto flex-grow-1" style={{ background: 'var(--primary-background-2' }}>
                                        <Tab.Pane eventKey={INBOUND_Call}>
                                            {startTwilio.incommingNumber !== undefined ? <InboundcallList /> :
                                                <NoCallsToLoad />}
                                        </Tab.Pane>
                                        <Tab.Pane eventKey={OUTBOUND_CALL}>
                                            <OutboundCallList />
                                        </Tab.Pane>
                                    </Tab.Content>
                                </Tab.Container>
                            </div>

                            <div style={{ background: 'var(--secondary-background)' }}>
                                <ResolveNavbar propDrawerToggle={propDrawerToggle} drawerClickToggleHandler={drawerClickToggleHandler} />
                                <Switch>
                                    <Route path="/:corporate/twilioDashboard/inbound" exact component={props => <InboundCall />} />
                                    <Route path="/:corporate/twilioDashboard/outbound" exact component={props => <OutboundCall />} />
                                </Switch>
                                {(openDrawer || openProps) && <BackDrop backDropClickHandler={backDropClickHandler} />}
                            </div>
                            <AdditionalDetails openProps={openProps} />
                            <ToastContainer autoClose={2000} position="top-center" />
                        </div>
                    )}
                    />
                </div>
            </Router>

        </div>
    )
}

export default TwilioCall
