import React from 'react'
import { useChat } from '../../../../context/chat/chatContext';
import { ListGroup } from 'react-bootstrap'
import '../../../../chatComponents/chatList/ChatList.css'
import AgentConvo from '../singleChat/AgentConvo';
import Chats from '../../../../chatComponents/chatList/Chats';

const AgentsConvoList = () => {
    const chat = useChat();
    return (
        <ListGroup variant="flush" style={{ height: '90%' }}>
            {chat.agentConversationList.map((conversation, index) => (
                <div
                    key={index}
                    style={{ background: 'var(--primary-background-2' }}
                >
                    {<Chats key={conversation.conversation.id} user={conversation} index={index} />}
                </div>
            ))}
        </ListGroup>
    )
}

export default AgentsConvoList
