import React from 'react';
import '../../../chatComponents/chatList/ChatList.css';
import dialer from '../../../../assets/images/endUser.png'
import { useStartDevice } from '../../../context/startDevice/startDeviceContext';

const IncommingCall = ({ incommingNumber }) => {
    const getNumber = useStartDevice();
    const time = new Date().toLocaleTimeString();
    return (
        <div
            className="conversation"
        >

            <img src={dialer} alt="img" />
            {/* {user.conversation.additionalAttributes.channelType === "WEBCHAT" ? <img src={WEB} alt="" /> : user.conversation.additionalAttributes.channelType === "WHATSAPP" ? <img src={whatsApp} alt="" /> : user.conversation.additionalAttributes.channelType === "FACEBOOK" ? <img src={facebook} alt="" /> : user.conversation.additionalAttributes.channelType === "TWITTER" ? <img src={twitter} alt="" /> : <img src={endUser} alt="" />} */}

            <div
                className="title-text"
            // className={isNewConversations(newConversationId, user.conversation.id) ? 'title-text-unread' : 'title-text'}
            >
            </div>

            <div
                className="created-date"
            // className={isNewConversations(newConversationId, user.conversation.id) ? 'new-created-date' : "created-date"}
            >
                {time}
                {/* {convertUTCDateToLocalDateV2(user.conversation.createdAt)} */}
            </div>
            <div
                className="conversation-message"
            // className={isNewConversations(newConversationId, user.conversation.id) || isNewMessage(newMsgConvoId, user.conversation.id) ? 'new-conversation-message' : 'conversation-message'}
            >
                {getNumber.incommingNumber}
            </div>



        </div >
    )
}

export default IncommingCall
