export default DataPacket;



const removetext = (data) => {

    if (typeof data === "string") {
        let editedtext = data.replace("__TRANSLITERATE__::", "");
        editedtext = editedtext.replace("<notranslate>", "");
        editedtext = editedtext.replace("</notranslate>", "");
        editedtext = editedtext.replace("__NO_TRANSLITERATE__::", "");
        editedtext = editedtext.replace("__NO_TRANSLITERATE::", "");
        editedtext = editedtext.replace("<reverse_transliterate>", "")
        editedtext = editedtext.replace("</reverse_transliterate>", "")
        editedtext = editedtext.replace("__TRANSLITERATION_TRANSLATE__::", "")

        return maketextbold(replaceAngleBrackets(editedtext));
    } else {
        return data
    }

}
export const maketextbold = (input) => {
    let res = input.replace(/\*(.*?)\*/gm, function (str) {
        str = "<strong>" + str.replace(/\*/g, "") + "</strong>"

        return str

    })

    return interpretnewline(res);

}
export const interpretnewline = (str) => {
    return str.replace(/\n/g, "<br>");

}
export const replaceAngleBrackets = (editedtext) => {
    editedtext = editedtext.replace(/</g, "&lt;")
    editedtext = editedtext.replace(/>/g, "&gt;")
    return editedtext
}

const Packetv2 = (props) => {
    
    const replaceXMLText = (str) => {
        if (typeof str === "string" && typeof str !== "object") {

            let tags = {
                "<strong>": "*",
                "</strong>": "*",
                "<italic>": "*",
                "</italic>": "*",
                "<newline>": "\n"
            }
            for (let key in tags) {
                let regex = new RegExp(key, "ig");
                str = str.replace(regex, tags[key])
            }
            return str;
        }
        else {
            return str
        }
    }
    if (props.channelType === "FACEBOOK") {
        return (
            <FaceBookDataPacket messageData={props.data} ></FaceBookDataPacket>
        );
    }
    if (props.channelType === "GOOGLE_MESSAGES") {
        return (
            <GoogleMessageDataPacket messageData={props.data} ></GoogleMessageDataPacket>
        );
    }
    // if (props.channelType === "FACEBOOK") {
    //     return (
    //         <FaceBookDataPacket messageData={props.data} ></FaceBookDataPacket>
    //     );
    // }
    const handleSubmit = (type, data) => {
        // if (props.onSubmit && typeof props.onSubmit == 'function') {
        //     props.onSubmit(type, data)
        // }
    }

    if (typeof props?.data === "string") {
        return (
            <SimpleText themeColor={props.themeColor} data={replaceXMLText(props.data)} ></SimpleText>
        )
    } else if (props?.data.hasOwnProperty("text") && !props?.data.hasOwnProperty("type")) {
        return (
            <SimpleText themeColor={props.themeColor} data={replaceXMLText(props.data)} ></SimpleText>
        )
    }
    switch (props.data.type) {
        case 'TEXT':
        case 'HEADER1':
        case 'HEADER2':
        case 'HEADER3':
        case 'HEADER4':
        case 'HEADER5':
        case 'HEADER6':
        case 'PARA1':
        case 'PARA2':
        case 'PARA3':
        case 'PARA4':
        case 'PARA5':
        case 'PARA6':
        case 'FOOTER1':
        case 'FOOTER2':
        case 'FOOTER3':
        case "HSEPARATOR":
        case "CALL":
        case "L1_AGENT":
        case "IMAGE_TYPE1":
        case 'HORZ_IMAGES_TYPE1':
        case 'VERT_IMAGES_TYPE1':
        case 'POSTAL_ADDRESS':
        case 'TABLE':
        case 'HYPERLINK':
        case "IMAGE_ARRAY":
        case "VIDEO":
        case "AUDIO":
        case "NAVIGATE":
        case 'PIE_CHART':
        case 'STACKED_BAR_CHART':
        case 'BAR_CHART':
        case 'LINE_CHART':
            return (
                <PrimitivePacket themeColor={props.themeColor} data={props.data} ></PrimitivePacket>
            );
        default:
    }
    return (
        <NonPrimitivePacket readOnly={props.readOnly} themeColor={props.themeColor} data={props.data} onSubmit={handleSubmit}></NonPrimitivePacket>
    );
}

