const NonPrimitivePacket = (props) => {
    const handleSubmit = (type, data) => {
        if (props.onSubmit && typeof props.onSubmit == 'function') {
            props.onSubmit(type, data)
        }
    }

    switch (props.data.type) {
        case "CARD_TYPE1":
            return (
                <CardType1 readOnly={props.readOnly} themeColor={props.themeColor} data={props.data} onSubmit={handleSubmit}></CardType1>
            )
        case "HORZ_CARDS_TYPE1":
            return (
                <HorzCardsType1 readOnly={props.readOnly} themeColor={props.themeColor} data={props.data} onSubmit={handleSubmit}></HorzCardsType1>
            )
        case 'VERT_CARDS_TYPE1':
            return (
                <VertCardsType1 readOnly={props.readOnly} themeColor={props.themeColor} data={props.data} onSubmit={handleSubmit}></VertCardsType1>
            )
        case 'OPTIONS':
            return (
                <OptionsPacket readOnly={props.readOnly} themeColor={props.themeColor} data={props.data} onSubmit={handleSubmit}></OptionsPacket>
            )
        case 'FORM':
            return (
                <FormPacket readOnly={props.readOnly} themeColor={props.themeColor} data={props.data} onSubmit={handleSubmit}></FormPacket>
            )
        case 'FILLED_FORM':
            return (
                <FilledFormPacket readOnly={props.readOnly} themeColor={props.themeColor} data={props.data} onSubmit={handleSubmit}></FilledFormPacket>
            )
        case 'SENSE':
            return (
                <SensePacket readOnly={props.readOnly} themeColor={props.themeColor} data={props.data} onSubmit={handleSubmit}></SensePacket>
            )
        case 'SENSE_SUGGESTION':
            return (
                <SensePacketView readOnly={props.readOnly} themeColor={props.themeColor} data={props.data} onSubmit={handleSubmit}></SensePacketView>
            )
        default:
    }
    return (
        <p>Yet to support</p>
    );

}

const ReplaceNewLine = (str) => {
    if (typeof str !== "string") {
        return ""
    } else {
        return str.toString().replace(new RegExp("\\n", "g"), "<br>")
    }

}
const replaceXMLText = (str) => {
    if (typeof str === "string" && typeof str !== "object") {

        let tags = {
            "<strong>": "*",
            "</strong>": "*",
            "<italic>": "*",
            "</italic>": "*",
            "<newline>": "\n",
            "<br>": "\n",
        }
        for (let key in tags) {
            let regex = new RegExp(key, "ig");
            str = str.replace(regex, tags[key])
        }
        return str;
    }
    else {
        return str
    }
}
const SimpleText = (props) => {
    const data = replaceXMLText(props.data.data || props.data.text || props.data)
    return (
        <p className="SimpleText" dangerouslySetInnerHTML={{ "__html": removetext(data) }}>
        </p>
    )
}

