const HorzCardsType1 = (props) => {
    let myGalleryScroller = null;
    let leftButton = null;
    let rightButton = null;

    useEffect(() => {
        // console.log("Initializing the scroll buttons")
        showScrollButtons()
    })


    const scrollTo = (e, amount) => {
        let pos = 0;
        let children = myGalleryScroller.querySelectorAll(":scope > .gallery_scroller_item");
        children.forEach((child) => {
            if (child.offsetLeft < myGalleryScroller.scrollLeft + (myGalleryScroller.offsetWidth / 2)) {
                pos = child.offsetWidth;
            }
        });
        if (amount < 0) {
            pos = myGalleryScroller.scrollLeft - pos;
        }
        else {
            pos = myGalleryScroller.scrollLeft + pos;
        }
        myGalleryScroller.scrollTo({
            left: pos,
            behavior: "smooth"
        })
    }

    const onScrollCards = (e) => {
        showScrollButtons();
    }

    const showScrollButtons = () => {
        if (leftButton) {
            leftButton.style.display = (myGalleryScroller.scrollLeft < 10) ? "none" : "flex";
        }
        if (rightButton) {
            rightButton.style.display = myGalleryScroller.scrollLeft > (myGalleryScroller.scrollWidth - myGalleryScroller.clientWidth - 10) ? "none" : "flex"
        }
    }


    const handleSubmit = (type, data) => {
        if (props.onSubmit && typeof props.onSubmit == 'function') {
            props.onSubmit(type, data)
        }
    }

    return (
        <div style={{ width: "100%", display: "flex", flexDirection: "column" }}>
            {
                props.data.data.TITLE !== "" ?
                    <div style={{ width: "100%", margin: '6px' }}>
                        <span style={{ margin: '0px' }} className='SimpleText'>
                            {props.data.data.TITLE}
                        </span>
                    </div> : null
            }
            {
                props.data.data.DESCRIPTION !== "" ?
                    < div style={{ width: "100%", margin: '6px' }}>
                        <span style={{ margin: '0px' }} className='Para4'>
                            {props.data.data.DESCRIPTION}
                        </span>
                    </div> : null
            }
            <div className="gallery">
                <div className="gallery_scroller scroller" ref={ref => myGalleryScroller = ref} onScroll={(e) => onScrollCards(e)} >
                    {
                        props.data.data.CARDS.map((card, i) => {
                            return (
                                <div key={i} className="gallery_scroller_item" style={{ marginRight: (props.data.data.CARDS.length > 1 ? 8 : 0) + 'px' }}>
                                    <NonPrimitivePacket readOnly={props.readOnly} themeColor={props.themeColor} data={card} onSubmit={handleSubmit}></NonPrimitivePacket>
                                </div>
                            )
                        })
                    }

                </div>
                <div
                    onClick={(e) => scrollTo(e, -200)} ref={ref => leftButton = ref}
                    style={{ cursor: 'pointer', position: 'absolute', top: '50%', left: '10px', height: '23px', width: '23px', borderRadius: '50%', background: '#fff9f9', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', boxShadow: 'var(--box-shadow-5)' }}
                >
                    <MdKeyboardArrowLeft />
                </div>
                <div
                    onClick={(e) => scrollTo(e, 200)} ref={ref => rightButton = ref}
                    style={{ cursor: 'pointer', position: 'absolute', top: '50%', right: '10px', height: '23px', width: '23px', borderRadius: '50%', background: '#fff9f9', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', boxShadow: 'var(--box-shadow-5)' }}
                >
                    <MdKeyboardArrowRight />
                </div>
            </div>
            <div style={{ width: "100%", display: "flex", flexDirection: "column", alignItems: "center" }} >
                {
                    props.data.data.SECTIONS.map((packet, i) => {
                        return (
                            <PrimitivePacket key={i} themeColor={props.themeColor} data={packet}></PrimitivePacket>
                        )
                    })
                }
            </div>
            <div style={{ width: "100%", display: "flex", flexDirection: "column", alignItems: "center" }} >
                {
                    props.data.data.BUTTONS.map((button, i) => {
                        return (
                            <OptionButton key={i} readOnly={props.readOnly} themeColor={props.themeColor} data={button} onSubmit={props.onSubmit}></OptionButton>
                        );
                    })
                }
            </div>
        </div >

    )
}

