const FormPacket = (props) => {
    const openForm = () => {
        if (props.onSubmit && typeof props.onSubmit == 'function') {
            props.onSubmit("FILL_FORM", props.data.data);
        }
    }

    return (
        <div>
            <div>
                <p className="SimpleText" dangerouslySetInnerHTML={{ __html: ReplaceNewLine(props.data.data.TITLE) }}>
                </p>
            </div>
            <div>
                <p className="Para4" dangerouslySetInnerHTML={{ __html: ReplaceNewLine(props.data.data.DESCRIPTION) }}>
                </p>
            </div>
            <div className="OptionButton" style={{
                backgroundColor: props.themeColor,
                pointerEvents: props.readOnly ? 'none' : 'auto',
                opacity: props.readOnly ? '0.5' : '1',
                textAlign: "center"
            }} onClick={() => openForm()} >
                Fill Form
            </div>
        </div>
    )
}

