const FilledFormPacket = (props) => {

    return (
        <div style={{ width: '100%' }}>
            {
                props.data.data.FIELDS.map((field, i) => {
                    switch (field.type) {
                        case 'TEXT':
                            return (
                                <SimpleText key={i} themeColor={props.themeColor} data={field}></SimpleText>
                            );
                        case 'POSTAL_ADDRESS':
                            return (
                                <div key={i} >
                                    <p className="Header2" dangerouslySetInnerHTML={{ __html: ReplaceNewLine(field.label) }}>

                                    </p>
                                    <PostalAddress themeColor={props.themeColor} data={field}></PostalAddress>
                                </div>
                            )
                        case 'ADDRESS':
                            return (
                                <div key={i} >
                                    <p className="Header2" dangerouslySetInnerHTML={{ __html: ReplaceNewLine(field.label) }}>

                                    </p>
                                    <GoogleAddress themeColor={props.themeColor} data={field}></GoogleAddress>
                                </div>
                            )
                        case 'PHOTO':
                            return (
                                <div key={i} >
                                    <p className="Para2" >
                                        Photo Uploaded
                                    </p>
                                </div>
                            )
                        case 'VOICE':
                            return (
                                <div>
                                    <p className="Para2" >
                                        Voice Uploaded
                                    </p>
                                </div>
                            );
                        case 'SINGLE_OPTION':
                            return (
                                <div key={i} >
                                    <p className="Header2" >{field.LABEL}</p>
                                    <p className="Para2" >{field.VALUE}</p>
                                </div>
                            )
                        default:
                            return (
                                <div>
                                    <p className="Para2" >
                                        Unknown type
                                    </p>
                                </div>
                            );

                    }
                })
            }
        </div>
    )

}
