import React, { useEffect, useState } from 'react';
import { InputGroup, Button } from 'react-bootstrap';
import { faPenSquare } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import "./WinnoProDetails.css";
const InputData = ({ refs, type, option, optionArray, ticketInfo, minLength, maxLength, handleChange, openBtn, updateDetails, propertyName, ticketInfoType, name }) => {
    const [changeSize, setChangeSize] = useState(false);
    const [moreChangeSize, setMoreChangeSize] = useState(false);
    useEffect(() => {
        if ((propertyName.length >= 13 || propertyName.length >= 12) && propertyName.length <= 18) {
            setChangeSize(true)
        } else if (propertyName.length >= 18) {
            setMoreChangeSize(true);
        }
        else {
            setMoreChangeSize(false);
            setChangeSize(false)
        }

    }, [propertyName])


    const getKeyByValue = (obj, value) => {
        return Object.keys(obj).find(key => obj[key] === value)
    }
    // console.log("ticket infooo", optionArray)
    return (
        <>
            <td id="propertyColumn" className={`${changeSize && "change-size"} ${moreChangeSize && "more-change-size"}`}>
                <div className='property-name'>
                    {propertyName + ":"}
                </div>
            </td >
            <td id="valueBox">
                <InputGroup >
                    {option ?
                        <select id="inputBox" ref={refs} onChange={(e) => handleChange(e)} value={`${ticketInfoType}`}>
                            {optionArray.map((bol, key) => <option key={key} value={bol}>{bol.toString()}</option>)}
                        </select>
                        : <input type={type} name={name} ref={refs} minLength={minLength} maxLength={maxLength} id="inputBox" defaultValue={ticketInfo === undefined ? '' : ticketInfoType} onChange={handleChange} />}
                    <InputGroup.Append style={{ marginLeft: '4px' }}>
                        {openBtn ? <Button onClick={updateDetails} size="sm" variant="success"><FontAwesomeIcon icon={faPenSquare} /></Button> : <Button onClick={updateDetails} disabled size="sm" variant="success"><FontAwesomeIcon icon={faPenSquare} /></Button>}
                    </InputGroup.Append>
                </InputGroup>
            </td>
        </>
    )

};

export default InputData;
