import './App.css';
import SideBar from './components/chatComponents/sideBar/Sidebar';
import { useState, useEffect } from 'react'
import ChatBox from './components/chatComponents/chatBox/ChatBox';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import nofile from './assets/images/file.png'
import Login from './components/pages/loginPage/Login';
import { config } from './config/config'
import { io } from 'socket.io-client'
import ClientVideo from './components/pages/clientVideo/ClientVideo';
import ClientCall from './components/pages/callingOptions/ClientCall';
import chatImg from './assets/images/chat.png'
import Loading from './components/pages/loading/Loading';
import TwilioCall from './components/pages/twilio-call/TwilioCall';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import NotFound from './components/pages/notFound/NotFound';
import { useStartDevice } from './components/context/startDevice/startDeviceContext';
import OutboundCall from './components/pages/twilio-call/OutboundCall';
import BackDrop from './components/chatComponents/backDrop/BackDrop';
import AgentProp from './components/chatComponents/agentDetails/AgentProp';
import { useMediaQuery } from 'react-responsive';
import { useOutboundCall } from './components/context/outboundCall/outboundCallContext';
import CallComplete from './components/pages/twilio-call/CallComplete';
import { useTime } from './components/context/callTime/calltimeContext';
import { updateCommonInbox } from './components/pages/twilio-call/service/api';
import { useChat } from './components/context/chat/chatContext';
import SuperVisorDashboard from './components/pages/supervisorDashboard/SuperVisorDashboard';
import AgentsList from './components/pages/supervisorDashboard/sideBar/chatList/AgentsList';
import BrowserNotify from './components/browseNotification/BrowserNotify';
import Notification from "react-web-notification";
import NoPreview from './components/chatComponents/extras/NoPreview';
import DefaultPge from './components/chatComponents/extras/DefaultPge';
import Analytics from './components/pages/analytics/Analytics';

const endpoint = config.proxyServer;
// eslint-disable-next-line no-useless-concat
let options = {
  reconnection: true
}
if(config.websocketpath) {
   options["path"] = config.websocketpath;
}

const socketMain = io(endpoint, options)

function App() {

  const startDevice = useStartDevice();
  const outboundCall = useOutboundCall();
  const time = useTime();
  const chat = useChat();

  const isDesktopOrLaptop = useMediaQuery({
    minWidth: "800px"
  });
  const [showMod, setShowMod] = useState(false);


  useEffect(() => {
    const signinData = {
      email: sessionStorage.getItem(`email${sessionStorage.getItem('agentId')}`),
      password: sessionStorage.getItem(`password${sessionStorage.getItem('agentId')}`),
      accountname: sessionStorage.getItem('accountname')
    }
    console.log('reload after register', signinData);
    if (signinData.email !== null && signinData.password !== null && signinData.accountname !== null) {
      chat.handleSignIn(signinData)
    }
  }, [])

  useEffect(() => {
    const requireId = outboundCall.outboundCallLogs.filter((reqId) => reqId._id === outboundCall.selectedNumberId && reqId.reqId);
    if (requireId[0]) {
      sessionStorage.setItem('reqId', requireId[0].reqId);
    }
  }, [outboundCall.outboundCallLogs, outboundCall.selectedNumberId])

  useEffect(() => {
    sessionStorage.setItem('startDate', outboundCall.startDate);
    sessionStorage.setItem('endDate', outboundCall.endDate);
  }, [outboundCall.endDate, outboundCall.startDate])



  const removeAssignedTicket = async () => {
    const res = await updateCommonInbox(sessionStorage.getItem('token'), sessionStorage.getItem('corporate'), sessionStorage.getItem('reqId'), "")
    console.log(res, 'assign...');
    if (res.status === 200) {
      const refresh = await outboundCall.refreshList();
      console.log(refresh, 'refresh');
    }
  }
  const handleCloseMod = () => setShowMod(false);
  const handleShowMod = () => {
    setShowMod(true);
  }

  // useEffect(() => {
  //   navigator.serviceWorker.register('/sw.js')
  //     .then(function (registration) {
  //       console.log('1st regis', registration)
  //     }).catch((err) => {
  //       console.log(err)
  //     })
  // }, [])


  useEffect(() => {
    const workerDetails = {
      workerSid: sessionStorage.getItem('workerSid'),
      accountname: sessionStorage.getItem('accountname'),
      // workername: sessionStorage.getItem('identity')
    }
    if (chat.loggedIn && workerDetails.workerSid !== 'undefined') {
      console.log(workerDetails, 'workerDetails');
      if (workerDetails.workerSid !== null && workerDetails.accountname !== null) {
        socketMain.emit('twilioWorker', workerDetails);
        socketMain.on("twilioCallStatus", (data) => {
          console.log("Call status", data);
          outboundCall.setCallStatus(data.CallStatus)
          if (data.CallStatus === "no-answer" || data.CallStatus === "failed" || data.CallStatus === "busy") {
            removeAssignedTicket()
            console.log('no answer');
          } else if (data.CallStatus === "completed") {
            console.log('outboundcall completed');
            handleShowMod()
          } else if (data.CallStatus === "in-progress") {
            time.toggle()
          }
        });
        socketMain.on("_refresh_assign_ticket", async (data) => {
          console.log(data, 'refresh socket');
          console.log(outboundCall.startDate, outboundCall.endDate, 'date range');
          outboundCall.getRangeofUnansweredTickets(sessionStorage.getItem('startDate'), sessionStorage.getItem('endDate'), outboundCall.agentId)
        })
      }
    }
    return () => {
      if (chat.loggedIn === false) {
        socketMain.off('twilioWorker');
        socketMain.off('twilioCallStatus');
      }
    }
  }, [chat.loggedIn])




  return (
    <Router>
      <Switch>
        <Route
          path='/:corporate/twilioDashboard/inbound'
          exact
          render={() => (
            <>
              <CallComplete showMod={showMod} handleCloseMod={handleCloseMod} />

              {startDevice.open ? <TwilioCall />
                :
                <div className="loading-spinner">

                  <Loading />
                  <h5 className="loading-text">Loading...</h5>
                </div>
              }
            </>
          )}
        />
        <Route
          path='/:corporate/dashboard'
          exact
          render={(props) => (

            <div>
              {chat.chatLoading ?
                <div >
                  <div id="grid">
                    <SideBar />
                    {chat.showChat || isDesktopOrLaptop === false ?
                      <ChatBox />
                      :
                      <DefaultPge />
                    }
                    {chat.showChat ?
                      <AgentProp socketMain={socketMain} />
                      :
                      <NoPreview />
                    }
                    {(chat.openDrawer || chat.openProps) && <BackDrop />}
                  </div>
                </div>
                :
                <div className="loading-spinner">
                  <Loading />
                  <h5 className="loading-text">Loading...</h5>
                </div>
              }
              {/* <BrowserNotify /> */}
              <ToastContainer autoClose={2000} position="top-center" />
            </div>
          )}
        />

        <Route path={'/analytics'}
          render={() => (
            <Analytics />
          )}
        />

        <Route path='/:corporate' exact
          render={() => (
            <div>
              <Login />
            </div>
          )} />
        <Route
          path='/:corporate/managerDashboard/myList'
          exact
          render={() => (
            <>
              {chat.openManager ?
                <SuperVisorDashboard />
                :
                <div className="loading-spinner">
                  <Loading />
                  <h5 className="loading-text">Loading...</h5>
                </div>
              }
            </>
          )}
        />

        <Route
          exact
          path='/:corporate/managerDashboard/agentsList'
          render={() => (
            <AgentsList />
          )}
        />

        <Route path={`/webRTC/video/:token`} exact
          render={() => (
            <div>
              <ClientVideo socketMain={socketMain} />
            </div>
          )} />

        <Route path={`/webRTC/call/:token`} exact
          render={() => (
            <div>
              <ClientCall socketMain={socketMain} />
            </div>
          )} />
        <Route path={`/:corporate/twilioDashboard/outbound`} exact
          render={() => (
            <div>
              <OutboundCall />
            </div>
          )} />

        <Route
          render={() => (<NotFound />)}
        />
      </Switch>
    </Router>

  );
}

export default App;