import React, { useEffect, useState } from 'react'
import { Modal, Button } from 'react-bootstrap';
import { useTime } from '../../context/callTime/calltimeContext';
import { useOutboundCall } from '../../context/outboundCall/outboundCallContext';
import { useStartDevice } from '../../context/startDevice/startDeviceContext';

const CallComplete = ({ showMod, handleCloseMod }) => {
    const startDevice = useStartDevice();
    const remove = useOutboundCall();
    const checkInbound = useTime();

    const removeAssignedTicket = async () => {
        const removeTicket = await remove.assignAgentInCommoninbox('');
        console.log(removeTicket);
        if (removeTicket.status === 200) {
            const refresh = await remove.refreshList();
            console.log(refresh, 'refresh in modal');
            handleCloseMod();
        }

    }

    const openResolveButton = () => {
        startDevice.setDisableInbound(true);
        remove.setOpenOutboundResolve(true);
        handleCloseMod();

    }
    // let callStatus;
    // if (remove.callStatus !== undefined) {
    //     callStatus = remove.callStatus;
    // }

    // console.log(remove.callStatus, 'answer event');
    // console.log(checkInbound.isInbound, 'checking inbound');


    return (
        <div>
            <Modal
                show={showMod}
                onHide={handleCloseMod}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>Call Ended</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    Do you want to resolve the ticket..?
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary"
                        onClick={removeAssignedTicket}
                    >
                        NO
                    </Button>
                    <Button variant="primary" onClick={openResolveButton}>YES</Button>
                </Modal.Footer>
            </Modal>

        </div>
    )
}

export default CallComplete
