import React, { useState } from 'react'
import './AddProp.css'
import { Card, Button, Container, Col, } from 'react-bootstrap'
import { faVideo, faPhoneSquareAlt, } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import AgentVideo from '../../pages/videoCall/AgentVideo'
import AgentCall from '../../pages/callingOptions/AgentCall'
import WinnoProDetails from './winnoProDetails/WinnoProDetails'
import { useEffect } from 'react'
import noPreview from '../../../assets/images/file.png'
import { useMediaQuery } from 'react-responsive'
import { useChat } from '../../context/chat/chatContext'

const AgentProp = ({ socketMain, clientCallEnded }) => {
    const [showVideo, setShowVideo] = useState(false)
    const [showCall, setShowCall] = useState(false)
    const [modalShow, setModalShow] = useState(false);
    const chat = useChat();
    let convoId = [];
    let chatData = [];
    if (chat.isManagerList) {
        if (chat.conversationsId && chat.chatList) {
            convoId = chat.conversationsId;
            chatData = chat.chatList
            // console.log(chatData, 'chat data');
        }
    } else {
        if (chat.conversationsId && chat.agentConversationList) {
            convoId = chat.conversationsId;
            chatData = chat.agentConversationList;
            // console.log(chatData, 'chat data');
        }
    }
    const [endVideoCall, setEndVideoCall] = useState(false);
    const [endAudioCall, setEndAudioCall] = useState(false);
    const isDesktopOrLaptop = useMediaQuery({
        minWidth: "800px"
    });
    const [currentVideoId, setCurrentVideoId] = useState(undefined)
    const [currentCallId, setCurrentCallId] = useState(undefined)
    const ticketId = chatData.find((ticket) => ticket.conversation.id === convoId && ticket.conversation.additionalAttributes.ticketID)
    const name = chatData.find((userid) => userid.conversation.id === convoId && userid.sender.name)
    const corporate = chatData.find((corp) => corp.conversation.id === convoId && corp.conversation.additionalAttributes.corporate)
    const channelType = chatData.find((chnlType) => chnlType.conversation.id === convoId && chnlType.conversation.additionalAttributes.channelType)
    let ticketID
    if (ticketId && ticketId.conversation) {
        ticketID = ticketId.conversation.additionalAttributes.ticketID
    }
    let userID
    if (channelType && corporate && name.sender) {
        userID = `::guest:${channelType.conversation.additionalAttributes.channelType}:${name.sender.name}@@${corporate.conversation.additionalAttributes.corporate}`
    }


    useEffect(() => {
        if (convoId !== undefined && currentVideoId !== undefined) {
            if (currentVideoId !== undefined ? currentVideoId : convoId !== convoId !== undefined ? convoId : '' && currentVideoId !== undefined) {
                if (window.confirm("Do you want to end this call")) {
                    // setShowVideo(false)
                    // setCurrentVideoId(undefined)
                    setEndVideoCall(true)
                }
                // if (allData.filter((el) => (el.conversation.id === currentVideoId)).length === 1) {
                // } else {
                //     setShowVideo(false)y
                // }

            }
        }

        if (convoId !== undefined && currentCallId !== undefined) {
            if (currentCallId !== undefined ? currentCallId : convoId !== convoId !== undefined ? convoId : '' && currentCallId !== undefined) {

                if (window.confirm("Do you want to end this call")) {
                    setEndAudioCall(true)
                    // setShowCall(false)
                    // setCurrentCallId(undefined)
                }
            }
        }

    }, [convoId])




    const getClientDetails = (category, subCategory) => {
        if (category === 'WINNOWPRO_CATEGORY' && subCategory === 'DIGITAL_RETAILING') {
            return (<WinnoProDetails ticketID={ticketID} />)
        }
        // else if (category === 'WINNOWPRO_CATEGORY' && subCategory === 'DIGITAL_RETAILING2') {
        //     return 2;
        // }
        // else if (category === 'WINNOWPRO_CATEGORY2' && subCategory === 'DIGITAL_RETAILING1') {
        //     return 3;
        // }
        // else if (category === 'WINNOWPRO_CATEGORY2' && subCategory === 'DIGITAL_RETAILING2') {
        //     return 4;
        // }
        else {
            return (<div>
                <h5 className="no-preview-text" >no preview available</h5>
                <div className="no-preview-img">
                    <img src={noPreview} alt="no preview available" style={{ width: '166px' }} />
                </div>
            </div>)
        }
    }
    const handleVideoBtn = () => {
        setCurrentVideoId(convoId)
        setShowVideo(!showVideo)
    }

    const handleCallBtn = () => {
        setCurrentCallId(convoId)
        setShowCall(!showCall)
    }

    let drawerClasses = 'side-drawer-props';
    if (chat.openProps) {
        drawerClasses = 'side-drawer-props open'
    }


    return (
        <div className={isDesktopOrLaptop ? '' : drawerClasses}>
            <Card
                bg='#3fa3ff'
                text={'dark'}
                className='add-details'>
                <div className="rtc-btns">
                    <Container style={{ marginTop: '9px' }}>
                        {chatData.map(callingOptions => callingOptions.conversation.id === convoId &&
                            <div key={callingOptions.conversation.id}>

                                {chat.isManagerList ?
                                    <div className="group-btns">
                                        <Col className="call-optns">
                                            {chat.isWebRtcEnable && showCall === false && <Button style={{ boxShadow: 'var(--box-shadow-5)' }} className={showVideo && 'disable-btn'} variant="primary" onClick={handleVideoBtn}><FontAwesomeIcon icon={faVideo} ></FontAwesomeIcon></Button>}
                                        </Col>
                                        <Col className="call-optns">
                                            {chat.isWebRtcEnable && showVideo === false && <Button style={{ boxShadow: 'var(--box-shadow-5)' }} className={showCall && 'disable-btn'} variant="primary" onClick={handleCallBtn}><FontAwesomeIcon icon={faPhoneSquareAlt} ></FontAwesomeIcon></Button>}
                                        </Col>
                                    </div>
                                    :
                                    <div className="group-btns" >
                                        <Col className="call-optns">
                                            {!chat.isAgentLoggedIn && chat.assignedChatToMgr && showCall === false ?
                                                <Button className={showVideo && 'disable-btn'} style={{ boxShadow: 'var(--box-shadow-5)' }} variant="primary" onClick={handleVideoBtn}><FontAwesomeIcon icon={faVideo} ></FontAwesomeIcon></Button>
                                                :
                                                <Button disabled className={showVideo && 'disable-btn'} style={{ boxShadow: 'var(--box-shadow-5)' }} variant="primary" onClick={handleVideoBtn}><FontAwesomeIcon icon={faVideo} ></FontAwesomeIcon></Button>}
                                        </Col>
                                        <Col className="call-optns">
                                            {!chat.isAgentLoggedIn && chat.assignedChatToMgr && showCall === false ?
                                                <Button className={showCall && 'disable-btn'} style={{ boxShadow: 'var(--box-shadow-5)' }} variant="primary" onClick={handleCallBtn}><FontAwesomeIcon icon={faPhoneSquareAlt} ></FontAwesomeIcon></Button>
                                                :
                                                <Button disabled className={showCall && 'disable-btn'} style={{ boxShadow: 'var(--box-shadow-5)' }} variant="primary" onClick={handleCallBtn}><FontAwesomeIcon icon={faPhoneSquareAlt} ></FontAwesomeIcon></Button>}
                                        </Col>
                                    </div>
                                }
                            </div>
                        )}
                    </Container>
                    <div>
                        {showVideo && <AgentVideo endVideoCall={endVideoCall} setEndVideoCall={setEndVideoCall} showVideo={showVideo} ticketID={ticketID} setCurrentVideoId={setCurrentVideoId} socketMain={socketMain} clientCallEnded={clientCallEnded} setShowVideo={setShowVideo} />}
                        {showCall && <AgentCall endAudioCall={endAudioCall} setEndAudioCall={setEndAudioCall} ticketID={ticketID} setCurrentCallId={setCurrentCallId} socketMain={socketMain} setShowCall={setShowCall} />}
                    </div>

                </div>
                <Card
                    bg='#3fa3ff'
                    text={'dark'}
                    className='add-details scroller'>
                    {chatData.map((getDetails) => getDetails.conversation.id === convoId &&
                        <div key={getDetails.conversation.id}>
                            {chat.isManagerList ?
                                <div>
                                    {getClientDetails(getDetails.conversation.additionalAttributes.categoryID, getDetails.conversation.additionalAttributes.subCategoryID)}
                                </div>
                                :
                                <fieldset disabled={!chat.isAgentLoggedIn && chat.assignedChatToMgr ? false : true} >
                                    {getClientDetails(getDetails.conversation.additionalAttributes.categoryID, getDetails.conversation.additionalAttributes.subCategoryID)}
                                </fieldset >
                            }
                        </div>

                    )}
                </Card>
            </Card>

        </div>
    )
}

export default AgentProp
