import React, { useEffect, useState, useRef } from 'react';
import styled from "styled-components";
import { config } from "../../../config/config"
import { v1 as uuidv1 } from 'uuid';
import axios from "axios"
import Peer from 'peerjs'
import agentCalling from '../../../assets/images/telephone.png'
// import usePictureInPicture from 'react-use-pip'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShare, faMicrophone, faMicrophoneSlash, faPhoneSlash, faMobileScreen } from '@fortawesome/free-solid-svg-icons'
import { Button, Col, Row, Container } from 'react-bootstrap';
import hangUp from './hang-up.png'
import { useNotify } from '../../context/notification/notifyContext';
import './CallingOpt.css'
import { useChat } from '../../context/chat/chatContext';

// const Container = styled.div`
//   height: 100vh;
//   width: 100%;
//   display: flex;
//   flex-direction: column;
// `;

// const Row = styled.div`
//   display: flex;
//   width: 100%;
// `;

const Video = styled.video`
  border: 1px solid blue;
  width: 1%;
    height: 1%;

    background: black;
`;
const ClientCall = styled.video`
  border: 1px solid blue;
  width: 100%;
  height: 50px;
  background: black;
`;

function AgentCall({ setCurrentCallId, endAudioCall, setEndAudioCall, ticketID, socketMain, setShowCall }) {

    const [showMike, setshowMike] = useState(true);
    const [endCall, setEndCall] = useState(false);
    const [webId, setWebId] = useState()
    const [clientMute, setClientMute] = useState(false)
    const [clientCallEnd, setClientCallEnd] = useState(false);
    const [newPeer, setNewPeer] = useState()
    const userCall = useRef();
    const remoteCall = useRef();
    const notify = useNotify();
    const chat = useChat();
    const token = uuidv1();
    let localStream;

    // const {
    //     isPictureInPictureActive,
    //     isPictureInPictureAvailable,
    //     togglePictureInPicture,
    // } = usePictureInPicture(remoteCall)

    useEffect(() => {

        callClient()

    }, []);

    useEffect(() => {
        if (chat.resolveAudioOrVideo) {
            endStream()
            chat.setResolveAudioOrVideo(false)
        }
    }, [chat.resolveAudioOrVideo])

    useEffect(() => {
        if (endAudioCall) {
            endStream();
            setEndAudioCall(false)
        }
    }, [endAudioCall])

    const sendNotification = () => {
        const body = {
            mute: showMike,
            token: webId,
        }
        console.log('notification body', body)
        console.log(body.message, body.token)
        axios.post(`${config.baseUrl}/agentNotification`,
            body,
            { headers: { 'Content-Type': 'application/json' } }
        ).then((res) => {
            console.log(res)
        }).catch(err => console.log(err))
    }

    const sendLink = () => {
        if (webId) {
            const apiMsg = `${config.baseWebUrl}/webRTC/call/${webId}`
            console.log(apiMsg)

            chat.handleNewSentMessage(apiMsg, chat.conversationsId)
        }
    }

    const callClient = () => {
        const peer = new Peer(ticketID,
            {
                config: {
                    'iceServers': [
                        // {
                        //     'urls': 'stun:stun.l.google.com:19302'
                        // },
                        {
                            'urls': config.iceServersUrl,
                            'username': config.iceSereverUsername,
                            'credential': config.iceServerCredential,
                        }
                    ]
                }
            }

        );
        setNewPeer(peer)

        peer.on('open', (id) => {
            console.log(id);
            setWebId(id)
            var getUserMedia = navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia || navigator.mediaDevices.mozGetUserMedia;
            getUserMedia({ video: "false", audio: "true" })
                .then(stream => {
                    userStream(stream);
                    localStream = stream;
                })
            const videoCallDetails = {
                agentid: sessionStorage.getItem('agentId'),
                webRtcId: id
            }
            console.log('call id details', videoCallDetails)
            socketMain.emit('clientVideo', videoCallDetails)
            socketMain.on('agentWebRTCMessage', (res) => {
                console.log(res)
                setClientMute(res.agentDetails.mute)
                if (res.agentDetails.endCall) {
                    setClientCallEnd(true)
                }
            })


        })

        peer.on('error', function (err) {
            if (err.type === "network") {
                setTimeout(function () {
                    console.log('reconnecting');
                    peer.reconnect();
                }, 1000);
            } else if (err.type === "socket-error") {
                setTimeout(function () {
                    callClient();
                }, 1000);
            } else if (err.type === 'unavailable-id') {
                alert('id not generated, please try again')
                endStream()
            }
        });


        peer.on("disconnected", function () {
            // alert("You have end the session");
        })

        peer.on("close", function () {
            alert("Connection to signaling server and peer-to-peer connections have been killed. You ID is taken away. You have been destroyed");
        })


        peer.on('call', function (call) {

            call.answer(localStream);
            call.on('stream', function (remoteStream) {
                console.log('rmote stream', remoteStream);
                remoteCall.current.srcObject = remoteStream
            });
            // setTimeout(() => {
            //     // if (window.confirm("Do you want to accept the incomming call?")) {
            //     // } else {
            //     //     call.close();
            //     // }
            // }, 1000)

        });
    }

    const userStream = (stream) => {
        userCall.current.srcObject = stream;
    }

    const endStream = () => {
        newPeer.disconnect();
        if (newPeer.disconnected) {
            if (userCall.current.srcObject !== null) {

                userCall.current.srcObject.getTracks().forEach((track) => {
                    track.stop();
                });
            }
            const body = {
                endCall: true,
                token: webId,
            }
            axios.post(`${config.baseUrl}/agentNotification`,
                body,
                { headers: { 'Content-Type': 'application/json' } }
            ).then((res) => {
                console.log(res)
            }).catch(err => console.log(err))
            setEndCall(true);
            setShowCall(false);
            setCurrentCallId(undefined);
        }
    }

    var mute = () => {
        if (userCall.current.srcObject !== null) {
            userCall.current.srcObject.getTracks().forEach(t => t.enabled = !t.enabled);
        }
        setshowMike(!showMike);
        sendNotification()
        // if (showMike) {
        //     notify.agentMute()
        // } else {
        //     notify.agentUnmute()
        // }
    }

    let UserCall;

    UserCall = (
        <Video playsInline muted ref={userCall} autoPlay />
    );


    let RemoteCall;

    RemoteCall = (
        <ClientCall playsInline poster={endCall ? hangUp : agentCalling} ref={remoteCall} autoPlay />
    );


    return (
        <div>
            {UserCall}
            {clientCallEnd ? <h5 style={{ paddingLeft: '27px' }}>Client has ended the call</h5> : ""}
            {clientMute && <FontAwesomeIcon className="clientMute" icon={faMicrophoneSlash} />}
            {RemoteCall}
            {/* {isPictureInPictureAvailable && (
                <Button style={{ marginLeft: '39px' }}
                    onClick={() => togglePictureInPicture(!isPictureInPictureActive)}
                >
                    {isPictureInPictureActive ? <FontAwesomeIcon icon={faWindowMaximize} /> : <FontAwesomeIcon icon={faWindowRestore} />}
                </Button>
            )} */}
            <Container>
                <Row className="justify-content-md-center">
                    <Col className="call-btns">
                        <Button
                            variant={showMike ? 'primary' : 'danger'}
                            onClick={mute}>
                            {showMike ? <FontAwesomeIcon icon={faMicrophoneSlash} /> : <FontAwesomeIcon icon={faMicrophone} />}
                        </Button>
                    </Col>
                    <Col className="call-btns">
                        <Button onClick={sendLink} variant="primary">
                            <FontAwesomeIcon icon={faShare} />
                        </Button>
                    </Col>
                    <Col className="call-btns">
                        <Button onClick={endStream} variant="danger">
                            <FontAwesomeIcon icon={faPhoneSlash} />
                        </Button>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default AgentCall
