import React, { useEffect } from 'react'
import ChatList from '../../../../chatComponents/chatList/ChatList';
import NothingToLoad from '../../../../chatComponents/extras/NothingToLoad';
import { useChat } from '../../../../context/chat/chatContext';

const MyList = ({ agentsList }) => {
    const chat = useChat();
    useEffect(() => {
        chat.refreshConversationList()
    }, [])
    console.log(chat.refreshList, 'positively do not remove this console log');//positively do not remove this log
    return (
        <div>
            {chat.chatList.length > 0 ? <ChatList /> : <NothingToLoad />}
        </div>
    )
}

export default MyList
