import React, { useState } from 'react';
import { ListGroup } from 'react-bootstrap';
import { useStartDevice } from '../../../context/startDevice/startDeviceContext';
import InboundCall from '../InboundCall';
import IncommingCall from './IncommingCall';


const InboundcallList = () => {
    const getIncommingData = useStartDevice();
    // console.log(getIncommingData.incommingNumber, 'before incomming number');

    return (
        <ListGroup variant="flush" style={{ height: '90%' }}>
            {getIncommingData.startDevice && <div
                style={{ background: 'var(--primary-background-2)' }}
            >
                {getIncommingData.incommingNumber && <IncommingCall />}
            </div>}
        </ListGroup>
    )
}

export default InboundcallList
