import React from 'react'
import nofile from '../../../assets/images/file.png'
import './extra.css'

const NoPreview = () => {
    return (
        <div className="before-chat-box" style={{ background: 'var(--primary-background-2)', boxShadow: 'var(--box-shadow)' }}>
            <h5 className="disp-text" style={{ color: 'var(--primary-text-color)' }}  >no preview available</h5>
            <img src={nofile} alt="no preview" style={{ width: '150px' }} />
        </div>
    )
}

export default NoPreview