import React from 'react'
import './extra.css'

const NoCalls = (props) => {
    return (
        <div className='no-incomming'>
            <h4>{props.toDisp}</h4>
            <div className="no-calls">
                <img
                    src={props.src}
                    width="200" height="200" alt="no calls" />
            </div>
        </div>
    )
}

export default NoCalls