import React, { useContext, useState, useEffect, useCallback, useMemo } from 'react'
import { useStartDevice } from '../startDevice/startDeviceContext';
const callTimeContext = React.createContext()

export function useTime() {
    return useContext(callTimeContext)
}

export function DurationProvider({ children }) {

    const [currentStatus, setCurrentStatus] = useState("");
    const [totalTime, setTotalTime] = useState("");
    const [isInbound, setIsInbound] = useState(true);
    const [isOutbound, setIsOutBound] = useState(true);
    const [resRejected, setResRejected] = useState(false);
    const [isResAccepted, setIsResAccepted] = useState(false)


    let nowTime = new Date().getTime();

    const startTime = () => {
        nowTime = new Date().getTime()
    }

    const getTimeLapsed = () => {
        return new Date().getTime() - nowTime
    }

    const toggle = () => {
        setCurrentStatus("inCall")
        startTime()

    }
    // console.log("now Time", getTimeLapsed());

    // useEffect(() => {
    //     setDuration((pre) => {
    //         return {
    //             ...pre,
    //             hours,
    //             minutes,
    //             seconds
    //         }
    //     })
    // }, [isRunning, seconds]);

    const handleOutboundConnecting = () => {
        setCurrentStatus("connecting")
    }
    const acceptIncomming = () => {

        setCurrentStatus("inncommingAccept")
    }
    const getTotalTime = (finalTime) => {
        setTotalTime(`${finalTime.hours}:${finalTime.minutes}:${finalTime.seconds}`)
    }
    const reset = () => {
        setCurrentStatus("callEnded");
        setIsResAccepted(false)
    }
    const goToHome = () => {
        setCurrentStatus("homeBtn");
    }
    const openDialPad = () => {
        setCurrentStatus("dialPad");
    }



    // useEffect(() => {
    //     let interval = null;
    //     if (isActive) {
    //         interval = setInterval(() => {
    //             setHours(String(Math.floor(parseInt(second) / 3600)).padStart(2, "0"));
    //             setMinutes(String(Math.floor((parseInt(second) - parseInt(hours) * 3600) / 60)).padStart(2, "0"));
    //             setSeconds(String(parseInt(second) - (parseInt(hours) * 3600 + parseInt(minutes) * 60)).padStart(2, "0"));
    //             setSecond(second => parseInt(second) + parseInt("01"));
    //         }, 1000);
    //     } else if (!isActive && second !== 0) {
    //         clearInterval(interval);
    //     }
    //     return () => clearInterval(interval);
    // }, [isActive, second]);
    // console.log("timmer", hours, minutes, seconds)



    const value = {
        currentStatus,
        totalTime,
        isInbound,
        resRejected,
        isOutbound,
        isResAccepted,
        setIsResAccepted,
        setIsInbound,
        startTime,
        getTimeLapsed,
        getTotalTime,
        // nowTime,
        // isRunning,
        setIsOutBound,
        setTotalTime,
        setResRejected,
        acceptIncomming,
        setCurrentStatus,
        handleOutboundConnecting,
        openDialPad,
        goToHome,
        toggle,
        reset,
        // pause
    }
    return (
        <callTimeContext.Provider value={value}>
            {children}
        </callTimeContext.Provider>
    )
}
