import React, { useEffect, useState, useRef, useCallback } from 'react';
import styled from "styled-components";
import { config } from "../../../config/config"
import axios from "axios"
import Peer from 'peerjs'
import usePictureInPicture from 'react-use-pip'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVideo, faVideoSlash, faMicrophone, faMicrophoneSlash, faPhoneSlash, faShare } from '@fortawesome/free-solid-svg-icons'
import { Button, Row, Col, Container } from 'react-bootstrap';
import waiting from './waiting3.gif'
import { useNotify } from '../../context/notification/notifyContext';
import './AgentVideo.css'
import enumerateDevices from 'enumerate-devices';
import { RiCameraSwitchFill } from 'react-icons/ri';
import { Modal } from 'react-bootstrap';
import { useMediaQuery } from 'react-responsive';
import { useChat } from '../../context/chat/chatContext';





// const Container = styled.div`
//   height: 100vh;
//   width: 100%;
//   display: flex;
//   flex-direction: column;
// `;

// const Row = styled.div`
//   display: flex;
//   width: 100%;
// `;

const Video = styled.video`
border: 1px solid blue;
width: 33%;
height: 24%;
background: black;
position: absolute;
top:0px;
left: 0px
`;
let ClientVideo = styled.video`
border: 1px solid blue;
width: 100%;
height: 100%;
  background: black;
`;

const AgentVideo = ({ setCurrentVideoId, setEndVideoCall, endVideoCall, ticketID, socketMain, setShowVideo }) => {

    const [showMike, setshowMike] = useState(true);
    const [clientMute, setClientMute] = useState(true);
    const [clientCallEnd, setClientCallEnd] = useState(false);
    const [showCammera, setShowCammera] = useState(true);
    const [webId, setWebId] = useState();
    const [clientMuteSmall, setClientMuteSmall] = useState(false);
    // const clientEnd = useVideo()
    const userVideo = useRef();
    const remoteVideo = useRef();
    const muteNotify = useNotify();
    const chat = useChat()
    const [switchCamm, setSwitchCamm] = useState(true);
    const [peerCall, setPeerCall] = useState();
    const [show, setShow] = useState(false);
    const [newPeer, setNewPeer] = useState();
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    let localStream;
    let cam;
    let mic;
    var getUserMedia = navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia || navigator.mediaDevices.mozGetUserMedia;
    const isDesktopOrLaptop = useMediaQuery({
        minWidth: "800px"
    });



    useEffect(() => {
        callClient('user');
        // if (isDesktopOrLaptop) {
        // } else {
        //     handleShow();
        // }
    }, [])

    const frontCamm = () => {
        callClient('user');
        handleClose();
    }

    const backCamm = () => {
        callClient('environment');
        handleClose();
    }

    useEffect(() => {
        if (chat.resolveAudioOrVideo) {
            endStream()
            chat.setResolveAudioOrVideo(false)
        }
    }, [chat.resolveAudioOrVideo])

    useEffect(() => {
        if (endVideoCall) {
            endStream();
            setEndVideoCall(false)
        }

    }, [endVideoCall])

    const sendNotification = () => {
        const body = {
            mute: showMike,
            token: webId,
        }
        console.log('notification body', body)
        console.log(body.message, body.token)
        axios.post(`${config.baseUrl}/agentNotification`,
            body,
            { headers: { 'Content-Type': 'application/json' } }
        ).then((res) => {
            console.log(res)
        }).catch(err => console.log(err))
    }




    const shareLink = () => {
        const apiMsg = `${config.baseWebUrl}/webRTC/video/${ticketID}`
        console.log(apiMsg);
        chat.handleNewSentMessage(apiMsg, chat.conversationsId)
    }

    var peerObj;
    var lastPeerId;
    var conn;


    const callClient = (ideal) => {
        const peer = new Peer(ticketID,
            {
                config: {
                    'iceServers': [
                        // {
                        //     'urls': 'stun:stun.l.google.com:19302'
                        // },
                        {
                            'urls': config.iceServersUrl,
                            'username': config.iceSereverUsername,
                            'credential': config.iceServerCredential,
                        }
                    ]
                }
            }

        );
        setNewPeer(peer)
        console.log(peer, 'peer');

        peer.on('open', (id) => {
            console.log(id);
            setWebId(id)
            const videoCallDetails = {
                agentid: sessionStorage.getItem('agentId'),
                webRtcId: id

            }
            socketMain.emit('clientVideo', videoCallDetails)
            socketMain.on('agentWebRTCMessage', (res) => {
                setClientMute(res.agentDetails.mute)
                setClientMuteSmall(true)
                if (res.agentDetails.endCall) {
                    setClientCallEnd(true)
                }
            })

            navigator.mediaDevices.enumerateDevices().then(function (devices) {
                cam = devices.find(function (device) {
                    return device.kind === "videoinput";

                });
                mic = devices.find(function (device) {
                    return device.kind === "audioinput";
                });
                var constraints = {
                    audio: mic,
                    video: {
                        cam,
                        facingMode: {
                            exact: ideal
                        }
                    },
                };



                var getUserMedia = navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia || navigator.mediaDevices.mozGetUserMedia;

                getUserMedia(constraints)
                    .then(stream => {
                        userVideo.current.srcObject = stream;
                        userVideo.current.play();
                        localStream = stream
                        console.log('local stream', stream);

                    }).catch(function (err) {
                        console.log(err.name);
                    });
            });
        })

        peer.on('call', function (call) {
            setPeerCall(call);
            console.log(call, 'agent call');
            console.log(localStream, 'inside peer.on');

            call.answer(localStream);

            call.on('stream', function (remoteStream) {
                console.log('rmote stream', remoteStream);
                remoteVideo.current.srcObject = remoteStream;
                setClientCallEnd(false);
            });
        });
        peer.on('disconnected', () => {
            console.log('disconnected');
        })
        peer.on('error', function (err) {
            if (err.type === "network") {
                setTimeout(function () {
                    console.log('reconnecting');
                    peer.reconnect();
                }, 1000);
            } else if (err.type === "socket-error") {
                setTimeout(function () {
                    callClient();
                }, 1000);
            } else if (err.type === 'unavailable-id') {
                alert('id not generated, please try again')
                endStream()
            }
        });

    }


    const endStream = () => {
        newPeer.disconnect();
        // newPeer.destroy();
        if (newPeer.disconnected) {
            if (userVideo.current.srcObject !== null) {
                userVideo.current.srcObject.getTracks().forEach((track) => {
                    track.stop();
                });
            }
            setCurrentVideoId(undefined)
            const body = {
                endCall: true,
                token: webId,
            }
            axios.post(`${config.baseUrl}/agentNotification`,
                body,
                { headers: { 'Content-Type': 'application/json' } }
            ).then((res) => {
                console.log(res)
            }).catch(err => console.log(err))
            setShowVideo(false);
        }
    }
    let mic_switch = true;
    let video_switch = true;

    const toggleVideo = () => {
        if (userVideo.current.srcObject != null && userVideo.current.srcObject.getVideoTracks().length > 0) {
            video_switch = !video_switch;

            userVideo.current.srcObject.getVideoTracks().forEach(t => t.enabled = !t.enabled);
        }
        setShowCammera(!showCammera);
    }


    const mute = () => {
        if (userVideo.current.srcObject !== null && userVideo.current.srcObject.getAudioTracks().length > 0) {
            mic_switch = !mic_switch;

            userVideo.current.srcObject.getAudioTracks().forEach(t => t.enabled = !t.enabled);
        }
        setshowMike(!showMike);
        sendNotification()
    }

    const changeTrack = (ideal) => {
        let constraints = {
            audio: true,
            video: {
                width: 640,
                height: 480,
                facingMode: {
                    exact: ideal
                }
            }
        }
        getUserMedia(constraints).then(stream => {
            peerCall.peerConnection.getSenders()[1].replaceTrack(stream.getVideoTracks()[0]);
            userVideo.current.srcObject = stream;

        }).catch(err => console.log(err))
    }

    const switchCamera = () => {

        setSwitchCamm(!switchCamm)

        if (userVideo.current.srcObject !== null) {

            userVideo.current.srcObject.getTracks().forEach((track) => {
                track.stop();
            });
            if (switchCamm) {
                changeTrack('environment')
                // callAgent('environment')
            } else {

                changeTrack('user')
                // callAgent('user')
            }
        }

    }



    const switchClientScreen = () => {
        setClientMuteSmall(false);
        userVideo.current.style.width = '33%';
        userVideo.current.style.height = '24%';
        userVideo.current.style.cursor = 'pointer';
        userVideo.current.style.zIndex = 1000000000;
        userVideo.current.style.position = 'absolute';
        userVideo.current.style.top = '0px';
        remoteVideo.current.style.width = '100%';
        remoteVideo.current.style.height = '100%';
        remoteVideo.current.style.position = 'initial';
    }

    const switchAgentScreen = () => {
        console.log('switch agent');
        setClientMuteSmall(true);
        remoteVideo.current.style.width = '33%';
        remoteVideo.current.style.height = '24%';
        remoteVideo.current.style.cursor = 'pointer';
        remoteVideo.current.style.position = 'absolute';
        remoteVideo.current.style.top = '0px';
        userVideo.current.style.width = '100%';
        userVideo.current.style.height = '100%';
        userVideo.current.style.position = 'revert';
    }





    let UserVideo;
    let RemoteVideo;

    UserVideo = (
        <Video poster={waiting} onClick={switchAgentScreen} playsInline muted ref={userVideo} autoPlay />
    );



    RemoteVideo = (
        <ClientVideo poster={waiting} onClick={switchClientScreen} playsInline ref={remoteVideo} autoPlay />
    );


    return (
        <div style={{ position: 'relative' }}>
            <Modal
                show={show}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header >
                    <Modal.Title>Camera Facing</Modal.Title>
                </Modal.Header>
                <Modal.Body>Do you want to open front cammera or rear cammera ?</Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={frontCamm}>
                        Front
                    </Button>
                    <Button variant="primary" onClick={backCamm}>
                        Rear
                    </Button>
                </Modal.Footer>
            </Modal>
            <div>
                {UserVideo}
            </div>
            <div style={{ display: 'inline' }}>
                {clientMute === false && <FontAwesomeIcon className={clientMuteSmall ? "clientMute-icon" : "clientMute-small"} icon={faMicrophoneSlash} />}
                {RemoteVideo}
            </div>
            {clientCallEnd ? <h5 style={{ paddingLeft: '27px' }}>Client has ended the call</h5> : ""}
            <Container fluid="md">
                <Row xs="auto" className="justify-content-md-center">
                    <Col className="call-btns">
                        {/* {isPictureInPictureAvailable && (
                            <Button
                                onClick={() => togglePictureInPicture(!isPictureInPictureActive)}
                            >
                                {isPictureInPictureActive ? <FontAwesomeIcon icon={faWindowMaximize} /> : <FontAwesomeIcon icon={faWindowRestore} />}
                            </Button>
                        )} */}
                        <Button
                            onClick={toggleVideo}
                            variant={showCammera ? 'primary' : 'danger'}
                        >
                            {/* <FontAwesomeIcon icon={faCameraRetro} /> */}
                            {showCammera ? <FontAwesomeIcon icon={faVideo} /> : <FontAwesomeIcon icon={faVideoSlash} />}
                        </Button>
                    </Col>
                    <Col className="call-btns">

                        <Button
                            variant={showMike ? 'primary' : 'danger'}
                            onClick={mute}>
                            {showMike ? <FontAwesomeIcon variant="danger" icon={faMicrophone} /> : <FontAwesomeIcon icon={faMicrophoneSlash} />}
                        </Button>
                    </Col>
                    <Col className="call-btns">

                        <Button
                            variant='primary'
                            onClick={switchCamera}>
                            <RiCameraSwitchFill className="cam-switch" />
                        </Button>
                    </Col>
                    <Col className="call-btns">

                        <Button
                            variant="primary"
                            onClick={shareLink}>
                            <FontAwesomeIcon variant="primary" icon={faShare} />
                        </Button>
                    </Col>
                    <Col className="call-btns">
                        <Button onClick={endStream} variant="danger">
                            <FontAwesomeIcon icon={faPhoneSlash} />
                        </Button>
                    </Col>
                </Row>
            </Container>
        </div>
    );

}

export default AgentVideo