import React, { useEffect, useState, useRef, useCallback } from 'react';
import styled from "styled-components";
import { useParams } from "react-router-dom"
import { config } from '../../../config/config';
import axios from 'axios'
import Peer from 'peerjs'
import './ClientVideo.css';
import goodDay from '../../../assets/images/sunshine.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVideo, faVideoSlash, faMicrophone, faMicrophoneSlash, faCameraRetro, faPhoneSlash, faCameraRotate } from '@fortawesome/free-solid-svg-icons'
import { Button, Col, Container } from 'react-bootstrap';
import Time from './Time';
import { useNotify } from '../../context/notification/notifyContext';
import { RiCameraSwitchFill } from 'react-icons/ri';


const Video = styled.video`
border: 1px solid blue;
width: 24%;
height: 16vh;
position: absolute;
right: 0px;
top: 0px;
background: black;
`;
const AgentVideo = styled.video`
border: 1px solid blue;
width: 100%;
  height: 80vh;
  
  background: black;
  
`;


function ClientVideo({ socketMain, setSwitched }) {
    const [showCammera, setShowCammera] = useState(true);
    const userVideo = useRef();
    const remoteVideo = useRef();
    const clientMuteRef = useRef();
    const [agentEndedCall, setAgentEndedCall] = useState(false)
    const [agentMute, setAgentMute] = useState(false)
    const frontCamera = "user";
    const rearCamera = "environment";
    const [ideal, setIdeal] = useState(frontCamera)
    const [callEnded, setCallEnded] = useState(false)
    const [tglmute, setTglMute] = useState(false)
    const cMute = useNotify();
    const [switchCamm, setSwitchCamm] = useState(true);
    const [newPeer, setNewPeer] = useState();
    const [peerCall, setPeerCall] = useState();
    const [changedStream, setChangedStream] = useState();
    const [agentSmallScreen, setAgentSmallScreen] = useState(false);
    var getUserMedia = navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia || navigator.mediaDevices.mozGetUserMedia;






    let { type, token } = useParams();

    useEffect(() => {
        socketMain.emit('agentVideo', token)
        socketMain.on('agentNotification', (res => {
            console.log('agent mute res', res)
            if (res.agentDetails.mute) {
                setAgentMute(true)
                // cMute.agentMuteNotify()
            } else {
                setAgentMute(false)
                // cMute.agentUnmuteNotify()
            }
            if (res.agentDetails.endCall) {
                setAgentEndedCall(true)
                // cMute.agentEndedCall()
            }
        }))
    }, [token])

    const sendNotification = () => {
        const body = {
            endCall: callEnded,
            mute: tglmute,
            token: token,
        }
        console.log('notification body', body)
        console.log(body.message, body.token)
        axios.post(`${config.baseUrl}/clientNotification`,
            body,
            { headers: { 'Content-Type': 'application/json' } }
        ).then((res) => {
            console.log(res)
        }).catch(err => console.log(err))
    }

    useEffect(() => {
        callAgent('user')

    }, []);


    const callAgent = () => {
        const peer = new Peer()
        setNewPeer(peer);

        let constraints = {
            audio: true,
            video: {
                width: 640,
                height: 480,
                // facingMode: {
                //     exact: ideal
                // }
            }
        }
        peer.on('open', (id) => {
            console.log('connection with id:', id);
            getUserMedia(constraints).then(stream => {
                console.log('client streaming', stream);
                AgentStream(stream)
                console.log(token);
                let call = peer.call(token, stream);
                console.log(call);
                setPeerCall(call);
                call.on('stream', function (stream) {
                    console.log(stream);
                    remoteVideo.current.srcObject = stream;
                    console.log('remoteStream of  client streaming', stream);
                })

            }).catch(err => console.log(err))
        })

        peer.on('close', () => {
            console.log('peer.on(close)');
        })

        peer.on('error', (err) => {
            console.log(err.type, 'peer error');
            if (err.type === "network") {
                setTimeout(function () {
                    console.log('reconnecting');
                    peer.reconnect();
                }, 1000);
            } else if (err.type === "socket-error") {
                setTimeout(function () {
                    callAgent();
                }, 1000);
            }
            // let reconnect = false;
            // let runNext = true;

            // // const FATAL_ERRORS = ['invalid-id', 'invalid-key', 'network', 'ssl-unavailable', 'server-error', 'socket-error', 'socket-closed', 'unavailable-id', 'webrtc'];
            // const FATAL_ERRORS = ['network'];
            // if (FATAL_ERRORS.includes(err.type)) {
            //     const reconnectTimer = setInterval(() => {
            //         if (!reconnect && runNext) {
            //             runNext = false;
            //             const peer = new Peer()
            //             setNewPeer(peer);

            //             let constraints = {
            //                 audio: true,
            //                 video: {
            //                     width: 640,
            //                     height: 480,
            //                     // facingMode: {
            //                     //     exact: ideal
            //                     // }
            //                 }
            //             }
            //             peer.on('open', (id) => {
            //                 console.log('reconnecting with id:', id);
            //                 // setReconnect(true);
            //                 reconnect = true;
            //                 getUserMedia(constraints).then(stream => {
            //                     console.log('reconnecting client streaming', stream);
            //                     userVideo.current.srcObject = stream;
            //                     // userVideo.current.play();
            //                     console.log(token);
            //                     let call = peer.call(token, stream);
            //                     console.log(call);
            //                     setPeerCall(call);
            //                     call.on('stream', function (stream) {
            //                         console.log(stream);
            //                         remoteVideo.current.srcObject = stream;
            //                         console.log('reconnecting remote stream', stream);
            //                     })

            //                 }).catch(err => console.log(err))
            //             })
            //             peer.on('error', (err) => {
            //                 // setReconnect(false);
            //                 reconnect = false;
            //             })
            //             if (reconnect) {
            //                 clearInterval(reconnectTimer);
            //             } else {
            //                 runNext = true;
            //             }

            //         }
            //     }, 1000)

            // } else {
            //     console.log('Non fatal error: ', err.type);
            // }
        })

    }

    const AgentStream = (localStream) => {
        userVideo.current.srcObject = localStream;
        userVideo.current.play();
    }





    const endStream = () => {
        newPeer.destroy();
        if (userVideo.current.srcObject !== null) {

            userVideo.current.srcObject.getTracks().forEach((track) => {
                track.stop();
            });
        }
        setCallEnded(true)
        const body = {
            endCall: true,
            token: token,
        }
        axios.post(`${config.baseUrl}/clientNotification`,
            body,
            { headers: { 'Content-Type': 'application/json' } }
        ).then((res) => {
            console.log(res)
        }).catch(err => console.log(err))
    }

    let mic_switch = true;
    let video_switch = true;

    const toggleVideo = () => {
        if (userVideo.current.srcObject != null && userVideo.current.srcObject.getVideoTracks().length > 0) {
            video_switch = !video_switch;

            userVideo.current.srcObject.getVideoTracks().forEach(t => t.enabled = !t.enabled);
        }
        setShowCammera(!showCammera);
    }


    const toggleMute = () => {

        if (userVideo.current.srcObject != null && userVideo.current.srcObject.getAudioTracks().length > 0) {
            mic_switch = !mic_switch;

            userVideo.current.srcObject.getAudioTracks().forEach(t => t.enabled = !t.enabled);
        }

        setTglMute(!tglmute)
        // cMute.clientMute()
        sendNotification()

    }

    const changeTrack = (ideal) => {
        let constraints = {
            audio: true,
            video: {
                width: 640,
                height: 480,
                facingMode: {
                    exact: ideal
                }
            }
        }
        getUserMedia(constraints).then(stream => {
            setChangedStream(stream);
            console.log(peerCall, 'peer call');
            console.log(typeof (peerCall.peerConnection.getSenders()[1].replaceTrack), 'get senders');
            peerCall.peerConnection.getSenders()[1].replaceTrack(stream.getVideoTracks()[0]);
            userVideo.current.srcObject = stream;

        }).catch(err => console.log(err))
    }



    // const [facingMode, setFacingMode] = useState(frontCamera);

    const switchCamera = () => {

        setSwitchCamm(!switchCamm)

        if (userVideo.current.srcObject !== null) {

            userVideo.current.srcObject.getTracks().forEach((track) => {
                track.stop();
            });
            if (switchCamm) {
                changeTrack('environment')
                // callAgent('environment')
            } else {

                changeTrack('user')
                // callAgent('user')
            }
        }

    }

    const switchClientScreen = () => {
        setAgentSmallScreen(true);
        userVideo.current.style.width = '100%';
        userVideo.current.style.height = '80vh';
        userVideo.current.style.position = 'initial';
        remoteVideo.current.style.top = '0px';
        remoteVideo.current.style.right = '0px';
        remoteVideo.current.style.position = 'absolute';
        remoteVideo.current.style.width = '24%';
        remoteVideo.current.style.height = '16vh';
    }

    const switchAgentScreen = () => {
        setAgentSmallScreen(false);
        remoteVideo.current.style.width = '100%';
        remoteVideo.current.style.height = '80vh';
        remoteVideo.current.style.position = 'initial';
        userVideo.current.style.zIndex = 100;
        userVideo.current.style.top = '0px';
        userVideo.current.style.right = '0px';
        userVideo.current.style.position = 'absolute';
        userVideo.current.style.width = '24%';
        userVideo.current.style.height = '16vh';
        console.log(agentSmallScreen, 'toggle mute');
    }


    let UserVideo;
    let RemoteVideo;

    UserVideo = (
        <Video playsInline
            muted
            ref={userVideo}
            onClick={switchClientScreen}
            autoPlay />
    );

    RemoteVideo = (
        <AgentVideo playsInline onClick={switchAgentScreen} ref={remoteVideo} autoPlay />
    )







    return (
        <>
            {callEnded === false ? <Container fluid='xs' className="videoContainer">
                {/* <Row > */}
                {/* <Col xs={12} style={{ justifyContent: 'center', textAlign: 'center' }}>


                </Col> */}
                {agentEndedCall && <h5 style={{ paddingLeft: '27px', color: 'aliceblue' }}>Agent has ended the call</h5>}
                <div>
                    {UserVideo}
                </div>
                <div style={{ display: 'inline' }}>

                    {agentMute &&
                        <FontAwesomeIcon className={!agentSmallScreen ? "client-mute" : "client-mute-small"} icon={faMicrophoneSlash} />
                    }
                    {RemoteVideo}
                </div>

                <div>
                    <Time />
                </div>
                <div className="optionBar">
                    <Button
                        style={{ marginRight: '45px', borderRadius: '50%' }}
                        onClick={toggleVideo}
                        variant={showCammera ? 'primary' : 'danger'}
                    >
                        {showCammera ? <FontAwesomeIcon icon={faVideo} /> : <FontAwesomeIcon icon={faVideoSlash} />}
                    </Button>
                    <Button onClick={switchCamera} style={{ marginRight: '45px', borderRadius: '50%' }} >
                        {/* <FontAwesomeIcon icon={faCameraRetro} /> */}
                        <RiCameraSwitchFill className="cam-switch" />
                    </Button>
                    {/* <Button onClick={rearCamm} style={{ marginRight: '45px', borderRadius: '50%' }} >
                        <FontAwesomeIcon icon={faCameraRetro} />
                    </Button> */}
                    <Button variant={tglmute ? 'danger' : 'primary'} style={{ marginRight: '45px', borderRadius: '50%' }} onClick={toggleMute}>{tglmute ? <FontAwesomeIcon icon={faMicrophoneSlash} /> : <FontAwesomeIcon icon={faMicrophone} />}</Button>
                    <Button onClick={endStream} variant="danger" style={{ borderRadius: '50%' }} >
                        <FontAwesomeIcon icon={faPhoneSlash} />
                    </Button>
                </div>


            </Container> :
                <div style={{ textAlign: 'center', marginTop: '50px' }}>
                    <h1 >You Left The Meeting</h1>
                    <h1>Thank You</h1>
                    <p>Have A Nice Day</p>
                    <img src={goodDay} alt="no preview available" style={{ width: '280px' }} />
                </div>

            }
        </>
    );
}

export default ClientVideo;
