import React, { useState, useEffect, memo } from 'react';
import { useTime } from '../../context/callTime/calltimeContext';



const CallDuration = ({ extraStyle, isRunning }) => {
    const time = useTime();
    const [currentTime, setCurrentTime] = useState({
        running: false,
        hours: "00",
        minutes: "00",
        seconds: "00",
        second: "00",
    });

    // useEffect(() => {
    //     // handleStart(true)
    //     setInterval(() => {
    //         setDuration(msToTime(time.getTimeLapsed()))
    //         // msToTime(time.getTimeLapsed())
    //         console.log("ms to seconds", msToTime(time.getTimeLapsed()))
    //     }, 1000)
    //     return () => clearInterval(convertTime)
    // }, [time.getTimeLapsed])

    // console.log('time comp', timeComp)

    function msToTime(time) {
        const second = 1000;
        const minute = second * 60;
        const hour = minute * 60;

        let hours = Math.floor(time / hour % 24);
        let minutes = Math.floor(time / minute % 60);
        let seconds = Math.floor(time / second % 60);


        return hours + ':' + minutes + ":" + seconds;
    }





    useEffect(() => {
        handleStart(isRunning)
    }, [])

    const handleStart = (currentState) => {
        setCurrentTime((prev) => ({ ...prev, running: currentState }))
    }
    // time.setTotalTime(`${currentTime.hours}:${currentTime.minutes}:${currentTime.seconds}`)
    // console.log("current time", time.currentTime)
    useEffect(() => {
        let interval = null;
        if (currentTime.running && time.isResAccepted) {
            interval = setInterval(() => {
                timeLogic()
            }, 1000);
        } else if (!currentTime.running && currentTime.second !== 0) {
            clearInterval(interval);
        }
        return () => {
            clearInterval(interval)
        };
    }, [currentTime.running, time.isResAccepted]);
    useEffect(() => {
        sessionStorage.setItem("hours", currentTime.hours)
        sessionStorage.setItem("minutes", currentTime.minutes)
        sessionStorage.setItem("seconds", currentTime.seconds)
    }, [currentTime.seconds])
    console.log("current time", currentTime)
    const timeLogic = () => (
        setCurrentTime((prev) => {
            return {
                ...prev,
                hours: String(Math.floor(parseInt(prev.second) / 3600)).padStart(2, "0"),
                minutes: String(Math.floor((parseInt(prev.second) - parseInt(prev.hours) * 3600) / 60)).padStart(2, "0"),
                seconds: String(parseInt(prev.second) - (parseInt(prev.hours) * 3600 + parseInt(prev.minutes) * 60)).padStart(2, "0"),
                second: parseInt(prev.second) + parseInt("01")
            }
        })

    )



    return (
        <div className="app" style={{ color: 'white' }} >
            <p style={extraStyle}>
                {currentTime.hours} : {currentTime.minutes} : {currentTime.seconds}
                {/* {duration} */}
            </p>
        </div>);
};
export default memo(CallDuration);
