import React from 'react'
import { Navbar, Dropdown } from 'react-bootstrap';
import { useState, useEffect } from 'react'
// import './TwilioHeader.css'
import '../../../chatComponents/header/Header.css';
import LogOut from '../../loginPage/LogOut';
import { useStartDevice } from '../../../context/startDevice/startDeviceContext';
import { useOutboundCall } from '../../../context/outboundCall/outboundCallContext';
import ChangeName from '../../../chatComponents/header/ChangeName';
import ChangePassword from '../../../chatComponents/header/ChangePassword';
// import agent from '../../../assets/images/agent.png'
// import LogOut from '../../pages/loginPage/LogOut';
// import axios from 'axios';
// import { config } from '../../../config/config'




const TwilioHeader = () => {
    const connectionStatus = useStartDevice();
    const [corporate, setCorporate] = useState();
    const [changeTwilioName, setChangeTwilioName] = useState(false);
    const [changeTwilioPassword, setChangeTwilioPassword] = useState(false);
    const [twilioNameChanged, setTwilioNameChanged] = useState(false);
    const agentName = useOutboundCall();


    useEffect(() => {
        setCorporate(sessionStorage.getItem('corporate'))
    }, [])



    const handleLogout = () => {
        sessionStorage.clear();
        window.location.href = `/${corporate}`;
    }

    const openTwilioNameModal = () => setChangeTwilioName(true);
    const openTwilioPasswordModal = () => setChangeTwilioPassword(true);


    return (
        <div className="agent-nav-bar">

            <Navbar className='nav-bar-1'  >
                {/* <Navbar.Brand> */}
                {connectionStatus.connected ? <span className="dot" style={{ backgroundColor: '#14c741' }}></span> : <span className="dot" style={{ backgroundColor: 'red' }}></span>}
                <img
                    alt=""
                    src={'/assets/images/worker.png'}
                    width="30"
                    height="30"
                    className="d-inline-block align-top"
                    style={{
                        borderRadius: "100%", marginLeft: '7px', marginRight: '7px'
                    }}
                />{' '}
                <div className="agent-name"
                >
                    {!twilioNameChanged ? <div className="agent-username"
                    >{sessionStorage.getItem('agentName') !== 'undefined' ? sessionStorage.getItem('agentName') : connectionStatus.renderWorker.friendlyName}
                    </div> :
                        <div className="agent-username"
                        >{sessionStorage.getItem('newAgentName')}
                        </div>}
                </div>

                {/* </Navbar.Brand> */}
                <div className="header-spacer"></div>
                <Dropdown className="float-right">

                    <Dropdown.Toggle split variant="#1f93ff" style={{ background: "#3fa3ff" }} id="dropdown-split-basic" />

                    <Dropdown.Menu className='dropdown-menu'>

                        {/* <Dropdown.Divider />
                    <Dropdown href="" id="dropdown-split-basic" style={{ marginLeft: '15px' }}>Change Status{status.map((stat) => (<Dropdown.Item key={stat.id} onClick={() => handleStatusChange(stat.id)} >{stat.currentStatus}</Dropdown.Item>))}</Dropdown>
                    <Dropdown.Divider /> */}
                        <Dropdown.Item style={{ color: 'black !important' }}>
                            <div onClick={connectionStatus.toggleWorkerAvailability}>{connectionStatus.renderWorker.activityName === "Available" ? "offline" : "online"}</div>
                        </Dropdown.Item>
                        <Dropdown.Item style={{ color: 'black !important' }} onClick={openTwilioNameModal}>
                            change name
                        </Dropdown.Item>
                        <Dropdown.Item style={{ color: 'black !important' }} onClick={openTwilioPasswordModal}>
                            change password
                        </Dropdown.Item>
                        <Dropdown.Item style={{ color: 'black !important' }}>
                            <LogOut />
                        </Dropdown.Item>

                    </Dropdown.Menu>
                </Dropdown>
            </Navbar>

            {changeTwilioName && <ChangeName onClose={() => setChangeTwilioName(false)} setNameChanged={setTwilioNameChanged} changeNameModal={changeTwilioName} />}
            {changeTwilioPassword && <ChangePassword onHide={() => setChangeTwilioPassword(false)} changePasswordModal={changeTwilioPassword} />}
        </div >
    )
}

export default TwilioHeader
