import React from 'react'
import Chats from '../../../../chatComponents/chatList/Chats'
import '../../../../chatComponents/chatList/ChatList.css'
import { ListGroup } from 'react-bootstrap'
import { useChat } from '../../../../context/chat/chatContext'
import Agent from '../singleChat/Agent'

const Agents = () => {
    const chat = useChat()

    const compare = (a, b) => {
        // if (a.status === "online" && b.status === "offline") {
        //     return -1;
        // }
        // if (a.status === "offline" && b.status === "online") {
        //     return 1;
        // }
        // return 0;
        if (a.status === "online" && b.status === "offline") {
            return -1;
        }
        if (a.status === "online" && b.status === "busy") {
            return -1;
        }
        if (a.status === "offline" && b.status === "online") {
            return 1;
        }
        if (a.status === "offline" && b.status === "busy") {
            return 1;
        }
        if (a.status === "busy" && b.status === "online") {
            return 1;
        }
        if (a.status === "busy" && b.status === "offline") {
            return -1;
        }
        return 0;
    }



    return (

        <ListGroup variant="flush">
            {chat.agentsList.sort(compare).map((conversation, index) => (
                <div
                    key={index}
                    style={{ background: 'var(--primary-backgroun-2)' }}
                >
                    {<Agent key={conversation.id} user={conversation} index={index} />}
                </div>
            ))}
        </ListGroup>



    )
}

export default Agents
