import React, { useContext } from 'react'
import { toast } from 'react-toastify';

const notifyContext = React.createContext()

export function useNotify() {
    return useContext(notifyContext)
}

export function NotificationProvider({ children }) {
    const agentMute = () => toast.warning("You are on mute!");
    const agentUnmute = () => toast.warning("You have unmuted");
    const convoResolved = () => {
        console.log('resolved');
        toast.warning("Conversation Resolved", { autoClose: 2000, hideProgressBar: true })
    }
    const clientMute = () => toast.warning("you are on mute")
    const clientUnmute = () => toast.warning("you have unmuted the call")
    const winnowProUpdate = () => toast.warning("updated...", { autoClose: 2000, hideProgressBar: true })
    const wProNotUpdated = () => toast.warning("cannot update..!", { autoClose: 2000, hideProgressBar: true })
    const changedPassword = () => toast.warning("Password changed successfully", { autoClose: 2000, hideProgressBar: true })
    const multipleLogin = () => toast.warning("the current account is loggedIn in a different place...! ", { autoClose: 3000, hideProgressBar: true })
    const managerLoggedUoff = () => toast.warning("Manager has logged you off.!", { autoClose: 3000, hideProgressBar: true })
    const ticketDetailsNotFound = () => toast.warning("Ticket details not found ", { autoClose: 2000, hideProgressBar: true })
    const ticketCancled = () => toast.warning("Sorry!! client has canceled the ticket.", { autoClose: 2000, hideProgressBar: true })
    const convoUnAttended = () => toast.warning("Conversation was left unattended", { autoClose: 2000, hideProgressBar: true })
    const agentUnmuteNotify = () => toast.warning("Agent has unmuted the call")
    const agentEndedCall = () => toast.warning("Agent has ended the call")
    const value = {
        agentMute,
        agentUnmute,
        convoResolved,
        clientMute,
        winnowProUpdate,
        changedPassword,
        managerLoggedUoff,
        ticketCancled,
        multipleLogin,
        wProNotUpdated,
        ticketDetailsNotFound,
        convoUnAttended,
        agentUnmuteNotify,
        agentEndedCall,
        clientUnmute
    }
    return (
        <>
            <notifyContext.Provider value={value}>
                {children}
            </notifyContext.Provider>

        </>
    )
}
