import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import { faTable } from '@fortawesome/free-solid-svg-icons';
import '../sideBar/DrawerToggleButton.css'
import { Button } from 'react-bootstrap';

const PropToggleButton = ({ propDrawerToggle }) => {
    return (
        <Button className="toggle-prop-button" onClick={propDrawerToggle} >
            {/* <div className="toggle-button-line" />
            <div className="toggle-button-line" />
            <div className="toggle-button-line" /> */}
            <div className="table-icon">
                <FontAwesomeIcon icon={faTable} style={{ fontSize: 'x-large' }} />
            </div>
        </Button>
    )
}

export default PropToggleButton
