import React, { useEffect, useState } from 'react'
import { Modal, Button } from 'react-bootstrap';

const Reservations = ({ reservationSid, setDisableOutbound, newWorkerClient, rejectReservation, show, handleClose, setIsAnswered }) => {


    const reservationAccepted = () => {
        console.log(reservationSid, 'accept reserve');
        acceptReservation(reservationSid);
        setDisableOutbound(true);
        setIsAnswered(true)
        handleClose();

    }

    const reservationRejected = () => {
        rejectReservation(reservationSid);
        handleClose();
    }

    const acceptReservation = (reservationSid) => {
        console.log(reservationSid, 'reservationSid');
        newWorkerClient.fetchReservations(function (error, reservations) {
            for (let reservation of reservations.data) {
                if (reservation.sid === reservationSid) {
                    if (reservation.task.attributes.conference) {
                        // There is already a conference, so it's likely a transfer
                        console.log(
                            "Joining conference " +
                            reservation.task.attributes.conference.room_name
                        );
                        reservation.call(
                            null,
                            "https://amber-ibis-1382.twil.io/join-conference?conferenceRoomName=" +
                            reservation.task.attributes.conference.room_name,
                            null,
                            true
                        );
                    } else {
                        reservation.dequeue();

                    }
                    break;
                }
            }
        });
    }


    // const rejectReservation = (reservationSid) => {
    //     console.log(reservationSid, 'reservationSid');
    //     newWorkerClient.fetchReservations(function (error, reservations) {
    //         for (let reservation of reservations.data) {
    //             if (reservation.sid === reservationSid) {
    //                 reservation.reject();
    //                 reservationStatus.setStartDevice(false);
    //                 reset.setCurrentStatus('');
    //                 reservationStatus.setIncommingNumber(undefined);
    //                 reservationStatus.setTicketID('')
    //                 reservationStatus.setCategory('')
    //                 break;
    //             }
    //         }
    //     });
    // }



    return (
        <div>
            <Modal
                show={show}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header>
                    <Modal.Title>Reservation</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    Do you want to accept the reservation
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={reservationRejected}>
                        Reject
                    </Button>
                    <Button variant="primary" onClick={reservationAccepted}>Accept</Button>
                </Modal.Footer>
            </Modal>

        </div>
    )
}

export default Reservations
