import React, { useEffect, useState } from 'react'
import { Nav, Tab } from 'react-bootstrap'
import { useMediaQuery } from 'react-responsive'
import { useChat } from '../../context/chat/chatContext'
import ChatList from '../chatList/ChatList'
import NothingToLoad from '../extras/NothingToLoad'
import Header from '../header/Header'
import './sideBar.css'

const ALL_ID = 'all'
const SideBar = () => {
    const [activeKey, setActiveKey] = useState(ALL_ID)
    const chat = useChat();
    const isDesktopOrLaptop = useMediaQuery({
        minWidth: "800px"
    });


    let drawerClasses = 'd-flex flex-column side-drawer';
    if (chat.openDrawer) {
        drawerClasses = 'd-flex flex-column side-drawer open'
    }
    useEffect(() => {
        chat.refreshConversationList()
    }, [])
    console.log(chat.refreshList, 'positively do not remove this console log');//positively do not remove this log
    return (
        <div className={isDesktopOrLaptop ? 'd-flex flex-column' : drawerClasses} style={{ height: '100vh' }}>
            <Header />
            <Tab.Container activeKey={activeKey} onSelect={setActiveKey} >


                <Nav variant="" className="justify-content-left nav-change" style={{ borderRight: '1px solid #dee2e6' }}>

                    <Nav.Item className="nav-change" style={{ borderBottom: '1px solid #3fa3ff' }} >
                        <div className="nav-text-change" style={{ color: '#3fa3ff' }} key={ALL_ID}>Conversation List</div>
                    </Nav.Item>

                </Nav>
                <Tab.Content className="border-right overflow-auto flex-grow-1 conversation-pane scroller">

                    <Tab.Pane eventKey={ALL_ID}>

                        {chat.chatList.length > 0 ? <ChatList /> : <NothingToLoad />}

                    </Tab.Pane>
                </Tab.Content>
            </Tab.Container>

        </div>
    )
}

export default SideBar
