import React, { useState, useEffect } from 'react'
import { useMediaQuery } from 'react-responsive'
import Header from '../../../chatComponents/header/Header';
import { Nav, Tab } from 'react-bootstrap';
import '../../../chatComponents/sideBar/sideBar.css'
import ChatList from '../../../chatComponents/chatList/ChatList';
import { BrowserRouter as Router, Link, Route, Switch } from 'react-router-dom';
import MyList from './chatList/MyList';
import { useChat } from '../../../context/chat/chatContext';
import AgentsList from './chatList/AgentsList';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPowerOff } from '@fortawesome/free-solid-svg-icons';
import { logoutAllAgents } from '../../../chatComponents/service/ChatApi';


const SuperVisorSidebar = () => {
    const chat = useChat();
    const MY_LIST = '/myList';
    const AGENTS_LIST = '/agentsList';
    const [activeKey, setActiveKey] = useState();
    const [corporate, setCorporate] = useState();
    let drawerClasses = 'd-flex flex-column side-drawer';
    if (chat.openDrawer) {
        drawerClasses = 'd-flex flex-column side-drawer open'
    }

    useEffect(() => {
        setCorporate(sessionStorage.getItem('corporate'))
    }, [])
    // useEffect(() => {
    //     chat.refreshConversationList()
    // }, [])
    // console.log(chat.refreshList, 'positively do not remove this console log');//positively do not remove this log
    const isDesktopOrLaptop = useMediaQuery({
        minWidth: "800px"
    });
    return (
        <div className={isDesktopOrLaptop ? 'd-flex flex-column' : drawerClasses} style={{ height: '100vh', borderRight: '1px solid #dee2e6', background: 'white' }}>
            {/* <Router> */}
            <>
                <Route render={({ location, history }) => (

                    <div>
                        <Header />
                        <Tab.Container
                            defaultActiveKey={MY_LIST}
                            activeKey={activeKey}
                            onSelect={(selected) => {
                                const to = `/${corporate}/managerDashboard` + selected;
                                if (location.pathname !== to) {
                                    history.push({
                                        pathname: to,
                                        myList: true
                                    });
                                }
                            }}
                        >
                            <Nav className="justify-content-left nav-change" style={{ borderRight: '1px solid #dee2e6' }}>
                                <Nav.Item className="nav-change"  >
                                    <Nav.Link className="nav-text-change" onClick={chat.openManagerList} eventKey={MY_LIST}>
                                        My List
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item className="nav-change" >
                                    <Nav.Link className="nav-text-change" onClick={chat.openAgentsChatList} eventKey={AGENTS_LIST}>
                                        Agents List
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item className='nav-change'>
                                    <Nav.Link className='power-btn' onClick={chat.logoutAgents}>
                                        <FontAwesomeIcon icon={faPowerOff} />
                                    </Nav.Link>
                                </Nav.Item>
                            </Nav>
                            <Tab.Content className="border-right flex-grow-1" style={{ background: 'var(--primary-background-2)' }}>
                                <Tab.Pane eventKey={MY_LIST} className='listPane scroller'>
                                    <MyList />
                                </Tab.Pane>
                                <Tab.Pane eventKey={AGENTS_LIST} style={{ height: 'calc(100vh - 92px)' }}>
                                    <AgentsList />
                                </Tab.Pane>
                            </Tab.Content>
                        </Tab.Container>
                    </div>
                )}
                />
            </>

            {/* </Router> */}

        </div>
    )
}

export default SuperVisorSidebar




