import React, { useEffect, useState } from 'react'

const Time = () => {
    let time = new Date().toLocaleTimeString(navigator.language, { hour: '2-digit', minute: '2-digit' });
    const [currentTime, setCurrentTime] = useState(time)
    useEffect(() => {

        setInterval((time) => {
            time = new Date().toLocaleTimeString(navigator.language, { hour: '2-digit', minute: '2-digit' });
            setCurrentTime(time)
        }, 60000)
    })
    return (
        <>
            <h6 style={{ color: 'white' }}>{currentTime}</h6>
        </>
    )
}

export default Time
