import React from 'react'
import '../../../chatComponents/chatList/ChatList.css';
import dialer from '../../../../assets/images/endUser.png';
import whatsApp from '../../../../assets/images/whatsapp.png';
import facebook from '../../../../assets/images/facebook.png';
import WEB from '../../../../assets/images/WEB.png';
import twitter from '../../../../assets/images/twitter.png';
import { useOutboundCall } from '../../../context/outboundCall/outboundCallContext';
import { convertUTCDateToLocalDateV2 } from '../../../utility/utility';



const Call = ({ callLogs }) => {
    const outboundCallDetails = useOutboundCall();

    const getChannelTypeImage = (channelType) => {
        switch (channelType) {
            case 'WHATSAPP':
                return (<img src={whatsApp} alt="whatsapp" />)
            case 'FACEBOOK':
                return (<img src={facebook} alt="facebook" />)
            case 'WEBCHAT':
            case 'WEB_CHANNEL':
                return (<img src={WEB} alt="web" />)
            case 'TWITTER':
                return (<img src={twitter} alt="twitter" />)
            default:
                return (<img src={dialer} alt="default" />)
        }
    }

    // console.log(callLogs.callCenter.agentId, 'list agent id');

    return (
        <div
            // style={{ background: callLogs.callCenter.agentId !== "" && '#28a7456e' }}
            className={`conversation ${callLogs._id === outboundCallDetails.selectedNumberId && "selected"}`}
            onClick={() => outboundCallDetails.selectedCallId(callLogs._id)}
        >
            {getChannelTypeImage(callLogs.channel)}

            <div
                className="title-text"
            >
                {callLogs.userId}</div>



            <div
                className="created-date"
            >
                {convertUTCDateToLocalDateV2(callLogs.createdOn.replace(' ', 'T'), false)}
                {/* {callLogs.createdOn.substring(0, 10)} */}
            </div>

            <div
                className="conversation-message"
            >
                {callLogs.callCenter.phoneNumber}

            </div>




        </div >
    )
}

export default Call
