import React, { useEffect, useState } from 'react';
import Notification from "react-web-notification";
import { useChat } from '../context/chat/chatContext';

const BrowserNotify = () => {
    const chat = useChat();

    const handlePermissionGranted = () => {
        console.log('permission granted');
        chat.setIgnore(false);
    }

    const handlePermissionDenied = () => {
        console.log('permission denied');
        chat.setIgnore(true);
    }
    const handleNotSupported = () => {
        console.log('web notification not supported');
        chat.setIgnore(true);
    }
    const handleNotificationOnError = (e, tag) => {
        console.log(e, 'web notification not supported', tag);

    }
    const handleNotificationOnClose = (e, tag) => {
        console.log(e, 'web notification closed', tag);

    }
    const handleNotificationOnShow = (e, tag) => {
        console.log(e, 'web notification show', tag);

    }
    const handleNotificationOnClick = (e, tag) => {
        console.log(e, 'web notification show', tag);

    }

    useEffect(() => {
        console.log("GETNOTIFICATIONS")
        if (chat.swRegis && chat.swRegis.getNotifications) {
            chat.swRegis.getNotifications(chat.options).then(function (notifications) {
                console.log("swrigis", notifications);
            });
        }
    }, [chat.options, chat.swRegis])



    return (
        <>

            {
                chat.swRegis ? <Notification
                    ignore={chat.ignore}
                    notSupported={handleNotSupported}
                    onPermissionGranted={handlePermissionGranted}
                    onPermissionDenied={handlePermissionDenied}
                    onShow={handleNotificationOnShow}
                    // onClick={handleNotificationOnClick}
                    onClose={handleNotificationOnClose}
                    onError={handleNotificationOnError}
                    timeout={5000}
                    options={chat.options}
                    title={chat?.options?.title ? chat.options.title : "Notification"}
                    swRegistration={chat.swRegis}
                /> : null
            }
        </>
    );
};

export default BrowserNotify;
