const removetext = (data) => {
    if (typeof data === "string") {
        let editedtext = data.replace("__TRANSLITERATE__::", "");
        editedtext = editedtext.replace("<notranslate>", "");
        editedtext = editedtext.replace("</notranslate>", "");
        editedtext = editedtext.replace("__NO_TRANSLITERATE__::", "");
        editedtext = editedtext.replace("<reverse_transliterate>", "")
        editedtext = editedtext.replace("</reverse_transliterate>", "")
        editedtext = editedtext.replace("__TRANSLITERATION_TRANSLATE__::", "")

        return maketextbold(replaceAngleBrackets(editedtext));
    }
}

const NO_IMAGE_AVAILABLE = '/assets/no image available.jpg'

export const maketextbold = (input) => {
    let res = input.replace(/\*(.*?)\*/gm, function (str) {
        str = "<strong>" + str.replace(/\*/g, "") + "</strong>"

        return str

    })

    return interpretnewline(res);

}
export const interpretnewline = (str) => {
    return str.replace(/\n/g, "<br>");

}
export const replaceAngleBrackets = (editedtext) => {
    editedtext = editedtext.replace(/</g, "&lt;")
    editedtext = editedtext.replace(/>/g, "&gt;")
    return editedtext
}


const TextComp = ({ data }) => {

    return (
        <p className="SimpleText" dangerouslySetInnerHTML={{ "__html": removetext(data) }}>
        </p >
    )
}
const ButtonComp = ({ buttons }) => {
    return (
        <div
            style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}
        // justifyContent="center"
        >
            {buttons.map((button, index) => {

                return (
                    // <div
                    //     className="OptionButton"

                    // >
                    //     <p style={{ margin: '5px 10px', fontSize: "14px", textAlign: 'center' }}>
                    //         {button.title}
                    //     </p>

                    // </div>
                    <div
                        className="OptionButtonFB"
                        key={index}
                        title={button.title}
                        style={{ backgroundColor: '#8b99a7', marginBottom: '0.3rem', borderRadius: '1rem', width: '100%', size: "small" }}
                        variant="solid"
                        isdisabled
                    >
                        <p>
                            {button.title}
                        </p>
                    </div>
                );
            })}
        </div>
    );
}
const ButtonTemp = ({ data }) => {

    return (
        <div style={{ borderRadius: "small", backgroundColor: "white", borderWidth: "small", bordercolor: "black" }} >
            <div style={{ borderRadius: "small", width: "100%" }} >
                {
                    <TextComp data={data.text} />
                }
            </div>
            {
                data.buttons &&
                <ButtonComp buttons={data.buttons} />
            }
        </div>
    )
}

const GenricTemp = ({ messageData }) => {



    let myGalleryScroller = null;
    let leftButton = null;
    let rightButton = null;

    useEffect(() => {
        showScrollButtons()
    })

    const scrollTo = (e, amount) => {
        let pos = 0;
        let children = myGalleryScroller.querySelectorAll(":scope > .gallery_scroller_item");
        children.forEach((child) => {
            if (child.offsetLeft < myGalleryScroller.scrollLeft + (myGalleryScroller.offsetWidth / 2)) {
                pos = child.offsetWidth;
            }
        });
        if (amount < 0) {
            pos = myGalleryScroller.scrollLeft - pos;
        }
        else {
            pos = myGalleryScroller.scrollLeft + pos;
        }
        myGalleryScroller.scrollTo({
            left: pos,
            behavior: "smooth"
        })
    }

    const onScrollCards = (e) => {
        showScrollButtons();
    }

    const showScrollButtons = () => {
        if (leftButton) {
            leftButton.style.display = (myGalleryScroller.scrollLeft < 10) ? "none" : "flex";
        }
        if (rightButton) {
            rightButton.style.display = myGalleryScroller.scrollLeft > (myGalleryScroller.scrollWidth - myGalleryScroller.clientWidth - 10) ? "none" : "flex"
        }
    }

    const renderImages = () => {
        return messageData?.attachment.payload.elements.map((element, i) => {
            return (
                <div key={i} className="gallery_scroller_item">
                    <img className="imgView" src={element.image_url ? element.image_url : NO_IMAGE_AVAILABLE} alt="" />
                </div>
            )
        })
    }

    const render = () => {
        return (
            <div className="gallery_scroller scroller" ref={ref => myGalleryScroller = ref} onScroll={(e) => { onScrollCards(e); }}  >
                {renderImages()}
            </div >
        )
    }

    return (

        <div style={{ width: "100%", display: "flex", flexDirection: "column" }}>

            <div className="gallery">
                <div className="gallery_scroller scroller" ref={ref => myGalleryScroller = ref} onScroll={(e) => onScrollCards(e)} >
                    <div style={{
                        display: "flex"
                    }}>
                        {
                            messageData?.attachment.payload.elements.map((element, i) => {
                                return (
                                    <div key={i} className="gallery_scroller_item" style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", padding: 4, width: "240px" }}>
                                        <div className="CardType1">
                                            <div className="ImageType1">
                                                <img className="imgView" src={element.image_url ? element.image_url : NO_IMAGE_AVAILABLE} alt="" />
                                            </div>
                                            <div className="HSeparator"></div>
                                            <p className="Header1">
                                                {element.title}
                                            </p>
                                            <p className="SimpleText">
                                                {element.subtitle}
                                            </p>
                                            <ButtonComp buttons={element.buttons} />
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>

                </div>
                <div
                    onClick={(e) => scrollTo(e, -200)} ref={ref => leftButton = ref}
                    style={{ cursor: 'pointer', position: 'absolute', top: '50%', left: '10px', height: '23px', width: '23px', borderRadius: '50%', background: '#fff', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', boxShadow: 'gray 0px 3px 23px' }}
                >
                    <MdKeyboardArrowLeft />
                </div>
                <div
                    onClick={(e) => scrollTo(e, 200)} ref={ref => rightButton = ref}
                    style={{ cursor: 'pointer', position: 'absolute', top: '50%', right: '10px', height: '23px', width: '23px', borderRadius: '50%', background: '#fff', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', boxShadow: 'gray 0px 3px 23px' }}
                >
                    <MdKeyboardArrowRight />
                </div>
            </div>


        </div >

    )
}






const DateComp = ({ messageData }) => {
    return (
        <div style={{ display: 'flex', justifyContent: "flex-end" }} >
            <p style={{ fontFamily: 'cursive', color: 'gray', fontSize: 'small' }}>{formatDateTimeTicket(messageData?.date, "false")}</p>
        </div>)
}
const FaceBookDataPacket = ({ messageData }) => {
    //let isopenwebview = false
    let [isopenwebview, setIsopenwebview] = React.useState(false);

    const ClickButtonComp = (buttons, DataPacket) => {
        return (
            <div
                style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}
                justifyContent="center">
                {buttons.map((button, index) => {
                    return (
                        <Button
                            key={index}
                            title={button.title}
                            style={{ backgroundColor: "#3fa3ff", size: "small", color: "white", width: "100%" }}
                            variant="solid"
                            onClick={() => {
                                setIsopenwebview(true)
                            }}
                        >

                            {button.title}
                        </Button>
                    );
                })}
            </div>
        );
    }
    const ClickButtonTemp = ({ data, DataPacket }) => {

        return (
            <div borderRadius="sm" borderwidth="small" bordercolor="blackAlpha.500" bgcolor="white" >
                <div width="100%" borderRadius="sm">
                    {
                        <TextComp data={data.text} />
                    }
                </div>
                {
                    data.buttons &&
                    ClickButtonComp(data.buttons, DataPacket)
                }
            </div>
        )
    }

    const openwebview = (DataPacket) => {

        return (
            <div>
                <div>
                    <div width="100%" justifyContent="flex-end">
                        <Button
                            isRound
                            bgcolor="blue.300"
                            colorScheme="blue"
                            size="md"
                            // icon={<CloseIcon />}
                            onClick={() => { setIsopenwebview(false) }

                            }
                        >
                            <MdHighlightOff />
                        </Button>
                    </div>
                </div>
                <div>
                    <MainServerDataPacket data={DataPacket} />
                </div>

            </div>
        )
    }




    // const toast = useToast();


    let packetComponent = null;

    if (messageData) {



        if (typeof messageData === "string") {
            packetComponent = <TextComp data={messageData} />
        } else if (messageData?.hasOwnProperty("text")) {
            packetComponent = <TextComp data={messageData.text} />
        } else if (messageData?.hasOwnProperty("attachment") && !messageData?.hasOwnProperty("dataPacket")) {
            // console.log("//////...../////")
            if (messageData?.attachment?.payload?.template_type === "button") {

                packetComponent = <ButtonTemp data={messageData?.attachment.payload} />
            } else
                if (messageData?.attachment?.payload?.template_type === "generic") {

                    packetComponent = <GenricTemp messageData={messageData} />
                }

        } else if (messageData?.hasOwnProperty("attachment") && messageData?.hasOwnProperty("dataPacket")) {

            if (isopenwebview) {
                packetComponent = openwebview(messageData?.dataPacket)
            } else if (!isopenwebview) {
                packetComponent = <ClickButtonTemp data={messageData?.attachment.payload} DataPacket={messageData?.dataPacket} />
            }


        }
        // packetComponent += <DateComp messageData={messageData} />
        return (<div width="100%" p={2} >
            <div>
                {packetComponent}
            </div>
            {/* <DateComp messageData={messageData} /> */}
        </div>)



    } else {
        return (<></>)
    }

}

export default FaceBookDataPacket;