import React, { useContext, useState, useEffect } from "react";
import {
    conversationList,
    handleStatus,
    messageList,
    newSentMessage,
    signIn,
    getAgentsChatList,
    getAgentsList,
    getManagersChatList,
    agentStatus,
    assignConversationToManager,
    logoutAllAgents,
    facebookChatHistory,
    whatsAppChatHistory,
    webChatHistory,
    tollFreeChatHistory,
    googleMessagesChatHistory
} from "../../chatComponents/service/ChatApi";
import { useStartDevice } from "../startDevice/startDeviceContext";
import { io } from 'socket.io-client'
import { config } from "../../../config/config";
import { useNotify } from "../notification/notifyContext";
import useSound from 'use-sound';
import newMsgSound from "../../../assets/audio/WhatsApp Audio.mpeg";
import newChatSound from "../../../assets/audio/newChat.mp3";
import { convertUTCDateToLocalTimeObject } from "../../utility/utility";
import BrowserNotify from "../../browseNotification/BrowserNotify";

const chatContext = React.createContext();

export const useChat = () => {
    return useContext(chatContext);
}
const endpoint = config.proxyServer
// eslint-disable-next-line no-useless-concat
let options = {
  reconnection: true
}
if(config.websocketpath) {
   options["path"] = config.websocketpath;
}

const socketMain = io(endpoint, options)

const MAXREFRESH_TIMEOUT = 70000;
const REFRESH_TIMEOUT = 10000;

export const ChatProvider = ({ children }) => {

    const [conversationsId, setConversationsId] = useState();
    const [newConversationId, setNewConversationId] = useState([]);
    const [msgSoundPlaying, setMsgSoundPlaying] = useState(false);
    const [convoSoundPlaying, setConvoSoundPlaying] = useState(false);
    const [newMsgConvoId, setNewMsgConvoId] = useState([]);
    const [swRegis, setSwRegis] = useState(null);
    const [newMsgSending, setNewMsgSending] = useState({ sending: false, newMsg: "" });
    const [openDrawer, setOpenDrawer] = useState(false);
    const [openProps, setOpenProps] = useState(false);
    const [showChat, setShowChat] = useState(false);
    const [currentState, setCurrentState] = useState();
    const [chatList, setChatList] = useState([]);
    const [chatLoading, setChatLoading] = useState(false);
    const [chats, setChats] = useState([]);
    const [isWebRtcEnable, setIsWebRtcEnable] = useState(false);
    const [resolveAudioOrVideo, setResolveAudioOrVideo] = useState(false);
    const [loggedIn, setLoggedIn] = useState(false);
    const [agentsList, setAgentsList] = useState([]);
    const [currentAppLogo, setCurrentAppLogo] = useState('');
    const [agentConversationList, setAgentConversationList] = useState([]);
    const [openManager, setOpenManager] = useState(false);
    const [loadAgentsChats, setLoadAgentsChats] = useState(true);
    const [agentID, setAgentID] = useState();
    const [agentId, setAgentId] = useState();
    const [isManagerList, setIsManagerList] = useState(true);
    const [isManager, setIsManager] = useState(false);
    const [isAgentLoggedIn, setIsAgentLoggedIn] = useState(false);
    const [assignedChatToMgr, setAssignedChatToMgr] = useState(false);
    const [currentAgentStatus, setCurrentAgentStatus] = useState('offline');
    const [prevChats, setPrevChats] = useState([]);
    const [reloadList, setReloadList] = useState(false);
    const [saveRefreshTime, setSaveRefreshTime] = useState(new Date());
    const [refreshList, setRefreshList] = useState(false);
    const startDevice = useStartDevice();
    const notify = useNotify();
    const [msgSound] = useSound(newMsgSound);
    const [convoSound] = useSound(newChatSound);
    const [ignore, setIgnore] = useState(true);
    const [title, setTitle] = useState("");
    const [options, setOptions] = useState({});


    useEffect(() => {
        if (msgSoundPlaying) {
            msgSound()
            setMsgSoundPlaying(false)
        }

    }, [msgSoundPlaying])

    useEffect(() => {
        if (convoSoundPlaying) {
            convoSound()
            setConvoSoundPlaying(false)
        }

    }, [convoSoundPlaying])

    useEffect(() => {
        socketMain.on('agentConversation', handleSocketNewConversation)

        socketMain.on('agentMessage', handleSocketAgentMessage)
        return () => {
            socketMain.off('agentMessage', handleSocketAgentMessage)
            socketMain.off('agentConversation', handleSocketNewConversation)
        }
    }, [conversationsId])

    useEffect(() => {

        return () => {
            socketMain.off('__refreshAgent__', (data) => {
                console.log(' __refreshAgent__ socket event removed', data);
            });
            socketMain.off('__agent_login_logout__', (data) => {
                console.log('__agent_login_logout__ socket event removed', data);
            })
            socketMain.off('__manager_logged_off', (data) => {
                console.log('__manager_logged_off socket event removed', data);
            })
            socketMain.off('__conversation_unattended__', (data) => {
                console.log('__conversation_unattended__ socket event removed', data);
            })

        }
    }, [])

    const selectAgentChat = (id) => {
        setAgentID(id)
        setShowChat(false);
        sessionStorage.setItem('selectedAgentId', id)
        handleAgentsChatList(id)
        setLoadAgentsChats(false);
        const agentStatus = agentsList.find((list) => (list.agentID === id && list.status));
        if (agentStatus !== undefined) {
            setCurrentAgentStatus(agentStatus.status);
        }
    }



    const openManagerList = () => {
        setShowChat(false);
        setIsManagerList(true);
        handleConversationList(agentId)
    }

    const logoutAgents = () => {
        const alert = "Are you sure you want to logout all agents";
        if (window.confirm(alert) === true) {
            const logout = logoutAllAgents();
            console.log('logout all agents', logout)
        } else {
            console.log('cancled logging out all agents')
        }
    }

    const openAgentsChatList = () => {
        setShowChat(false);
        setIsManagerList(false);
        handleAgentsList()
        if (agentID !== undefined) {
            handleAgentsChatList(agentID)
        }
    }

    // web notification   ///////////////////////////////////////////////////////
    const handleNotificationDetails = (newTitle, channel, message) => {
        // debugger
        if (channel === "WEB_CHANNEL") {
            channel = "WEB"
        }
        let isIgnored = true;
        setIgnore((prev) => {
            isIgnored = prev
            return prev
        })
        if (isIgnored) {
            return
        }

        setOptions({
            title: newTitle || "title",
            tag: Date.now(),
            body: channel + " - " + message,
            icon: currentAppLogo,
            lang: "en",
            dir: "ltr",
            //   sound: "../sound.mp3"
        })

    }

    useEffect(() => {
        if (navigator?.serviceWorker) {
            navigator.serviceWorker.register('/sw.js');
            navigator.serviceWorker.ready.then(function (registration) {
                setSwRegis(registration)
                console.log('service worker registration', registration)
            }).catch((err) => {
                console.log('set=rvice worker registration', err)
            })
        }
    }, [])

    /////////////////////////////////////////////////////////////////


    /////////////////////// previous chat history /////////////////////

    const getChatHistory = () => {
        // debugger
        let channel;
        let ticketID;
        if (sessionStorage.getItem('workerToken') && startDevice.ticketID) {
            return getPreviousChats("TOLL_FREE", startDevice.ticketID)
        }
        else if (isManagerList) {
            if (chatList !== undefined) {

                const channelType = chatList.find((channel) => channel.conversation.id === conversationsId && channel.conversation.additionalAttributes.channelType)
                if (channelType?.conversation !== undefined) {
                    channel = channelType.conversation.additionalAttributes.channelType;
                    ticketID = channelType.conversation.additionalAttributes.ticketID;
                    return getPreviousChats(channel, ticketID)
                }
            }
        } else {

            if (agentConversationList !== undefined) {

                const channelType = agentConversationList.find((channel) => channel.conversation.id === conversationsId && channel.conversation.additionalAttributes.channelType)

                if (channelType?.conversation !== undefined) {
                    channel = channelType.conversation.additionalAttributes.channelType;
                    ticketID = channelType.conversation.additionalAttributes.ticketID;
                    return getPreviousChats(channel, ticketID)
                }
            }
        }

        // if (chats !== []) {
        // }
    }

    useEffect(() => {
        getChatHistory()
    }, [conversationsId, isManagerList])

    useEffect(() => {
        if (startDevice.ticketID) {
            getChatHistory()
        }
    }, [startDevice.ticketID])

    const trimChats = (oldChats) => {
        // const currentChats = chats.map(arr => {
        //     return arr.message
        // })
        const trimmedChats = oldChats.filter((arr) => {
            console.log('inside trimmed array', arr.data.text)
            return chats.every(arr1 => {
                return arr.data.text !== arr1.message
            })
        })
        return trimmedChats
    }

    const getPreviousChats = async (channelType, ticketId) => {
        if (channelType === 'FACEBOOK') {
            const fbchats = await facebookChatHistory(ticketId)
            const { data } = fbchats;
            // const oldChats = data.data.message_data;
            console.log('facebook complete chat history', data.data.message_data);
            setPrevChats(data.data.message_data);
        }
        else if (channelType === 'WHATSAPP') {
            const wachats = await whatsAppChatHistory(ticketId);
            const { data } = wachats;
            // const oldChats = data.data.message_data;
            setPrevChats(data.data.message_data);

            console.log('whatsapp complete chat history', data.data.message_data);
        } else if (channelType === 'WEB_CHANNEL') {
            const webchats = await webChatHistory(ticketId);
            const { data } = webchats;
            // const oldChats = data.data.message_data;
            console.log(' webchannel complete chat history', data.data.message_data);
            // setPrevChats(trimChats(oldChats));
            setPrevChats(data.data.message_data);
        } else if (channelType === 'GOOGLE_MESSAGES') {
            const gmChats = await googleMessagesChatHistory(ticketId);
            const { data } = gmChats;
            console.log("gm chats", data);
            console.log("google msg history", data.data.message_data);
            setPrevChats(data.data.message_data);

        } else if (channelType === "TOLL_FREE") {
            const tollFree = await tollFreeChatHistory(ticketId);
            const { data } = tollFree;
            console.log("toolFree", data.data.message_data)

            setPrevChats(data.data.message_data)
        }
    }


    //////////////////////////////////////////////////////////////////

    const handleAgentsChatList = async (agentId) => {
        const agentsList = await getAgentsChatList(agentId);
        if (agentsList) {
            const { data } = agentsList;
            const { Conversations } = data;
            setAgentConversationList(Conversations);
            console.log('agents chats list', Conversations);
            setLoadAgentsChats(true);
            setAssignedChatToMgr(false);
        }
    }
    const handleManagersChatList = async (managerId) => {
        const managersChatList = await conversationList(managerId)
        if (managersChatList) {
            const { data } = managersChatList.data
            console.log('managers chat list', data);
            setChatList(data);
        }
    }

    const changeAgentStatus = async (agentId, resolve) => {
        const status = await agentStatus(agentId, resolve);
        if (status) {
            console.log('changeStatus', status);
            const { data } = status;

            if (data.success) {
                setIsAgentLoggedIn(false);
                handleAgentsList()
                setCurrentAgentStatus('offline');
            }

        }
    }

    //time diff logic
    function diffInSeconds(time1, time2) {
        return Math.floor((time1.getTime() - time2.getTime()) / 1000);
    }

    const computeState = (ClastMsgTime, AlastMsgTime, source) => {
        let nowTime = new Date();
        const REPLIED_TIME1 = 30;
        const REPLIED_TIME2 = 10;
        const COLOR_RED = 1;
        const COLOR_ORANGE = 2;
        const COLOR_YELLOW = 3
        const COLOR_GREEN = 4;
        const COLOR_WHITE = 5;
        if (source === undefined) {
            console.log('source undefined');
            return COLOR_WHITE;
        }
        if (source === "SYSTEM") {
            if (diffInSeconds(nowTime, ClastMsgTime) > REPLIED_TIME1) {
                console.log("time difference > REPLY_TIME1(30s)");
                return COLOR_RED;
            }
            if (diffInSeconds(nowTime, ClastMsgTime) > REPLIED_TIME2) {
                console.log("time difference > REPLY_TIME2(10s)");
                return COLOR_ORANGE;
            }
        }
        if (source !== 'CONTACT') {
            console.log('source !== contact');
            return COLOR_WHITE;
        }
        // console.log(ClastMsgTime, 'ast time');

        if (AlastMsgTime) {
            if (ClastMsgTime.getTime() < AlastMsgTime.getTime()) {
                console.log('if agent last time is defined.. client last message time < Agent last message time');
                return COLOR_GREEN;
            }
        }
        if (diffInSeconds(nowTime, ClastMsgTime) > REPLIED_TIME1) {
            console.log("if agent last time is defined... time difference > REPLY_TIME1(30s)");
            return COLOR_RED;
        }
        if (diffInSeconds(nowTime, ClastMsgTime) > REPLIED_TIME2) {
            console.log("if agent last time is defined.... time difference > REPLY_TIME2(10s)");
            return COLOR_ORANGE;
        }
        if (AlastMsgTime) {
            if (diffInSeconds(ClastMsgTime, AlastMsgTime) >= REPLIED_TIME2) {
                console.log('differece in client last message time and agent last message time > REPLY_TIME2');
                if (diffInSeconds(nowTime, ClastMsgTime) >= REPLIED_TIME2) {
                    console.log('difference in client time and client last message time > REPLY_TIME2');
                    return COLOR_ORANGE
                }
            }
        }
        // console.log('time < 3min');
        return COLOR_GREEN;

    }

    const lastSeenTime = (clientLastseen, agentLastSeen, source) => {
        const ClastSeen = convertUTCDateToLocalTimeObject(clientLastseen);
        let AlastSeen = null;
        if (source === 'CONTACT' && agentLastSeen) {
            AlastSeen = convertUTCDateToLocalTimeObject(agentLastSeen);
        }
        const state = computeState(ClastSeen, AlastSeen, source);
        return state;
    }

    /////////////////////////////////////////////////////////////////////////////

    const reloadAgentsList = () => {
        // console.log('inside function', reloadList);
        setInterval(() => {
            console.log('inside set interval reload list', reloadList);
            setReloadList((prev) => {
                console.log('inside ', prev)
                if (prev) {
                    console.log("inside setReloadList", prev)
                    const trigredAgentId = parseInt(sessionStorage.getItem('trigredAgentId'));
                    const selectedAgentId = parseInt(sessionStorage.getItem('selectedAgentId'))
                    // console.log("inside true", "selected agent id", trigredAgentId, "current agent id", selectedAgentId);
                    let channel;
                    let ticketID;
                    setAgentConversationList((prev) => {
                        if (trigredAgentId && selectedAgentId) {
                            if (trigredAgentId === selectedAgentId) {
                                const channelType = prev.find((channel) => channel.conversation.id === +sessionStorage.getItem("selectedConvoId"))
                                if (channelType?.conversation !== undefined) {
                                    channel = channelType.conversation.additionalAttributes.channelType;
                                    ticketID = channelType.conversation.additionalAttributes.ticketID;
                                    getPreviousChats(channel, ticketID)
                                }
                            }
                        }
                        return prev
                    })

                    handleAgentsList();

                    if (trigredAgentId && selectedAgentId) {
                        if (trigredAgentId === selectedAgentId) {
                            handleAgentsChatList(trigredAgentId);
                        }
                    }
                    const saveTime = new Date()
                    // console.log('inside true condition', saveTime);
                    setSaveRefreshTime(saveTime);
                    setReloadList(false)
                    // return !prev
                } else {
                    console.log("inside !reload list", reloadList)
                    let nowTime = new Date();
                    if (saveRefreshTime) {
                        // console.log(nowTime, 'now time');
                        // console.log(saveRefreshTime, 'save  time');
                        setSaveRefreshTime((saveTime) => {
                            console.log((nowTime.getTime() - saveTime.getTime()) < (MAXREFRESH_TIMEOUT), 'nowtime - gettime');
                            if ((nowTime.getTime() - saveTime.getTime()) < (MAXREFRESH_TIMEOUT)) {
                                const trigredAgentId = parseInt(sessionStorage.getItem('trigredAgentId'));
                                const selectedAgentId = parseInt(sessionStorage.getItem('selectedAgentId'))
                                // console.log("inside false", "selected agent id", trigredAgentId, "current agent id", selectedAgentId);
                                handleAgentsList();
                                if (trigredAgentId && selectedAgentId) {
                                    if (trigredAgentId === selectedAgentId) {
                                        handleAgentsChatList(trigredAgentId);
                                    }
                                }
                            }
                            return saveTime;
                        })
                    }
                }
                return prev
            })

        }, REFRESH_TIMEOUT)
    }



    function refreshConversationList() {
        setInterval(() => {
            setRefreshList((prev) => {
                return !prev
            });
            // console.log('reload conversation list');
        }, REFRESH_TIMEOUT)
    }


    const getCurrentStatusOfAgent = (agentList) => {
        const trigredAgentId = parseInt(sessionStorage.getItem('trigredAgentId'));
        const agentStatus = agentList.find((list) => (list.agentID === trigredAgentId && list.status));
        if (agentStatus) {
            setCurrentAgentStatus(agentStatus.status);
        }

    }


    const handleAssignConvoToMgr = async (converseId) => {
        const assigned = await assignConversationToManager(converseId);
        if (assigned) {
            const { data } = assigned;
            if (data.success) {
                const list = agentConversationList.find((lists) => lists.conversation.id === converseId);
                console.log('asigned agent to manager', list);
                if (list.conversation.id === converseId) {
                    setShowChat(false);
                    handleAgentsChatList(agentID);
                    setAssignedChatToMgr(true);
                }
            }
        } else {
            setShowChat(true);
            setAssignedChatToMgr(false)
        }
        // console.log('assiged convo', assigned);
    }

    const handleAgentsList = async () => {
        const agentsConvoList = await getAgentsList();
        if (agentsConvoList) {
            const { data } = agentsConvoList;
            const { AgentsList } = data;
            console.log('agents list', AgentsList);
            // setCurrentAgentStatus(AgentsList.status);
            getCurrentStatusOfAgent(AgentsList);
            setAgentsList(AgentsList);
            setOpenManager(true);
        }
    }

    const handleSocketNewConversation = (conversationDetails) => {
        console.log('conversatin created details', conversationDetails);
        // console.log(conversationDetails.conversationid);
        setNewConversationId([...newConversationId, conversationDetails.conversationid])
        handleNotificationDetails('New Conversation', conversationDetails.additionalAttributes.channelType, 'created')
        handleConversationList(sessionStorage.getItem('agentId'))
        // console.log('new conversation id', newConversationId);
        setConvoSoundPlaying(true);
    }

    const handleSocketAgentMessage = messageDetails => {
        console.log("message details", messageDetails);
        if (messageDetails.fromAgent) {

            setNewMsgSending({ sending: false, newMsg: "" })
        }
        // debugger
        let managerList;
        setIsManagerList((prev) => {
            managerList = prev;
            return prev

        })
        if (conversationsId !== messageDetails.messageDetails.conversationid || !managerList) {
            setMsgSoundPlaying(true)
            setNewMsgConvoId([...newMsgConvoId, messageDetails.messageDetails.conversationid]);
            handleConversationList(agentId)
            handleNotificationDetails('New Message', messageDetails.messageDetails.additionAttributes.channelType, messageDetails.messageDetails.message)

        }
        // else if (!managerList) {
        //     setMsgSoundPlaying(true)
        //     setNewMsgConvoId([...newMsgConvoId, messageDetails.messageDetails.conversationid]);
        //     // handleManagersChatList(managerId);
        //     handleConversationList(agentId)
        //     handleNotificationDetails('New Message', messageDetails.messageDetails.additionAttributes.channelType, messageDetails.messageDetails.message)
        // }
        else {
            // handleMessageList(conversationsId);

            getChatHistory()
            handleConversationList(sessionStorage.getItem('agentId'))
            if (messageDetails.messageDetails?.additionAttributes?.channelType && messageDetails?.messageDetails?.message) {
                handleNotificationDetails('New Message', messageDetails.messageDetails.additionAttributes.channelType, messageDetails.messageDetails.message)
            }

        }

    }

    const drawerClickToggleHandler = () => {
        setOpenDrawer(!openDrawer)
    }
    const propDrawerToggle = () => {
        setOpenProps(!openProps)
    }
    const backDropClickHandler = () => {
        setOpenDrawer(false);
        setOpenProps(false);
    }

    const handleSignIn = async (loginData) => {
        // console.log(loginData, 'logindata');
        const res = await signIn(loginData);
        if (res) {
            const { data } = res;
            console.log('login details', data)
            // console.log('login status', data.status);
            sessionStorage.setItem('defaultAgentName', "Agent(" + data.Agent + ")");
            sessionStorage.setItem('agentName', data.name);
            sessionStorage.setItem('agentId', data.Agent);
            if (data.jwt) {
                sessionStorage.setItem('token', data.jwt);
            } else {
                sessionStorage.setItem('token', data.token)
            }
            sessionStorage.setItem(`email${data.Agent}`, data.email)
            sessionStorage.setItem(`password${data.Agent}`, data.password)
            sessionStorage.setItem(`accountname`, data.accountname)
            sessionStorage.setItem('workerToken', data.workerToken)
            sessionStorage.setItem('clientToken', data.voiceToken)
            sessionStorage.setItem('workerSid', data.workerSid)
            sessionStorage.setItem('identity', data.identity)
            setIsWebRtcEnable(data.webRTC);
            setAgentId(data.Agent);
            if (data.isManager) {
                setIsManager(true);
                sessionStorage.setItem('isManager', data.isManager)
                handelChangeStatus(data.status);
                handleConversationList(data.Agent)
                // handleManagersChatList(data.Agent);
                // setManagerId(data.Agent);
                handleAgentsList();
                agentRegis(data.Agent);
            } else if (data.workerToken !== undefined) {
                setLoggedIn(true)
                startDevice.registerWorker(sessionStorage.getItem('workerToken'));
            } else {
                sessionStorage.setItem('isManager', data.isManager)
                setLoggedIn(true)
                agentRegis(data.Agent)
                handelChangeStatus(data.status);
                handleConversationList(data.Agent)
            }
            // if (data.workerToken !== undefined) {
            //     startDevice.registerWorker(sessionStorage.getItem('workerToken'));

            // }
            // if (data.Agent) {
            //     handelChangeStatus(data.status);
            //     agentRegis()
            //     handleConversationList(data.Agent, data.token)
            // }
        }
    }

    const delSelectedId = (conversationArr, prevId) => {
        setNewConversationId(conversationArr.filter(id => id !== prevId))
        setNewMsgConvoId(conversationArr.filter(id => id !== prevId))
    }


    const selectChat = (id) => {
        setPrevChats([])
        setConversationsId(id);
        sessionStorage.setItem("selectedConvoId", id)
        // handleMessageList(id);
        getChatHistory()
        // if (chats !== []) {
        //     getPreviousChats(channel, ticketID)
        // }
        setShowChat(true);
        if (newMsgConvoId.includes(id) || newConversationId.includes(id)) {
            delSelectedId(newConversationId, id);
        }
        // setDisableResolve(false);
        setOpenDrawer(false);
    }




    const agentRegis = (id) => {
        const agentDetails = {
            agentid: id,
            accountname: sessionStorage.getItem('accountname'),
        }
        console.log('agent details after sign in', agentDetails);
        if (agentDetails.agentid !== null && agentDetails.accountname !== null) {
            socketMain.emit("agentRegister", agentDetails)
        }
        socketMain.removeAllListeners('connect')
        socketMain.removeAllListeners('disconnect')


        socketMain.on('disconnect', () => {
            startDevice.setConnected(false);
        })

        socketMain.on("connect", () => {
            startDevice.setConnected(true);
            if (agentDetails.agentid !== null && agentDetails.accountname !== null) {
                socketMain.emit("agentRegister", agentDetails)
            }
        });


        socketMain.on("multipleLogin", (data) => {
            console.log('multiple Login socket trigred', data);
            if (+data.agentid === +agentDetails.agentid && data.accountname === agentDetails.accountname) {
                notify.multipleLogin();
                setTimeout(() => {
                    window.location.href = `/${sessionStorage.getItem('corporate')}`;
                    sessionStorage.clear();
                }, 3000)

            }

        })
        socketMain.on("__manager_logged_off", (data) => {
            console.log('__manager_logged_off socket event trigred', data);
            if (+data.agentid === +agentDetails.agentid && data.accountname === agentDetails.accountname) {
                handleNotificationDetails("Logged Off", "System", "By Manager")
                notify.managerLoggedUoff();
                setTimeout(() => {
                    window.location.href = `/${sessionStorage.getItem('corporate')}`;
                    sessionStorage.clear();
                }, 3000)

            }

        })

        socketMain.on('__agent_login_logout__', (data) => {
            console.log('__agent_login_logout__ event trigred', data);
            sessionStorage.setItem('trigredAgentId', data.agentid)
            setAgentID(data.agentid)
            handleAgentsList();
        })
        socketMain.on('__manager_logged_off', (data) => {
            console.log("__manager_logged_off socket event trigred", data);
            sessionStorage.setItem('trigredAgentId', data.agentid);
            handleNotificationDetails("Logged Off", "System", "By Manager")
            notify.managerLoggedUoff();
            setAgentID(data.agentid)
            handleAgentsList();

        })

        socketMain.on('__refreshAgent__', (data) => {
            sessionStorage.setItem('trigredAgentId', data.agentid);
            console.log('__refreshAgent__ event trigred ', data);
            setReloadList(true);
        })

        socketMain.on('__clientOffline__', (data) => {
            notify.ticketCancled();
            console.log('__clientOffline__ event trigred ', data);
        })

        socketMain.on('__conversation_unattended__', (data) => {
            console.log('__conversation_unattended__ event trigred ', data);
            handleConversationList(data.agentid);
            setResolveAudioOrVideo(true);
            notify.convoUnAttended()
            handleNotificationDetails('Conversation unattended', "System", "Resolved")
            setShowChat(false);
            // setDisableResolve(true)
        })

    }

    const handleMessageList = async (conversationId) => {
        const messages = await messageList(conversationId);
        if (messages) {
            console.log('message list', messages.data.data);
            setChats(messages.data.data);
        }
    }

    const handleNewSentMessage = async (newMsg, conversationId) => {
        const newMessage = {
            conversationid: conversationId,
            message: newMsg,
            accountname: sessionStorage.getItem('accountname'),
        }
        setNewMsgSending({ sending: true, newMsg: newMsg })
        setChats([...chats, newMessage])
        const sentMsg = await newSentMessage(newMsg, conversationId);
        console.log('sending message', sentMsg);
        if (sentMsg) {
            getChatHistory()
            handleConversationList(sessionStorage.getItem('agentId'))
            setNewMsgSending({ sending: false, newMsg: newMsg })
            // console.log('new msg', sentMsg)
            // handleMessageList(conversationId);
        }
    }

    const handleConversationList = async (id) => {
        const list = await conversationList(id);
        if (list) {
            console.log('conversation list', list.data.data);
            setChatList(list.data.data);
            setChatLoading(true);
        }
    }
    const handelChangeStatus = async (status) => {
        const res = await handleStatus(status);
        if (res) {
            setCurrentState(res.data.status);
        }
    }


    const value = {
        agentConversationList,
        resolveAudioOrVideo,
        currentAgentStatus,
        newConversationId,
        assignedChatToMgr,
        conversationsId,
        loadAgentsChats,
        isAgentLoggedIn,
        isWebRtcEnable,
        // disableResolve,
        currentAppLogo,
        newMsgConvoId,
        isManagerList,
        newMsgSending,
        currentState,
        chatLoading,
        openDrawer,
        openManager,
        refreshList,
        agentsList,
        openProps,
        prevChats,
        isManager,
        loggedIn,
        agentID,
        chatList,
        showChat,
        options,
        swRegis,
        ignore,
        title,
        chats,
        setChats,
        setTitle,
        setIgnore,
        selectChat,
        setShowChat,
        handleSignIn,
        setPrevChats,
        lastSeenTime,
        logoutAgents,
        setOpenProps,
        setOpenDrawer,
        openManagerList,
        selectAgentChat,
        getPreviousChats,
        reloadAgentsList,
        propDrawerToggle,
        handleAgentsList,
        changeAgentStatus,
        handleMessageList,
        // setDisableResolve,
        setCurrentAppLogo,
        setIsAgentLoggedIn,
        openAgentsChatList,
        handelChangeStatus,
        setAssignedChatToMgr,
        handleNewSentMessage,
        backDropClickHandler,
        setCurrentAgentStatus,
        handleAssignConvoToMgr,
        handleManagersChatList,
        refreshConversationList,
        setResolveAudioOrVideo,
        handleConversationList,
        handleNotificationDetails,
        drawerClickToggleHandler,
    }

    return (
        <>
            <chatContext.Provider value={value} >
                {children}
                <BrowserNotify />
            </chatContext.Provider>
        </>
    )
}