import React, { useEffect } from 'react'
import './Loading.css'
import { useParams } from 'react-router-dom'
import { useChat } from '../../context/chat/chatContext';

const Loading = () => {
    const { corporate } = useParams();
    const chat = useChat();

    useEffect(() => {

        if (!chat.loggedIn && sessionStorage.getItem('token') === null) {
            if (window.location.pathname.includes('dashboard') || window.location.pathname.includes('Almawiz_Partner1/twilioDashboard/inbound')) {
                sessionStorage.clear();
                window.location.href = `/${corporate}`;
            }
        }
        if (!chat.isManager && sessionStorage.getItem('token') === null) {
            if (window.location.pathname.includes('managerDashboard/myList') || window.location.pathname.includes('managerDashboard/agentsList')) {
                sessionStorage.clear();
                window.location.href = `/${corporate}`;
            }
        }
    }, [])
    return (
        <div className="sk-folding-cube">
            <div className="sk-cube1 sk-cube"></div>
            <div className="sk-cube2 sk-cube"></div>
            <div className="sk-cube4 sk-cube"></div>
            <div className="sk-cube3 sk-cube"></div>
        </div>
    )
}

export default Loading
