import React, { useEffect, useState, useRef } from 'react';
import { Button, Form } from 'react-bootstrap';
import { faPaperPlane } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useChat } from '../../context/chat/chatContext';
const ChatSend = ({ wraperRef, sentMessage, setsentMessage }) => {
    const chat = useChat();
    const innerRef = useRef();
    // const wraperRef = useRef();
    // const [sentMessage, setsentMessage] = useState('');
    const [resized, setResized] = useState(false);
    const [keyboardOpen, setKeyboardOpen] = useState(false);
    const appHeight = () => {
        if (wraperRef && wraperRef.current && wraperRef.current.style) {
            wraperRef.current.style.height = window.innerHeight + 'px'
            setResized(true);
        }
    }
    window.addEventListener('resize', appHeight)

    useEffect(() => {
        appHeight()
    }, [keyboardOpen])

    function xresize() {
        let height_new = window.innerHeight;
        var diff = Math.abs(height_old - height_new);
        var perc = Math.round((diff / height_old) * 100);
        // console.log(height_new, 'new height');
        // console.log(height_old, 'old height');
        // console.log(diff, 'diff');
        // console.log(perc, 'perc');
        if (perc > 50) {
            xblur();
            window.removeEventListener('resize', xresize);
        }
    }
    let height_old = window.innerHeight;
    const xFocus = (e) => {
        setTimeout(function () {
            height_old = window.innerHeight;
            window.scroll(0, 0)
            window.addEventListener('resize', xresize);
            setKeyboardOpen(true)

        }, 500);
        appHeight();
    }
    function xblur() {
        setKeyboardOpen(false)
        window.removeEventListener('resize', xresize);
    }


    return (
        <div className="send-bar">
            <Form.Group className="input-grid">
                <Form.Control disabled={!chat.isManagerList ? true : false} onFocus={xFocus} onBlur={xblur} ref={innerRef} style={{ borderRadius: '7px' }} type="text" placeholder="Type a message..." value={sentMessage} className="send-messages" onChange={(e) => setsentMessage(e.target.value)} />
                <Button disabled={!chat.isManagerList ? true : false} variant="success" type="submit" className="send-btn" ><FontAwesomeIcon icon={faPaperPlane} ></FontAwesomeIcon></Button>
            </Form.Group>
        </div>
    )
}

export default ChatSend