import React, { useEffect, useRef } from 'react';
import { Button, Navbar, } from 'react-bootstrap';
import { useState, useCallback } from 'react';
import axios from 'axios';
import './chatBox.css'
import { config } from '../../../config/config.js'
import WEB from '../../../assets/images/WEB.png'
import whatsapp from '../../../assets/images/whatsapp.png'
import facebook from '../../../assets/images/facebook.png'
import google from '../../../assets/images/google.png'
import twitter from '../../../assets/images/twitter.png'
import userimg from '../../../assets/images/endUser.png'
import { useNotify } from '../../context/notification/notifyContext';
import DrawerToggleButton from '../sideBar/DrawerToggleButton';
import PropToggleButton from '../agentDetails/PropToggleButton';
import { useMediaQuery } from 'react-responsive';
import { useChat } from '../../context/chat/chatContext';
import ChatBubble from './ChatBubble';
import ChatSend from './ChatSend';
import DefaultPge from '../extras/DefaultPge';



const ChatBox = () => {
    const [sentMessage, setsentMessage] = useState('');
    const [channelType, setChannelType] = useState();
    const resolvedSuccess = useNotify();
    const wraperRef = useRef();
    const chat = useChat();
    const convoId = chat.conversationsId;
    const isDesktopOrLaptop = useMediaQuery({
        minWidth: "800px"
    });


    const setRef = useCallback(node => {
        if (node) {
            node.scrollIntoView({ smooth: true })
        }
    }, []);

    const isMobile = {
        Android: function () {
            return navigator.userAgent.match(/Android/i);
        },
        iOS: function () {
            return navigator.userAgent.match(/iPhone|iPad|iPod/i)
        }
    }

    // let channelType;

    useEffect(() => {
        console.log('chat list', chat.chatList, convoId)
        if (chat.isManagerList) {
            if (chat.chatList) {
                setChannelType(chat.chatList.find(channel => channel.conversation.id === convoId && channel.conversation.additionalAttributes.channelType))
            }
        } else {
            if (chat.agentConversationList) {
                const channel = chat.agentConversationList.find(channel => channel.conversation.id === convoId && channel.conversation.additionalAttributes.channelType)
                console.log('channel', channel)
                setChannelType(chat.agentConversationList.find(channel => channel.conversation.id === convoId && channel.conversation.additionalAttributes.channelType))
            }
        }

    }, [chat.agentConversationList, chat.chatList, chat.isManagerList, convoId])

    const ressolveHandle = () => {
        const changeToggle = 'resolved'


        const headers = {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${sessionStorage.getItem('token')}`
        }

        axios.post(`${config.baseUrl}/account/${sessionStorage.getItem('accountname')}/agent/${sessionStorage.getItem('agentId')}/conversations/${convoId}/toggle`,
            { conversationstatus: changeToggle },
            { headers: headers }
        ).then((res) => {
            console.log('ressolved', res);
            chat.handleConversationList(sessionStorage.getItem('agentId'))
            chat.handleMessageList(convoId)
            chat.setPrevChats([])
            chat.setResolveAudioOrVideo(true);
            resolvedSuccess.convoResolved();
            chat.setShowChat(false);
        }).catch((err) => {
            console.log(err);
            alert('conversation not resolved')
        })
    }

    const keyDownHandleMethod = (e) => {
        if (e.key === 'Enter') {
            chat.setOpenDrawer(false);
            addMessage(e, false)
        }
    }
    const addMessage = async (e, val) => {
        console.log('add message', val);
        e.preventDefault();
        if (sentMessage.trim().length !== 0) {

            const res = await chat.handleNewSentMessage(sentMessage, convoId);
            console.log('new messages added', res);
            setsentMessage('')

        }
    }

    const openResolveButton = () => {
        if (chat.conversationsId) {
            chat.handleAssignConvoToMgr(chat.conversationsId);
        }
    }




    const getChannelTypeImage = (channelType) => {
        switch (channelType) {
            case 'WEBCHAT':
            case 'WEB_CHANNEL':
                return (<img
                    alt=""
                    src={WEB}
                    width="40"
                    height="40"
                    className="d-inline-block align-top"
                    style={{ borderRadius: "100%" }}
                />)
            case 'WHATSAPP':
                return (<img
                    alt=""
                    src={whatsapp}
                    width="40"
                    height="40"
                    className="d-inline-block align-top"
                    style={{ borderRadius: "100%" }}
                />)
            case 'FACEBOOK':
                return (
                    <img
                        alt=""
                        src={facebook}
                        width="40"
                        height="40"
                        className="d-inline-block align-top"
                        style={{ borderRadius: "100%" }}
                    />
                )
            case 'GOOGLE_MESSAGES':
                return (
                    <img
                        alt=""
                        src={google}
                        width="40"
                        height="40"
                        className="d-inline-block align-top"
                        style={{ borderRadius: "100%" }}
                    />
                )
            case 'TWITTER':
                return (<img
                    alt=""
                    src={twitter}
                    width="40"
                    height="40"
                    className="d-inline-block align-top"
                    style={{ borderRadius: "100%" }}
                />)

            default:
                return (<img
                    alt=""
                    src={userimg}
                    width="40"
                    height="40"
                    className="d-inline-block align-top"
                    style={{ borderRadius: "100%" }}
                />)
        }
    }




    return (
        <form
            ref={wraperRef}
            className="wraper"
            onKeyDown={keyDownHandleMethod} onSubmit={(e) => { addMessage(e, true) }}>

            <Navbar variant="tabs"
                className="nav-bar">
                {isDesktopOrLaptop ? '' : <div id="toggle-button">
                    <DrawerToggleButton drawerClickToggleHandler={chat.drawerClickToggleHandler} />
                </div>}
                {chat.isManagerList ?
                    chat.chatList.map((profilePik, index) => profilePik.conversation.id === convoId && <div className='channel-logo' key={index}>{getChannelTypeImage(profilePik.conversation.additionalAttributes.channelType)}</div>)
                    :
                    chat.agentConversationList.map((profilePik, index) => profilePik.conversation.id === convoId && <div key={index}>{getChannelTypeImage(profilePik.conversation.additionalAttributes.channelType)}</div>)
                }
                {chat.isManagerList ?
                    chat.chatList.map((nameData, index) => nameData.conversation.id === convoId && <Navbar.Brand key={index} href="" className="sender-name">{nameData.sender.name}</Navbar.Brand>)
                    :
                    chat.agentConversationList.map((nameData, index) => nameData.conversation.id === convoId && <Navbar.Brand key={index} href="" className="sender-name">{nameData.sender.name}</Navbar.Brand>)
                }
                <div className="spacer"></div>
                <div className="justify-content-end">
                    {chat.isManagerList ?
                        <Button variant="success" className='resolve-btn' onClick={ressolveHandle} >Resolve</Button>
                        :
                        !chat.assignedChatToMgr && chat.currentAgentStatus === 'online' ?
                            <Button disabled variant="success"  >Assign Chat</Button>
                            :
                            <Button variant="success" onClick={openResolveButton} >Assign Chat</Button>
                    }
                </div>
                {chat.showChat && <div style={{ marginLeft: '0.5rem' }}>
                    {isDesktopOrLaptop ? '' : <PropToggleButton propDrawerToggle={chat.propDrawerToggle} />}
                </div>}
            </Navbar>
            {isDesktopOrLaptop ?
                <ChatBubble setRef={setRef} channelType={channelType} />
                :
                chat.showChat ?
                    <ChatBubble setRef={setRef} channelType={channelType} />
                    :
                    <DefaultPge />
            }
            <ChatSend wraperRef={wraperRef} sentMessage={sentMessage} setsentMessage={setsentMessage} />
        </form>
    )
}

export default ChatBox