import React, { useEffect, useState, useCallback } from 'react';
import Phone from './phone/Phone';
import { useStartDevice } from '../../context/startDevice/startDeviceContext';
import { useTime } from '../../context/callTime/calltimeContext';
import { useHistory } from 'react-router-dom';
import { useOutboundCall } from '../../context/outboundCall/outboundCallContext';
import NoCalls from '../../chatComponents/extras/NoCalls';


const OutboundCall = () => {
    const updatedDevice = useStartDevice();
    const callTime = useTime();
    const [corporate, setCorporate] = useState(sessionStorage.getItem('corporate'));
    const history = useHistory();
    const assign = useOutboundCall();

    const handleAcceptCall = async () => {
        const res = await assign.assignAgentInCommoninbox(assign.agentId)
        console.log(res, 'assign...');
        if (res.status === 200) {
            const refresh = await assign.refreshList();
            console.log(refresh, 'refresh');
        }

        if (updatedDevice.newDevice) {
            const outboundCall = await updatedDevice.newDevice.connect({ To: assign.selectedNumber });
            outboundCall.on("accept", function (conn) {
                console.log(conn, 'accept out');
                if (conn) {
                    const res = assign.callLogs(conn.parameters.CallSid);
                    console.log(res, 'log res');
                }
            });
            console.log(outboundCall, "Outbound Call");
            outboundCall.status(outboundCall.parameters.CallSid);
            callTime.handleOutboundConnecting()
            updatedDevice.setDisableInbound(true);
            // if (updatedDevice.callSid !== undefined) {
            // }
        } else {
            alert('unable to make call');
        }
    }
    const hangup = () => {
        if (updatedDevice.newDevice) {
            updatedDevice.newDevice.disconnectAll();
            callTime.reset()
        }
    }



    useEffect(() => {
        const to = `/${corporate}/twilioDashboard/inbound`;
        if (history.location.isInbound === undefined) {
            history.push(to)
        }
    }, [])


    return (
        <div>
            {assign.selectedNumberId ?
                <Phone hangup={hangup} selectedNumber={assign.selectedNumber} handleAcceptCall={handleAcceptCall} />
                :
                <NoCalls toDisp="Select Left Pane To Make Outbound Calls" src="/assets/images/outgoing-call.png" />
            }
        </div>
    )
}

export default OutboundCall
