import React, { useState } from 'react';
import { Modal, Button, FloatingLabel, Form, InputGroup } from 'react-bootstrap';
import axios from 'axios';
import { config } from '../../../config/config';
import { useNotify } from '../../context/notification/notifyContext';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEye, faEyeSlash } from '@fortawesome/free-solid-svg-icons'
const ChangePassword = ({ onHide, changePasswordModal }) => {
    const successfullyChanged = useNotify();
    const [testNewPassword, setTestNewPassword] = useState(false);
    const [notMatching, setNotMatching] = useState(false);
    const [newPassword, setNewPassword] = useState();
    const [oldPassword, setOldPassword] = useState();
    const [reEnterPassword, setReEnterPassword] = useState();
    const [showOldPassword, setShowOldPassword] = useState(false);
    const [showNewPassword, setShowNewPassword] = useState(false);
    const [showReEnterPassword, setShowReEnterPassword] = useState(false);
    // const handleClose = 
    const toggleOldPasswordVisiblity = () => {
        setShowOldPassword(showOldPassword ? false : true);
    };
    const toggleNewPasswordVisiblity = () => {
        setShowNewPassword(showNewPassword ? false : true);
    };
    const toggleReEnterPasswordVisiblity = () => {
        setShowReEnterPassword(showReEnterPassword ? false : true);
    };

    const handleNewPassword = (e) => {
        setNewPassword(e.target.value)
    }
    const handleReEnterPassword = (e) => {
        setReEnterPassword(e.target.value);
    }
    const handleOldPassword = (e) => {
        setOldPassword(e.target.value);
    }
    const handleSubmit = () => {
        const checkPassword = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*\\d)(?=.*[@$!%*?&])[A-Za-z\\d@$!%*?&]{8,10}$");
        const testPassword = checkPassword.test(newPassword);
        if (testPassword) {
            setTestNewPassword(false);
            if (newPassword === reEnterPassword) {
                setNotMatching(false);
                const headers = {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${sessionStorage.getItem('token')}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept'
                }
                const body = {
                    oldPassword: oldPassword,
                    newPassword: newPassword
                }
                axios.post(`${config.baseUrl}/editPassword`,
                    body,
                    { headers: headers }
                ).then((res) => {
                    console.log('password changed', res);
                    const { data } = res;
                    if (data.success) {
                        successfullyChanged.changedPassword();
                        onHide();
                    }
                }).catch((err) => {
                    if (err.response.status === 400) {
                        alert(err.response.data.error)
                    }
                })
            } else {
                setNotMatching(true);
            }
        } else {
            setTestNewPassword(true);
        }
    }
    return (
        <div>
            <Modal show={changePasswordModal}
                onHide={onHide}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>Change Password</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form.Group className="mb-3" >
                        <Form.Label>Enter the old password </Form.Label>
                        <InputGroup>
                            <Form.Control onChange={handleOldPassword} type={showOldPassword ? "text" : "password"} placeholder="Old Password" />
                            <InputGroup.Text className="see-password" onClick={toggleOldPasswordVisiblity}>{showOldPassword ? <FontAwesomeIcon icon={faEyeSlash} /> : <FontAwesomeIcon icon={faEye} />}</InputGroup.Text>
                        </InputGroup>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>Enter the new password </Form.Label>
                        <InputGroup>
                            <Form.Control onChange={handleNewPassword} type={showNewPassword ? "text" : "password"} placeholder="New Password" />
                            <InputGroup.Text className="see-password" onClick={toggleNewPasswordVisiblity}>{showNewPassword ? <FontAwesomeIcon icon={faEyeSlash} /> : <FontAwesomeIcon icon={faEye} />}</InputGroup.Text>
                        </InputGroup>
                        {testNewPassword && <Form.Text style={{ color: 'red', fontSize: 'small' }}>
                            Password must contain minimum eight and maximum 10 characters, at least one uppercase letter, one lowercase letter, one number and one special character
                        </Form.Text>}
                    </Form.Group>
                    <Form.Group className="mb-3" >
                        <Form.Label>Renter the new password </Form.Label>
                        <InputGroup>
                            <Form.Control onChange={handleReEnterPassword} type={showReEnterPassword ? "text" : "password"} placeholder="New Password" />
                            <InputGroup.Text className="see-password" onClick={toggleReEnterPasswordVisiblity}>{showReEnterPassword ? <FontAwesomeIcon icon={faEyeSlash} /> : <FontAwesomeIcon icon={faEye} />}</InputGroup.Text>
                        </InputGroup>
                        {notMatching && <Form.Text style={{ color: 'red', fontSize: 'small' }}>
                            Password not matching
                        </Form.Text>}
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={onHide}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={handleSubmit}>
                        Save Changes
                    </Button>
                </Modal.Footer>
            </Modal>
        </div>
    )
}

export default ChangePassword
