import React from 'react'
import './DrawerToggleButton.css'

const DrawerToggleButton = ({ drawerClickToggleHandler }) => {
    return (
        <button className="toggle-button" onClick={drawerClickToggleHandler} >
            <div className="toggle-button-line" />
            <div className="toggle-button-line" />
            <div className="toggle-button-line" />
        </button>
    )
}

export default DrawerToggleButton
