import React, { useEffect, useState, useMemo } from 'react';
import { ListGroup } from 'react-bootstrap';
import Call from './Call';
import './Listings.css'
import { InputGroup, FormControl, Button } from 'react-bootstrap';
import { getAgentUnavailableTickets } from '../service/api';
import { useOutboundCall } from '../../../context/outboundCall/outboundCallContext';
import { useStartDevice } from '../../../context/startDevice/startDeviceContext';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faRedo } from '@fortawesome/free-solid-svg-icons';
import NoCallsToLoad from '../../../chatComponents/extras/NoCallsToLoad';


const OutboundCallList = () => {

    const callDetails = useOutboundCall();
    const startDevice = useStartDevice();


    const handleStartDate = (e) => {
        callDetails.setStartDate(e.target.value);
    }

    const handleEndDate = (e) => {
        callDetails.setEndDate(e.target.value);
    }


    useEffect(() => {
        callDetails.getRangeofUnansweredTickets(callDetails.startDate, callDetails.endDate, callDetails.agentId)
    }, [callDetails.endDate, callDetails.startDate])

    const handleRefresh = () => {
        callDetails.getRangeofUnansweredTickets(callDetails.startDate, callDetails.endDate, callDetails.agentId)
    }




    return (
        <>
            <div className="outbound-tab">
                <div className="set-dates">
                    <InputGroup size="sm" className="mt-1">
                        <InputGroup.Text id="inputGroup-sizing-sm" style={{ fontSize: '10px', color: "#3fa3ff", background: "#313334" }}>Start Date</InputGroup.Text>
                        <FormControl type="date" aria-label="Small" onChange={handleStartDate} defaultValue={callDetails.sevenDaysBeforeDate} aria-describedby="inputGroup-sizing-sm" />
                    </InputGroup>
                    <InputGroup size="sm">
                        <InputGroup.Text id="inputGroup-sizing-sm" style={{ fontSize: '10px', width: '69px', color: "#3fa3ff", background: "#313334" }}>End Date</InputGroup.Text>
                        <FormControl type="date" aria-label="Small" onChange={handleEndDate} defaultValue={callDetails.today} aria-describedby="inputGroup-sizing-sm" />
                    </InputGroup>
                </div>
                <div className="reload-btn">
                    <Button onClick={handleRefresh}>
                        <FontAwesomeIcon icon={faRedo} />
                    </Button>
                </div>
                {/* <Button variant="primary" style={{ width: '100%' }}
                // onClick={() => callDetails.getRangeofUnansweredTickets(startDate, endDate, callDetails.agentId)}
                >Submit</Button> */}
            </div>
            {callDetails.outboundCallLogs.length > 0 ?
                <ListGroup disabled variant="flush" style={{ overflowY: 'auto', background: 'var(--primary-background-2' }}>
                    {callDetails.outboundCallLogs.map((callLogs, index) => (
                        <div
                            key={index}
                            // style={{ background: 'var(--primary-background-2' }}
                            className={startDevice.disableInbound ? "disable-list" : "enable-list"}
                        >
                            <Call
                                callLogs={callLogs}
                            />
                        </div>
                    ))}
                </ListGroup>
                : <NoCallsToLoad />}
        </>
    )
}

export default OutboundCallList
