import React, { useEffect } from 'react'
import { Tab, Nav } from 'react-bootstrap'
import ChatList from '../../../../chatComponents/chatList/ChatList'
import { useChat } from '../../../../context/chat/chatContext'
import Loading1 from '../../../loading/Loading1'
import Agents from '../listGroup/Agents'
import '../../../../chatComponents/sideBar/sideBar.css'
import { useHistory } from 'react-router-dom'
import AgentsConvoList from '../listGroup/AgentsConvoList'
import NothingToLoad from '../../../../chatComponents/extras/NothingToLoad'
const AgentsList = () => {
    const chat = useChat();
    const history = useHistory();
    const corporate = sessionStorage.getItem('corporate');
    useEffect(() => {
        const to = `/${corporate}/managerDashboard/myList`;
        console.log(history, 'history');
        if (history.location.myList === undefined) {
            history.push(to)
        }
    }, [])

    useEffect(() => {
        chat.reloadAgentsList()
    }, [])

    return (
        <>
            <div className='agent-list-pane scroller'>
                {chat.agentsList.length > 0 ?
                    <Agents />
                    :
                    <NothingToLoad />}
            </div>
            <Tab.Container>
                <Nav className="justify-content-left nav-change" style={{ borderRight: '1px solid #dee2e6' }}>
                    <Nav.Item className="nav-change" style={{ borderBottom: '1px solid #3fa3ff' }}>
                        <div className="nav-text-change" style={{ color: '#3fa3ff' }} >Agent's Chat List</div>
                    </Nav.Item>
                </Nav>
                {chat.loadAgentsChats ?
                    <div className='agent-chat-list-pane scroller'>
                        {chat.agentConversationList.length > 0 ?
                            <AgentsConvoList />
                            :
                            <NothingToLoad />
                        }
                    </div> :
                    <Loading1 />

                }
            </Tab.Container>
        </>
    )
}

export default AgentsList
