import React, { useRef, useState } from 'react'
import '../../../../chatComponents/chatList/ChatList.css'
import { convertUTCDateToLocalDateV2, convertUTCDateToLocalTimeObject } from '../../../../utility/utility'
import { Dropdown, OverlayTrigger, Tooltip, ButtonGroup, DropdownButton } from 'react-bootstrap';
import { useChat } from '../../../../context/chat/chatContext';
import Avatar from 'react-avatar';
import { Modal, Button } from 'react-bootstrap';

const Agent = ({ user }) => {
    const [show, setShow] = useState(false);
    const chat = useChat();
    const newConvoId = chat.newConversationId;
    let statusIndicator;
    if (user.status) {
        statusIndicator = user.status;
    }


    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);


    const isNewConversations = (conversationArr, prevId) => {
        for (let i = 0; i < conversationArr.length; i++) {
            if (conversationArr[i] === prevId) {
                return true;
            }
        }
        return false;
    }


    const handleJustLogout = () => {
        if (chat.agentID !== undefined) {
            chat.changeAgentStatus(chat.agentID, false)
            handleClose()
        }
    }

    const handleLogoutAndResolve = () => {
        if (chat.agentID !== undefined) {
            chat.changeAgentStatus(chat.agentID, true)
            handleClose()
        }
    }

    function diffInSeconds(time1, time2) {
        return Math.floor((time1.getTime() - time2.getTime()) / 1000);
    }

    function computeState(clientTime, agentTime, source) {
        let nowTime = new Date();
        const REPLIED_TIME1 = 30;
        const REPLIED_TIME2 = 10;
        const COLOR_RED = 1;
        const COLOR_ORANGE = 2;
        const COLOR_YELLOW = 3
        const COLOR_GREEN = 4;


        if (source === "SYSTEM" || !source) {
            if (clientTime === "" || !clientTime) {
                return COLOR_YELLOW
            }
            if (diffInSeconds(nowTime, clientTime) >= REPLIED_TIME1) {
                console.log('when client, agentTime wrt now time > REPLY_TIME1');
                return COLOR_RED;
            }
            if (diffInSeconds(nowTime, clientTime) >= REPLIED_TIME2) {
                console.log('when client, agentTime wrt now time > REPLY_TIME2');
                return COLOR_ORANGE
            }
        }
        else {

            if (clientTime === null || agentTime === null) {
                if (clientTime !== null) {
                    if (diffInSeconds(nowTime, clientTime) >= REPLIED_TIME1) {
                        console.log('when client, agentTime wrt now time > REPLY_TIME1');
                        return COLOR_RED;
                    }
                    if (diffInSeconds(nowTime, clientTime) >= REPLIED_TIME2) {
                        console.log('when client, agentTime wrt now time > REPLY_TIME2');
                        return COLOR_ORANGE
                    }
                }
                return COLOR_YELLOW
            }
            // console.log(diffInSeconds(clientTime, agentTime), 'diff in min');
            // console.log(diffInSeconds(nowTime, clientTime), 'diff in now time min');
            // console.log('client time', clientTime);
            // console.log('agent time', agentTime);
            if (clientTime.getTime() <= agentTime.getTime()) {
                console.log('when client <= agentTime..');
                return COLOR_GREEN;
            }

            if (diffInSeconds(nowTime, clientTime) >= REPLIED_TIME1) {
                console.log('when client, agentTime wrt now time > REPLY_TIME1');
                return COLOR_RED;
            }
            if (diffInSeconds(nowTime, clientTime) >= REPLIED_TIME2) {
                console.log('when client, agentTime wrt now time > REPLY_TIME2');
                return COLOR_ORANGE
            }

            if (diffInSeconds(clientTime, agentTime) >= REPLIED_TIME2) {
                console.log('when client, agentTime wrt  > REPLY_TIME2');
                if (diffInSeconds(nowTime, clientTime) >= REPLIED_TIME2) {
                    console.log('when client, agentTime wrt now time > REPLY_TIME2');
                    return COLOR_ORANGE
                }
            }

            return COLOR_GREEN
        }

    }



    const lastSeenTime = (lastSeen, lastReplied, incommingSource) => {
        const agentTime = convertUTCDateToLocalTimeObject(lastSeen);
        const clientTime = convertUTCDateToLocalTimeObject(lastReplied);
        return computeState(clientTime, agentTime, incommingSource)

    }



    const getStatusIndicator = (statusIndicator) => {
        switch (statusIndicator) {
            case 'online':
                return <Tooltip id="button-tooltip-2" >Online</Tooltip>;
            case 'offline':
                return <Tooltip id="button-tooltip-2" >Offline</Tooltip>;
            case "busy":
                return <Tooltip id="button-tooltip-2" >Busy</Tooltip>;
            default:
                return ''
        }
    }
    return (
        <>

            <div
                className={`conversation activity-color${lastSeenTime(user.last_seen, user.receivedMessage, user.lastMessageSource)} ${user.agentID === chat.agentID && "selected"}`} onClick={() => chat.selectAgentChat(user.agentID)} >
                <Avatar color={Avatar.getRandomColor('sitebase', ['#3fa3ff', '#03a9f4'])} name={user.name} size="40" round={true} />

                <div className={isNewConversations(newConvoId, user.agentID) ? 'title-text-unread' : 'title-text'}>
                    <OverlayTrigger
                        placement="right"
                        overlay={getStatusIndicator(statusIndicator)}
                    >
                        {statusIndicator && <div className={`status-indicator ${statusIndicator}`}></div>}
                    </OverlayTrigger>
                    {user.name}
                </div>
                <div className={isNewConversations(newConvoId, user.agentID) ? 'new-created-date' : "created-date"}>
                    {statusIndicator === 'online' && <DropdownButton as={ButtonGroup} title="" id="bg-nested-dropdown">
                        <Dropdown.Item onClick={handleShow}>Logout Agent</Dropdown.Item>
                    </DropdownButton>}
                </div>

                <div className="agent-last-seen">
                    {/* {user.last_seen} */}
                    last seen: {convertUTCDateToLocalDateV2(user.last_seen)}
                </div>

                <Modal
                    show={show}
                    onHide={handleClose}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Logout Agent</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        How do you wish to logout the agent.
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleJustLogout} >
                            Just Logout
                        </Button>
                        <Button variant="primary" onClick={handleLogoutAndResolve} >
                            Resolve and Logout
                        </Button>
                    </Modal.Footer>
                </Modal>



            </div >
        </>
    )
}

export default Agent
