import React from 'react'
import AgentProp from '../../chatComponents/agentDetails/AgentProp'
import ChatBox from '../../chatComponents/chatBox/ChatBox'
import SideBar from '../../chatComponents/sideBar/Sidebar';
import chatImg from '../../../assets/images/chat.png';
import nofile from '../../../assets/images/file.png'
import SuperVisorSidebar from './sideBar/SuperVisorSidebar';
import { useChat } from '../../context/chat/chatContext';
import { ToastContainer } from 'react-toastify';
import { BrowserRouter as Router, Link, Route, Switch, useHistory } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import AgentsChatBox from './agentsChatBox/AgentsChatBox';
import BrowserNotify from '../../browseNotification/BrowserNotify';
import Notification from "react-web-notification";
import DefaultPge from '../../chatComponents/extras/DefaultPge';
import NoPreview from '../../chatComponents/extras/NoPreview';
const SuperVisorDashboard = () => {
    const chat = useChat();
    const history = useHistory();
    const isDesktopOrLaptop = useMediaQuery({
        minWidth: "800px"
    });

    return (
        <>
            <div id="grid">
                <Router>
                    <SuperVisorSidebar />
                    {chat.showChat || isDesktopOrLaptop === false ?
                        <ChatBox />
                        :
                        <DefaultPge />
                    }
                    {chat.showChat ?
                        <AgentProp />
                        :
                        <NoPreview />
                    }
                    <BrowserNotify />
                    <ToastContainer autoClose={2000} position="top-center" />
                </Router>
            </div>
        </>
    )
}

export default SuperVisorDashboard
