import React from 'react'
import chatImg from '../../../assets/images/chat.png'

const DefaultPge = () => {
    return (
        <div className="before-chat-box" style={{ background: 'var(--chat-color-1)' }}>
            <img src={chatImg} alt="no preview" className="before-chat-boxImg" />
            <h5 className='disp-text'>Please select a conversation from left pane</h5>
        </div>
    )
}

export default DefaultPge