import React, { useState, useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { useTime } from '../../../context/callTime/calltimeContext';
import { useStartDevice } from '../../../context/startDevice/startDeviceContext';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAddressBook, faComment, faUser, faPhone } from '@fortawesome/free-solid-svg-icons';
import CallDuration from '../CallDuration';
import '../TwilioCall.css';
import Time from '../../clientVideo/Time';
import KeyPad from '../keyPad/KeyPad';
// import noCalls from '../../../../assets/images/no-calls.png'


const Phone = ({ currentStatus, selectedNumber, handleAcceptCall, }) => {
    const [totalTime, setTotalTime] = useState("");
    const toggleTime = useTime();
    const callUpdates = useStartDevice();
    const handleDialPad = () => {
        toggleTime.openDialPad()
    }
    const homeScreen = () => {
        toggleTime.goToHome();
    }

    // useEffect(() => {
    //     callUpdates.getPreviousCallHistory()
    // }, [])

    // console.log(toggleTime.currentStatus, 'currentPage');
    // console.log(toggleTime.isInbound, 'isInbound');
    return (
        <Container>
            {/* {callUpdates.startDevice === false && toggleTime.isInbound ?
                <div style={{ marginTop: '100px' }}>
                    <div className="no-calls">
                        <h4>No Incomming calls</h4>
                    </div>
                    <div className="no-calls" style={{ marginTop: '50px' }}>
                        <img src={noCalls} width="200" height="200" alt="no calls" />
                    </div>
                </div>
                : */}
            <Row className="justify-content-md-center">
                <Col className="start-btn-bgimg">
                    <div className="phone-wrapper">
                        <div className="iphone">
                            <div className="power"></div>
                            <div className="lock"></div>
                            <div className="volume up"></div>
                            <div className="volume down"></div>
                            <div className="camera"></div>
                            <div className="speaker"></div>
                            <div className="screen">
                                {/* {toggleTime.currentStatus === "incommingCall" &&

                                        <div className="rectangle">
                                            <h5 className="incomming-call-txt">{callUpdates.incommingNumber} calling</h5>
                                            <CallDuration />
                                            <div className="user-circle">
                                                <FontAwesomeIcon className="user-icon" icon={faUser} />
                                            </div>
                                            <Row className="margin-call">
                                               

                                                <Col className={toggleTime.isActive ? "div-end-callnone" : "div-end-call"}>
                                                    <div className="recieve-call" id="button-answer" onClick={acceptCall} >
                                                        <FontAwesomeIcon className="recieve-call-icon" icon={faPhone} />
                                                    </div>
                                                </Col>

                                            </Row>
                                        </div>




                                    } */}


                                {toggleTime.currentStatus === "inncommingAccept"
                                    &&
                                    <div className="rectangle">
                                        <h3 className="incomming-call-txt">{callUpdates.incommingNumber}</h3>
                                        <CallDuration extraStyle={{ fontSize: "small", marginTop: "-9px" }} isRunning={true} />
                                        <div className="user-circle">
                                            <FontAwesomeIcon className="user-icon" icon={faUser} />
                                        </div>
                                        <Row className="margin-end-call">
                                            <Col className="div-end-call">
                                                <div className="end-call" onClick={callUpdates.handleHangup} >
                                                    <FontAwesomeIcon className="end-call-icon" icon={faPhone} />
                                                </div>
                                            </Col>

                                            {/* <Col className={toggleTime.isActive ? "div-end-callnone" : "div-end-call"}>
                                                <div className="recieve-call">
                                                    <FontAwesomeIcon className="recieve-call-icon" icon={faPhone} />
                                                </div>
                                            </Col> */}
                                        </Row>
                                    </div>}
                                {currentStatus === "callEnded" && <div className="rectangle">
                                    <h5 className="incomming-call-txt">Call Ended</h5>
                                    <div className="user-circle">
                                        <FontAwesomeIcon className="user-icon" icon={faUser} />
                                    </div>
                                    <h6 className="incomming-call-txt">Call Duration</h6>
                                    {console.log("total time", sessionStorage.getItem("totalTime"))}
                                    <p className="incomming-call-time">{toggleTime.totalTime}</p>
                                </div>}
                                {/* ////////// home screen */}
                                {/* {currentStatus === "homeBtn" && <div className="home-screen">
                                        <Time />
                                        <Row className="make-a-call">
                                            <Col className="menu-bar" style={{ marginLeft: '10px' }}>
                                                <div className="call-app" onClick={handleDialPad} >
                                                    <FontAwesomeIcon className="recieve-call-icon" icon={faPhone} />
                                                </div>
                                            </Col>
                                            <Col className="menu-bar">
                                                <div className="contacts">
                                                    <FontAwesomeIcon className="recieve-call-icon" icon={faAddressBook} />
                                                </div>
                                            </Col>

                                            <Col className="menu-bar">
                                                <div className="call-app">
                                                    <FontAwesomeIcon className="recieve-call-icon" icon={faComment} />
                                                </div>
                                            </Col>

                                        </Row>
                                    </div>}
                                    {currentStatus === "dialPad" && <div className="home-screen">
                                        <KeyPad number={number} setNumber={setNumber} handleCall={callUpdates.handleCall} />
                                    </div>} */}



                                {/* outgoing calls pages */}


                                {toggleTime.currentStatus === "outgoingCall" &&
                                    <div className="rectangle">
                                        <h5 className="incomming-call-txt">Call {selectedNumber}</h5>
                                        {/* <CallDuration /> */}
                                        {/* <img className="caller-img" src={callId} alt="Iphone" /> */}
                                        <div className="user-circle">
                                            <FontAwesomeIcon className="user-icon" icon={faUser} />
                                        </div>
                                        <Row className="margin-call">
                                            {/* <Col className="div-end-call">
                                                    <div className="end-call"  >
                                                        <FontAwesomeIcon className="end-call-icon" icon={faPhone} />
                                                    </div>
                                                </Col> */}
                                            <Col className={toggleTime.currentTime.running ? "div-end-callnone" : "div-end-call"}>
                                                <div className="recieve-call" onClick={handleAcceptCall} >
                                                    <FontAwesomeIcon className="recieve-call-icon" icon={faPhone} />
                                                </div>
                                            </Col>
                                        </Row>
                                    </div>
                                }

                                {toggleTime.currentStatus === "connecting"
                                    &&
                                    <div className="rectangle">
                                        <h5 className="incomming-call-txt">Calling {selectedNumber}</h5>
                                        {/* <CallDuration /> */}
                                        {/* <img className="caller-img" src={callId} alt="Iphone" /> */}
                                        <div className="user-circle">
                                            <FontAwesomeIcon className="user-icon" icon={faUser} />
                                        </div>
                                        <Row className="margin-end-call">
                                            <Col className="div-end-call">
                                                <div className="end-call" onClick={callUpdates.handleHangup} >
                                                    <FontAwesomeIcon className="end-call-icon" icon={faPhone} />
                                                </div>
                                            </Col>

                                            {/* <Col className={toggleTime.isActive ? "div-end-callnone" : "div-end-call"}>
                                                <div className="recieve-call">
                                                    <FontAwesomeIcon className="recieve-call-icon" icon={faPhone} />
                                                </div>
                                            </Col> */}
                                        </Row>
                                    </div>}

                                {toggleTime.currentStatus === "inCall"
                                    &&
                                    <div className="rectangle">
                                        <h3 className="incomming-call-txt">In call</h3>
                                        <CallDuration isRunning={true} />
                                        {/* <img className="caller-img" src={callId} alt="Iphone" /> */}
                                        <div className="user-circle">
                                            <FontAwesomeIcon className="user-icon" icon={faUser} />
                                        </div>
                                        <Row className="margin-end-call">
                                            <Col className="div-end-call">
                                                <div className="end-call" onClick={callUpdates.handleHangup} >
                                                    <FontAwesomeIcon className="end-call-icon" icon={faPhone} />
                                                </div>
                                            </Col>

                                            {/* <Col className={toggleTime.isActive ? "div-end-callnone" : "div-end-call"}>
                                                <div className="recieve-call">
                                                    <FontAwesomeIcon className="recieve-call-icon" icon={faPhone} />
                                                </div>
                                            </Col> */}
                                        </Row>
                                    </div>}

                                {toggleTime.currentStatus === "callEnded" && <div className="rectangle">
                                    <h5 className="incomming-call-txt">Call Ended</h5>
                                    <div className="user-circle">
                                        <FontAwesomeIcon className="user-icon" icon={faUser} />
                                    </div>
                                    <h6 className="incomming-call-txt">Call Duration</h6>
                                    <p className="incomming-call-time">{`${sessionStorage.getItem("hours")}: ${sessionStorage.getItem("minutes")}: ${sessionStorage.getItem("seconds")}`}</p>
                                </div>}
                            </div>
                            <div className="button"
                            // onClick={homeScreen}
                            >
                                <div className="square"></div>
                            </div>
                        </div>
                    </div>
                </Col>
            </Row>
            {/* } */}
        </Container>
    )
}

export default Phone
