import React, { useContext, useState } from 'react'
import { callBackStatus, getAgentUnavailableTickets, handleRefreshList, resolveOutboundCall, updateCommonInbox } from '../../pages/twilio-call/service/api';
import { useTime } from '../callTime/calltimeContext';

const outboundCallContext = React.createContext()

export function useOutboundCall() {
    return useContext(outboundCallContext)
}

export function OutboundCallProvider({ children }) {
    const [outboundCallLogs, setOutboundCallLogs] = useState([]);
    const [selectedNumberId, setSelectedNumberId] = useState();
    const [callStatus, setCallStatus] = useState();
    const [showMod, setShowMod] = useState(false);
    const [openOutboundResolve, setOpenOutboundResolve] = useState(false);
    let today = new Date().toISOString().slice(0, 10)
    const sevenDaysBeforeDate = new Date(new Date().setDate(new Date().getDate() - 7)).toISOString().slice(0, 10);
    const [startDate, setStartDate] = useState(sevenDaysBeforeDate);
    const [endDate, setEndDate] = useState(today);
    const setPage = useTime();
    // const [reqId, setReqId] = useState()
    let agentId;
    if (sessionStorage.getItem('workerSid')) {
        agentId = `AGENT_${sessionStorage.getItem('workerSid')}`;
    }
    // const name = dummyLogs.find((name) => name.phoneNumber === selectedNumberId && name.name);
    const requireId = outboundCallLogs.find((reqId) => reqId._id === selectedNumberId && reqId.reqId);
    const number = outboundCallLogs.find((number) => number._id === selectedNumberId && number.callCenter.phoneNumber);
    let selectedNumber
    let reqId
    if (requireId) {
        reqId = requireId.reqId
    }

    // let dummyName;
    // if (name) {
    //     dummyName = name.name;
    //     console.log(dummyName, "dummyname");
    // }
    // console.log(name, 'dummy name');
    if (number) {
        selectedNumber = number.callCenter.phoneNumber
    }
    // if (selectedNumberId) {
    //     selectedNumber = selectedNumberId;
    // }

    const handleCloseMod = () => setShowMod(false);
    const handleShowMod = () => {
        setShowMod(true);
    }

    const getRangeofUnansweredTickets = async (startDate, endDate, agentId) => {
        console.log(startDate, 'starting date');
        console.log(endDate, 'ending Date');
        console.log(agentId, 'agent id');
        if (startDate !== undefined && endDate !== undefined) {
            const getUnavailableTickets = await getAgentUnavailableTickets(sessionStorage.getItem('corporate'), agentId, startDate, endDate, sessionStorage.getItem('token'))
            console.log(getUnavailableTickets.data, 'unanswered tickets');
            setOutboundCallLogs(getUnavailableTickets.data.data);
        }
    }

    const assignAgentInCommoninbox = async (agentID) => {
        console.log(agentID, 'request body');
        if (requireId !== undefined && agentId !== undefined) {
            const agentAssigned = await updateCommonInbox(sessionStorage.getItem('token'), sessionStorage.getItem('corporate'), reqId, agentID);
            console.log(agentAssigned, 'assigned agent');
            return agentAssigned;
        }
    }

    const resolveOutbound = async () => {
        const resolved = await resolveOutboundCall(sessionStorage.getItem('token'), sessionStorage.getItem('corporate'), reqId, agentId);
        console.log(resolved, 'resolved');
    }

    const callLogs = async (callSid) => {
        const logs = await callBackStatus(sessionStorage.getItem('token'), callSid, sessionStorage.getItem('workerSid'));
        console.log(logs, 'callLogs');
    }

    // const dummyCallList = async () => {
    //     const dummy = await callList();
    //     setDummyLogs(dummy.data.ticketList)
    //     console.log(dummy, 'dummy tickets');
    //     return dummy;
    // }

    const refreshList = async () => {
        console.log(sessionStorage.getItem('workerSid'), 'workerSid in context');
        const res = await handleRefreshList(sessionStorage.getItem('token'), sessionStorage.getItem('accountname'), sessionStorage.getItem('workerSid'));
        console.log(res);
        return res
    }

    // const assignDummy = async (name, status) => {
    //     const assign = await assignDummyTicket(name, status);
    //     console.log(assign, 'dummy assign');
    //     return assign
    // }

    const selectedCallId = (nmber) => {
        setSelectedNumberId(nmber)
        openOutboundCall()
    }
    const openOutboundCall = () => {
        setPage.setIsInbound(false)
        setPage.setCurrentStatus('outgoingCall');
    }


    const value = {
        agentId,
        outboundCallLogs,
        selectedNumberId,
        selectedNumber,
        sevenDaysBeforeDate,
        callStatus,
        openOutboundResolve,
        showMod,
        startDate,
        endDate,
        // dummyList,
        // dummyName,
        today,
        reqId,
        callLogs,
        refreshList,
        setStartDate,
        setEndDate,
        // setDummyList,
        // assignDummy,
        // dummyCallList,
        handleShowMod,
        handleCloseMod,
        setOpenOutboundResolve,
        setCallStatus,
        resolveOutbound,
        openOutboundCall,
        setSelectedNumberId,
        getRangeofUnansweredTickets,
        assignAgentInCommoninbox,
        selectedCallId
    }
    return (
        <outboundCallContext.Provider value={value}>
            {children}
        </outboundCallContext.Provider>
    )
}