import React, { useEffect, useState } from 'react';
import { Navbar, Nav, Button } from 'react-bootstrap';
import { useMediaQuery } from 'react-responsive';
import PropToggleButton from '../../../chatComponents/agentDetails/PropToggleButton';
import '../../../chatComponents/chatBox/chatBox.css'
import DrawerToggleButton from '../../../chatComponents/sideBar/DrawerToggleButton';
import { useTime } from '../../../context/callTime/calltimeContext';
import { useOutboundCall } from '../../../context/outboundCall/outboundCallContext';
import { useStartDevice } from '../../../context/startDevice/startDeviceContext';
import { taskComplete } from '../service/api';

const ResolveNavbar = ({ isWrapping, propDrawerToggle, drawerClickToggleHandler, setIsWrapping }) => {
    const resolveTask = useStartDevice();
    const resetPhone = useTime();
    const isDesktopOrLaptop = useMediaQuery({
        minWidth: "800px"
    });
    const resolveCall = useOutboundCall();
    let today = new Date().toISOString().slice(0, 10)
    const sevenDaysBeforeDate = new Date(new Date().setDate(new Date().getDate() - 7)).toISOString().slice(0, 10);
    const [startDate, setStartDate] = useState(sevenDaysBeforeDate);
    const [endDate, setEndDate] = useState(today);



    const completeTask = async () => {
        // console.log('inside complete task', taskSid);
        if (resolveCall.openOutboundResolve) {
            const resolve = await resolveCall.resolveOutbound();
            console.log(resolve);
            resolveCall.getRangeofUnansweredTickets(startDate, endDate, resolveCall.agentId)
            resolveCall.setOpenOutboundResolve(false)
            resolveTask.setStartDevice(false);
            resolveCall.setSelectedNumberId(undefined);
            resolveTask.setDisableInbound(false);
        } else {
            const tskComp = await taskComplete(resolveTask.taskSid, sessionStorage.getItem('accountname'), sessionStorage.getItem('token'));
            if (tskComp.data.success) {
                resolveTask.setStartDevice(false);
                resolveTask.setDisableOutbound(false);
                resolveTask.setTicketID('');
                resolveTask.setCategory('');
                resolveTask.setSubCategory('');
                resetPhone.setCurrentStatus('');
                resolveTask.setIncommingNumber(undefined)
                resolveTask.setIsWrapping(false)
            }
            console.log("complete task initiated", tskComp);
        }
    }

    console.log("reset", resetPhone.isInbound, resolveCall.openOutboundResolve)

    return (
        <div>
            <Navbar variant="tabs" className="nav-bar">
                {isDesktopOrLaptop ? '' : <DrawerToggleButton drawerClickToggleHandler={drawerClickToggleHandler} />}
                {/* {outboundCallLogs.map((name, index) => name.additionalInformation.sessionId === selectedNumber && <Navbar.Brand key={index} className="sender-name">{name.userId}</Navbar.Brand>)} */}
                <div style={{ display: 'flex', flex: 1 }} />

                {resetPhone.isInbound ?
                    <div style={{ marginRight: '0.5rem' }}>
                        {resolveTask.isWrapping || resolveTask.openResolve ? <Button variant="success" onClick={completeTask}  >Resolve</Button> : <Button variant="success" disabled  >Resolve</Button>}
                    </div>
                    :
                    <div style={{ marginRight: '0.5rem' }}>
                        {resolveCall.openOutboundResolve ? <Button variant="success" onClick={completeTask}  >Resolve</Button> : <Button variant="success" disabled  >Resolve</Button>}
                    </div>
                }
                {isDesktopOrLaptop ? '' : <PropToggleButton propDrawerToggle={propDrawerToggle} />}
            </Navbar>
        </div>
    )
}

export default ResolveNavbar
