import React, { useState } from 'react'
import AnalyticRoutes from './AnalyticRoutes'
import AnalyticsSideBar from './sideBar/AnalyticsSideBar'
import './Analytics.css'


const Analytics = () => {
    const [menuCollapse, setMenuCollapse] = useState(false);
    return (
        <div id={`${!menuCollapse ? "analytics-layout" : "collapse-layout"}`} >
            <div>
                <AnalyticsSideBar menuCollapse={menuCollapse} setMenuCollapse={setMenuCollapse} />
            </div>
            <div style={{ background: 'var(--chat-color-1)' }}>
                <AnalyticRoutes />
            </div>
        </div>
    )
}

export default Analytics