export const register = async (workerSid) => {
    console.log(workerSid, 'axios');
    try {
        return await axios.get(`${api}/workerToken?workerSid=${workerSid}`)
    }
    catch (err) {
        console.log(err);
    }
}
export const clientRegister = async (clientId) => {
    try {
        // console.log(clientId);
        // const urlEncodedVal = encodeURI(clientId)
        // console.log(urlEncodedVal)
        return await axios.get(`${api}/clientToken?workername=${clientId}`)
    }
    catch (error) {
        console.log(error);
        // throw new Error();
    }
}

export const taskComplete = async (taskSid, accountName, token) => {
    try {
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept'
        }
        return await axios.post(`${api}/completeTask`, {
            accountname: accountName,
            taskSid: taskSid
        },
            {
                headers: headers
            }
        )
    }
    catch (error) {
        console.log(error);
    }
}

export const getAgentUnavailableTickets = async (corporate, agentId, startDate, endDate, token) => {
    try {
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept'
        }
        return await axios.post(`${api}/proxy/businessInbox/commonInbox/getAllTicketsBasedOnAgentId`, {
            corporate: corporate,
            agentId: agentId,
            startDate: startDate,
            endDate: endDate
        },
            {
                headers: headers
            }
        )
    }
    catch (error) {
        console.log(error);
    }
}

