import React, { useEffect, useState, useMemo } from 'react'
import { useTime } from '../../context/callTime/calltimeContext';
import { useStartDevice } from '../../context/startDevice/startDeviceContext';
import Phone from './phone/Phone'
import { Button } from 'react-bootstrap';
import Modal from 'react-bootstrap/Modal';
import NoCalls from '../../chatComponents/extras/NoCalls';
import BotCallHistory from './botCall/BotCallHistory';
import BotChatBox from './botCall/BotChatBox';
import SwitchPhone from './botCall/SwitchPhone';
// import { hangUp } from './service/api';


const InboundCall = () => {
    const getIncomming = useStartDevice();
    const time = useTime()
    const [openBotConvo, setOpenBotConvo] = useState(false);
    const onClose = () => setOpenBotConvo(false)
    const onOpen = () => setOpenBotConvo(true)
    const [duration, setDuration] = useState({
        hours: 0,
        minutes: 0,
        seconds: 0,
    });
    // const {
    //     seconds,
    //     minutes,
    //     hours,
    //     days,
    //     isRunning,
    //     start,
    //     pause,
    //     reset,
    // } = useStopwatch({ autoStart: false });

    // const duration = {
    //     hours,
    //     minutes,
    //     seconds
    // }

    // useEffect(() => {
    //     setDuration((pre) => {
    //         return {
    //             ...pre,
    //             hours,
    //             minutes,
    //             seconds
    //         }
    //     })
    // }, [isRunning, seconds]);



    // console.log("time", time.duration, time.seconds)

    return (
        <div>

            {getIncomming.startDevice ?
                (
                    <div style={{ position: 'relative' }} >
                        {/* {time.openBotConvo ?
                            <BotChatBox />
                            :
                            } */}
                        <Phone />
                        <BotCallHistory onOpen={onOpen} />
                        {/* {!time.openBotConvo ?
                            :
                            <SwitchPhone />
                        } */}

                    </div>
                )
                :
                (<>
                    <NoCalls toDisp="No Incoming Calls" src="/assets/images/no-calls.png" />
                </>)
            }
            <Modal
                show={openBotConvo}
                onHide={onClose}
                backdrop="static"
                keyboard={false}
                dialogClassName="modal-50w"
                size="md"
                aria-labelledby="example-custom-modal-styling-title"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-custom-modal-styling-title" >BOT Call History</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <BotChatBox />
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={onClose} >
                        Close
                    </Button>

                </Modal.Footer>
            </Modal>
        </div>
    )
}

export default InboundCall
