import React, { useState } from "react";

import {
    ProSidebar,
    Menu,
    SubMenu,
    MenuItem,
    SidebarHeader,
    SidebarFooter,
    SidebarContent
} from "react-pro-sidebar";

import { FaList, FaRegHeart } from "react-icons/fa";
import {
    FiHome,
    FiLogOut,
    FiArrowLeftCircle,
    FiArrowRightCircle
} from "react-icons/fi";
import { RiPencilLine } from "react-icons/ri";
import { BiCog } from "react-icons/bi";

import "react-pro-sidebar/dist/css/styles.css";
import "./AnalyticsSideBar.css";
import { useChat } from "../../../context/chat/chatContext";
import { Image } from "react-bootstrap";
import { Link } from "react-router-dom";
import MenuItemList from "./MenuItemList";

const AnalyticsSideBar = ({ menuCollapse, setMenuCollapse }) => {
    const chat = useChat()
    const [selectedId, setSelectedId] = useState(0);
    // const [menuCollapse, setMenuCollapse] = useState(false);

    const menuIconClick = () => {
        menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
    };



    const MENU_LIST = [
        {
            contentName: "Agent Data",
            active: selectedId,
            icon: <FiHome />,
            link: "/Analytics/Agent"
        },
        {
            contentName: "Client Closed",
            active: selectedId,
            icon: <FaList />,
            link: "/Analytics/Agent"
        },
        {
            contentName: "Client Open Data",
            active: selectedId,
            icon: <FaRegHeart />,
            link: "/Analytics/Agent"
        },
        {
            contentName: "Client Data",
            active: selectedId,
            icon: <RiPencilLine />,
            link: "/Analytics/Agent"
        },
        {
            contentName: "Client Data",
            active: selectedId,
            icon: <BiCog />,
            link: "/Analytics/Agent"

        }

    ]

    const handleClick = (id) => {
        setSelectedId(id)


    }


    return (
        <>
            <div id="header">
                <ProSidebar collapsed={menuCollapse}>
                    <SidebarHeader>
                        <div className="logotext">
                            <p>{menuCollapse ?
                                <Image
                                    src={chat.currentAppLogo}
                                    alt="logo"
                                    roundedCircle
                                />
                                : "Agent Analytics"}</p>
                        </div>
                        <div className="closemenu" onClick={menuIconClick}>
                            {menuCollapse ? <FiArrowRightCircle /> : <FiArrowLeftCircle />}
                        </div>
                    </SidebarHeader>
                    <SidebarContent>
                        <Menu iconShape="round">
                            {MENU_LIST.map((content, index) => (
                                <MenuItemList index={index} content={content} handleClick={handleClick} />
                            ))}
                            {/* <MenuItem active={true} onClick={handleClick} icon={<FiHome />}>
                                Agents Data
                                <Link to={"/Analytics/Agent"} />
                            </MenuItem>
                            <MenuItem icon={<FaList />}>
                                Category
                            </MenuItem>
                            <MenuItem icon={<FaRegHeart />}>
                                Favourite
                            </MenuItem>
                            <MenuItem icon={<RiPencilLine />}>
                                Edit
                            </MenuItem>
                            <MenuItem icon={<BiCog />}>
                                Settings
                            </MenuItem> */}
                        </Menu>
                    </SidebarContent>
                    <SidebarFooter style={{ background: 'var(--primary-background)' }}>
                        <Menu iconShape="round">
                            <MenuItem icon={<FiLogOut />}>Go Back</MenuItem>
                        </Menu>
                    </SidebarFooter>
                </ProSidebar>
            </div>
        </>
    );
};

export default AnalyticsSideBar;
