function pad(num) {
   if (num < 10) {
      return "0" + num
   }
   return num;
}

function convertUTCDateToLocalDateV2(dateStr, timeOnly = false) {
   // console.log(dateStr, 'dateString')
   let dayOfWeek = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"]
   let comp = dateStr.split("T");
   if (comp.length != 2) {
      return "";
   }
   let dateComp = comp[0].split("/");
   if (dateComp.length != 3) {
      return "";
   }
   let timeComp = comp[1].split(":")
   if (timeComp.length < 2) {
      return "";
   }
   let dt = new Date(Date.UTC(Number(dateComp[0]), Number(dateComp[1]) - 1, Number(dateComp[2]), Number(timeComp[0]), Number(timeComp[1]), 0));
   let dtNow = new Date();
   let diff = (dtNow.getTime() - dt.getTime()) / (24 * 60 * 60 * 1000)
   if ((diff < 1) || timeOnly) {
      return pad((dt.getHours() > 12) ? (dt.getHours() - 12) : dt.getHours()) + ":" + pad(dt.getMinutes()) + " " + ((dt.getHours() > 12) ? "PM" : "AM")
   }
   else if (diff < 2) {
      return "Yesterday"
   }
   else if (diff < 7) {
      return dayOfWeek[dt.getDay()];
   }
   else {
      return dt.getFullYear() + "/" + pad(dt.getMonth() + 1) + "/" + pad(dt.getDate())
   }
   return "";
}

function convertUTCDateToLocalTimeObject(dateStr, timeOnly = false) {
   let dayOfWeek = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"]
   let comp = dateStr.split("T");

   if (comp.length != 2) {
      console.log(' comp lengt !=2');
      return null;
   }
   let dateComp = comp[0].split("/");
   if (dateComp.length != 3) {
      console.log(' comp lengt !=3');
      return null;
   }
   let timeComp = comp[1].split(":")
   if (timeComp.length < 2) {
      return null;
   }
   let dt = new Date(Date.UTC(Number(dateComp[0]), Number(dateComp[1]) - 1, Number(dateComp[2]), Number(timeComp[0]), Number(timeComp[1]), Number(timeComp[2] ? timeComp[2] : 0), 0));
   return dt
}

const formatDateTimeTicket = (dt, from) => {

   let month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
      "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
   let timezone;
   timezone = new Date().getTimezoneOffset();
   if (dt) {


      let dtTm = dt.split(" ");
      let dtComp = dtTm[0].split("/");
      let tmComp = dtTm[1].split(":");
      let dateTimeStamp = new Date(dtComp[0], Number(dtComp[1]) - 1, dtComp[2], tmComp[0], tmComp[1]).getTime() - (timezone * 60000);
      dateTimeStamp = new Date(dateTimeStamp);
      let ampm = "";
      let hour = dateTimeStamp.getHours();
      if (Number(hour) < 12) {
         ampm = "AM";
      } else {
         ampm = "PM";
         if (Number(hour) > 12) {
            hour = Number(hour) - 12;
         }
      }
      hour = pad(hour);

      if (from === "true") {

         return dateTimeStamp.getDate() + " " + month[Number(dateTimeStamp.getMonth())] + " " + dateTimeStamp.getFullYear() + " " + hour + ":" + pad(dateTimeStamp.getMinutes()) + " " + ampm;
      } else if (from === "false") {

         return hour + ":" + pad(dateTimeStamp.getMinutes()) + " " + ampm;
      } else if (from === "business") {

         return dateTimeStamp;
      }
   }
}

