const differenceday = (date) => {

   const todayDate = new Date()
   const diff = todayDate.getTime() - date.getTime()
   const diffInDays = diff / (1000 * 60 * 60 * 24);

   let ampm = "";
   let hour = date.getHours();
   if (Number(hour) < 12) {
      ampm = "AM";
   } else {
      ampm = "PM";
      if (Number(hour) > 12) {
         hour = Number(hour) - 12;
      }
   }
   if (diffInDays <= 1) {
      let hrs = date.getHours()
      if (Number(hrs) > 12) {
         hrs = Math.abs(hrs - 12);
      }
      return pad(hrs) + ":" + pad(date.getMinutes()) + ampm
   } else if (diffInDays <= 2) {
      return "Yesterday"
   } else {
      return date.getFullYear() + "/" + pad(date.getMonth() + 1) + "/" + pad(date.getDate())
   }
   //return diffInDays
}

export {
   convertUTCDateToLocalDateV2,
   convertUTCDateToLocalTimeObject,
   formatDateTimeTicket,
   differenceday
}