import axios from 'axios';
import React, { useState, useEffect } from 'react'
// import { useParams } from 'react-router-dom';
import { config } from '../../../config/config'
import { useChat } from '../../context/chat/chatContext';
import { useLocation } from 'react-router-dom';
import decode from 'jwt-decode'




const LogOut = () => {
    // const { corporate } = useParams
    const [corporate, setCorporate] = useState('');
    const location = useLocation();
    const chat = useChat()

    useEffect(() => {
        setCorporate(sessionStorage.getItem('corporate'))
    }, [])
    useEffect(() => {
        if (sessionStorage.getItem('token')) {
            const decodeToken = decode(sessionStorage.getItem('token'))
            if (decodeToken.exp * 1000 < new Date().getTime()) logout()
        }
    }, [location])

    const logout = async () => {
        if (chat.chatList.length > 0) {
            return alert("There are still unresolved conversation")
        }
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${sessionStorage.getItem('token')}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept'
        }
        await axios.post(`${config.baseUrl}/signout`,
            {},
            { headers: headers }
        ).then(res => {
            sessionStorage.clear();
            window.location.href = `/${corporate}`;
            console.log(res);
        }).catch(err => {
            console.log(err);
        })
    }

    return (
        <div onClick={logout}>
            logout
        </div>
    )
}

export default LogOut
