import React, { useState } from 'react';
import { Modal, Button, Form, InputGroup } from 'react-bootstrap';
import axios from 'axios';
import { config } from '../../../config/config';


const ChangeName = ({ changeNameModal, onClose, setNameChanged }) => {
    const [newName, setNewName] = useState();
    const [notMatching, setNotMatching] = useState(false);
    const handleNewName = (e) => {
        setNewName(e.target.value);
    }
    const handleClose = () => {
        onClose();

    }
    const handleSubmit = () => {
        console.log(newName);
        if (newName === undefined || "") {
            setNotMatching(true);
        } else {
            const headers = {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${sessionStorage.getItem('token')}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept'
            }
            axios.post(`${config.baseUrl}/editName`,
                {
                    name: newName
                },
                {
                    headers: headers
                }
            ).then((res) => {
                console.log(res);
                if (res.data.success === true) {
                    sessionStorage.setItem('newAgentName', newName);
                    setNameChanged(true);
                    onClose();
                    // setNotMatching(false);
                }
            }).catch((err) => {
                console.log(err);
            })
        }
    }
    return (
        <div>
            <Modal show={changeNameModal}
                onHide={onClose}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>CHANGE NAME</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Enter the new name </Form.Label>
                        <InputGroup>
                            <Form.Control onChange={handleNewName} type="text" placeholder="New Name" />
                        </InputGroup>
                        {notMatching && <Form.Text style={{ color: 'red', fontSize: 'small' }}>
                            Please Enter the name
                        </Form.Text>}
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={handleSubmit}>
                        Save Changes
                    </Button>
                </Modal.Footer>
            </Modal>
            {/* change name */}
        </div>
    )
}

export default ChangeName
