const DateComp = ({ messageData }) => {
    return (
        <div style={{ display: 'flex', justifyContent: "flex-end" }} >
            <p style={{ fontFamily: 'cursive', color: 'gray', fontSize: 'small' }}>{formatDateTimeTicket(messageData?.date, "false")}</p>
        </div>)
}
const GoogleMessageDataPacket = ({ messageData }) => {
    //let isopenwebview = false
    let [isopenwebview, setIsopenwebview] = React.useState(false);

    const ClickButtonComp = (buttons, DataPacket) => {
        return (
            <div
                style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}
            >
                {buttons.map((button, index) => {
                    return (
                        <Button
                            key={index}
                            title={button.title}
                            style={{ backgroundColor: "#3fa3ff", size: "small", color: "white", width: "100%" }}
                            variant="solid"
                            onClick={() => {
                                setIsopenwebview(true)
                            }}
                        >
                            {button.action.text}
                        </Button>
                    );
                })}
            </div>
        );
    }
    const ClickButtonTemp = ({ data, DataPacket }) => {

        return (
            <div style={{ borderRadius: "small", borderWidth: "small", borderColor: "black", backgroundColor: "white" }}  >
                <div style={{ width: "100%", borderRadius: "small" }} >
                    {
                        <TextComp data={data.text ? data.text : data.richCard ? data.richCard.standaloneCard.cardContent.description : ""} />
                    }
                </div>
                {
                    (data.suggestions &&
                        ClickButtonComp(data.suggestions, DataPacket)) || (data.richCard && ClickButtonComp(data.richCard.standaloneCard.cardContent.suggestions, DataPacket))
                }

            </div>
        )
    }

    const openwebview = (DataPacket) => {

        return (
            <div>
                <div>
                    <div width="100%" justifyContent="flex-end">
                        <Button
                            isRound
                            bgColor="blue.300"
                            colorScheme="blue"
                            size="sm"
                            // icon={<CloseIcon />}
                            icon={<MdKeyboardArrowLeft />}
                            onClick={() => { setIsopenwebview(false) }

                            }
                        >
                            <MdHighlightOff style={{ fontSize: 'large' }} />
                        </Button>
                    </div>
                </div>
                <div>
                    <MainServerDataPacket data={DataPacket} />
                </div>

            </div>
        )
    }




    // const toast = useToast();
    let packetComponent = null;
    if (messageData) {
        console.log("messagedata", messageData);
        if (typeof messageData === "string") {
            packetComponent = <TextComp data={messageData} />
        } else if (messageData.hasOwnProperty("dataPacket")) {
            if (isopenwebview) {
                packetComponent = openwebview(messageData.dataPacket)
            } else if (!isopenwebview) {
                packetComponent = <ClickButtonTemp data={messageData} DataPacket={messageData.dataPacket} />
            }
        } else if (messageData.hasOwnProperty("text") && !messageData.hasOwnProperty("suggestions")) {
            packetComponent = <TextComp data={messageData.text} />
        } else if (messageData.hasOwnProperty("text") && messageData.hasOwnProperty("suggestions")) {
            packetComponent = <ButtonCard data={messageData} />
        } else if (messageData.hasOwnProperty("richCard")) {
            if (messageData.richCard?.hasOwnProperty("standaloneCard")) {
                packetComponent = <StandaloneCard data={messageData.richCard.standaloneCard} />
            } else if (messageData.richCard?.hasOwnProperty("carouselCard")) {
                packetComponent = <CarouselCard data={messageData.richCard.carouselCard} />
            }
        }
        // packetComponent += <DateComp messageData={messageData} />
        return (<div width="100%" p={2} >
            <div>
                {packetComponent}
            </div>
            <DateComp messageData={messageData} />
        </div>)

    } else {
        return (<></>)
    }

}

export default GoogleMessageDataPacket;