import React from 'react'
import { Navbar, Dropdown } from 'react-bootstrap';
import { useState, useEffect } from 'react'
import './Header.css'
import agent from '../../../assets/images/agent.png'
import LogOut from '../../pages/loginPage/LogOut';
import ChangeName from './ChangeName';
import ChangePassword from './ChangePassword';
import { useChat } from '../../context/chat/chatContext';





const Header = () => {
    const [changeNameModal, setChangeNameModal] = useState(false);
    const [changePasswordModal, setChangePasswordModal] = useState(false);
    const [nameChanged, setNameChanged] = useState(false);
    const chatDetails = useChat();
    const [networkStatus, setNetworkStatus] = useState(true);
    const status = [
        {
            id: 1,
            currentStatus: 'online',
        },
        {
            id: 2,
            currentStatus: 'busy',
        }
    ];

    const openNewNameModal = () => setChangeNameModal(true);
    const openNewPasswordModal = () => setChangePasswordModal(true);
    // 
    useEffect(() => {
        window.addEventListener('offline', (event) => {
            setNetworkStatus(false)
        });

        window.addEventListener('online', (event) => {
            setNetworkStatus(true)
        });

        return function cleanupListener() {
            window.removeEventListener('online', setNetworkStatus(true))
            window.removeEventListener('offline', setNetworkStatus(false))
        }

    }, [])


    const handleStatusChange = (index) => {
        const color = status.find((statid) => (statid.id === index) && (statid.currentStatus))
        if (networkStatus) {
            console.log('online', networkStatus);
            chatDetails.handelChangeStatus(color.currentStatus)
        } else {
            console.log('offline', networkStatus);
        }
    }



    return (
        <div className="agent-nav-bar">

            <Navbar className='nav-bar-1'  >
                {/* <Navbar.Brand style={{ minWidth: '60%' }}> */}
                <span className="dot" style={chatDetails.currentState === "online" && networkStatus === true ? { 'backgroundColor ': 'green' } : chatDetails.currentState === "busy" || networkStatus === false ? { 'backgroundColor': 'red' } : { 'backgroundColor': 'white' }}></span>
                <img
                    alt=""
                    src={agent}
                    width="30"
                    height="30"
                    className="d-inline-block align-top"
                    style={{
                        borderRadius: "100%", marginLeft: '7px', marginRight: '7px'
                    }}
                />{' '}
                {!nameChanged ? <div className="agent-username"
                >
                    {sessionStorage.getItem('agentName') !== 'undefined' ? sessionStorage.getItem('agentName') : sessionStorage.getItem('defaultAgentName')}
                </div> :
                    <div className="agent-username"
                    >{sessionStorage.getItem('newAgentName')}
                    </div>}

                <div className="header-spacer dropdown-btn"></div>
                <Dropdown className="float-right">
                    <Dropdown.Toggle split variant="#1f93ff" className='dropdown-btn' id="dropdown-split-basic" />

                    <Dropdown.Menu className='dropdown-menu'>

                        <Dropdown.Divider />
                        <Dropdown id="dropdown-split-basic" style={{ marginLeft: '15px', color: '#3fa3ff' }}>
                            Change Status
                            {status.map((stat) => (<Dropdown.Item key={stat.id} onClick={() => handleStatusChange(stat.id)} >{stat.currentStatus}</Dropdown.Item>))}</Dropdown>
                        <Dropdown.Divider />
                        <Dropdown.Item onClick={openNewNameModal} style={{ color: 'black !important' }} >change name</Dropdown.Item>
                        <Dropdown.Item onClick={openNewPasswordModal} style={{ color: 'black !important' }}>change password</Dropdown.Item>
                        <Dropdown.Item style={{ color: 'black !important' }}><LogOut /></Dropdown.Item>

                    </Dropdown.Menu>
                </Dropdown>
                {/* </Navbar.Brand> */}
            </Navbar>

            {changeNameModal && <ChangeName setChangeNameModal={setChangeNameModal} onClose={() => setChangeNameModal(false)} setNameChanged={setNameChanged} changeNameModal={changeNameModal} />}
            {changePasswordModal && <ChangePassword onHide={() => setChangePasswordModal(false)} changePasswordModal={changePasswordModal} />}
        </div >
    )
}

export default Header
