import React from 'react'
import './ChatList.css'
import endUser from '../../../assets/images/endUser.png';
import WEB from '../../../assets/images/WEB.png';
import whatsApp from '../../../assets/images/whatsapp.png'
import facebook from '../../../assets/images/facebook.png'
import twitter from '../../../assets/images/twitter.png';
import google from '../../../assets/images/google.png'
import { convertUTCDateToLocalDateV2 } from '../../utility/utility'
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { useChat } from '../../context/chat/chatContext';

const Chats = ({ user }) => {

    const chat = useChat();
    const newConvoId = chat.newConversationId;
    let priorityIndicator;
    if (user.conversation.additionalAttributes.priority) {
        priorityIndicator = user.conversation.additionalAttributes.priority;
    }

    const isNewConversations = (conversationArr, prevId) => {
        for (let i = 0; i < conversationArr.length; i++) {
            if (conversationArr[i] === prevId) {
                return true;
            }
        }
        return false;
    }

    const isNewMessage = (conversationArr, prevId) => {
        for (let i = 0; i < conversationArr.length; i++) {
            if (conversationArr[i] === prevId) {
                return true;
            }
        }
        return false;
    }


    const getChannelTypeImage = (channelType) => {
        switch (channelType) {
            case 'WHATSAPP':
                return (<img src={whatsApp} alt="" />)
            case 'FACEBOOK':
                return (<img src={facebook} alt="" />)
            case 'WEBCHAT':
            case 'WEB_CHANNEL':
                return (<img src={WEB} alt="" />)
            case 'TWITTER':
                return (<img src={twitter} alt="" />)
            case 'GOOGLE_MESSAGES':
                return (<img src={google} alt="" />)
            default:
                return (<img src={endUser} alt="" />)
        }
    }




    const getPriorityIndicator = (priorityIndicator) => {
        switch (priorityIndicator) {
            case 1:
                return <Tooltip id="button-tooltip-2" >Very High</Tooltip>;
            case 2:
                return <Tooltip id="button-tooltip-2" >High</Tooltip>;
            case 3:
                return <Tooltip id="button-tooltip-2" >Medium</Tooltip>;
            case 4:
                return <Tooltip id="button-tooltip-2" >Low</Tooltip>;
            case 5:
                return <Tooltip id="button-tooltip-2" >Very Low</Tooltip>;
            default:
                return ''
        }
    }


    // console.log(lastSeenTime(user.conversation.createdAt, user.conversation.messageFrom), 'lastseen time');



    return (
        <>

            <div
                className={`conversation activity-color${chat.lastSeenTime(user.conversation.createdAt, user.conversation.lastMsgTimeFromAgentOrSystem, user.conversation.messageFrom)} ${user.conversation.id === chat.conversationsId && "selected"}`}
                onClick={() => chat.selectChat(user.conversation.id)} >
                {getChannelTypeImage(user.conversation.additionalAttributes.channelType)}
                <div className={isNewConversations(newConvoId, user.conversation.id) ? 'title-text-unread' : 'title-text'}>
                    <OverlayTrigger
                        placement="right"
                        overlay={getPriorityIndicator(priorityIndicator)}
                    >
                        {priorityIndicator && <div className={`priority-indicator status${priorityIndicator}`}></div>}
                    </OverlayTrigger>
                    {user.sender.name}
                </div>
                <div className={isNewConversations(newConvoId, user.conversation.id) ? 'new-created-date' : "created-date"}>
                    {convertUTCDateToLocalDateV2(user.conversation.createdAt)}
                </div>
                <div className={isNewConversations(newConvoId, user.conversation.id) || isNewMessage(chat.newMsgConvoId, user.conversation.id) ? 'new-conversation-message' : 'conversation-message'}>
                    {/* {user.conversation.messageFrom !== undefined && user.conversation.messageFrom === "CONTACT" && <FontAwesomeIcon style={{ fontSize: '12px', color: 'red', marginRight: '0.2rem' }} icon={faCopyright} />} */}
                    {user.conversation.lastmessage}
                </div>



            </div >
        </>
    )
}



export default Chats