import React, { useCallback } from 'react'
import ChatBubble from '../../../chatComponents/chatBox/ChatBubble'
import './botCallHistory.css'

const BotChatBox = () => {
    const setRef = useCallback(node => {
        if (node) {
            node.scrollIntoView(false)
        }
    }, []);
    return (
        <div className="bot-chat-box" >
            {console.log("chat bubble")}
            <ChatBubble setRef={setRef} twilioCallStyle={{ height: "67vh" }} />
        </div>
    )
}

export default BotChatBox