import Linkify from 'linkify-react'
import React, { useEffect } from 'react'
import DataPacket from '../../dataPackets/DataPacketv2'
import { formatDateTimeTicket } from '../../utility/utility'
import { useChat } from '../../context/chat/chatContext';
import Loading1 from '../../pages/loading/Loading1'
import "./chatBox.css"
import Sending from '../../pages/loading/Sending';

const ChatBubble = ({ setRef, channelType, twilioCallStyle }) => {
    const chat = useChat()
    // console.log(lastChat)
    const compare = (latestMsg) => {
        const lastChat = chat.prevChats.find((chats, index, arr) => {
            return index === arr.length - 1
        })
        // console.log("prev", lastChat, "latest", latestMsg)
        if (lastChat?.data?.text === latestMsg) {
            return true
        } else if (lastChat?.data?.text === undefined) {

            return false
        } else if (latestMsg === "") {
            return true
        } else if (lastChat?.from !== "AGENT") {
            return true
        } else {
            return false
        }
    }
    // console.log("message", compare(chat.newMsgSending.newMsg))

    return (
        <div className='chat-container scroller' style={twilioCallStyle} >
            {chat.prevChats.length > 0 ? <div>
                {chat.prevChats?.map((message, index) => (
                    <div key={index}>
                        {message.from === 'USER' ?
                            <div className="message-row other-message" key={index} ref={setRef}>
                                <div className="message-content" >
                                    <Linkify className="message-text" tagName="div" options={{ target: "_blank" }} >{message.data.text}</Linkify>
                                    <div className="message-time-sender" style={{ zIndex: '-1' }}>
                                        {formatDateTimeTicket(message.date, "false")}
                                        {/* {convertUTCDateToLocalDateV2(message.date, true)} */}
                                    </div>
                                </div>
                            </div>
                            :
                            message.from === "BOT" ?
                                <div className="message-row bot-message" key={index} ref={setRef}>
                                    <div className="message-content">
                                        {message.data.text === undefined ?
                                            <div className='from-msg-avatar'>
                                                
                                                {channelType?.conversation &&
                                                    <div className="message-text">
                                                        <DataPacket

                                                            channelType={channelType.conversation.additionalAttributes.channelType}
                                                            data={message.data} />

                                                    </div>

                                                }
                                                {!channelType?.conversation && <div className="message-text" >{message.data}</div>}
                                                {/* <Avatar color={Avatar.getRandomColor('sitebase', ['#3fa3ff', '#03a9f4'])} name={message.from} size="40" className='new-avatar' round={true} /> */}
                                            </div>
                                            :
                                            <div className="bot-message-section">
                                                <Linkify className="message-text" tagName="div" options={{ target: "_blank" }} >{message.data.text}</Linkify>
                                            </div>
                                        }
                                        <div className="message-time" key={index} style={{ zIndex: '-1' }}>
                                            {formatDateTimeTicket(message.date, "false")}
                                            {/* {differenceday(formatDateTimeTicket(message.date, "business"))} */}
                                            {/* {convertUTCDateToLocalDateV2(message.date, true)} */}
                                        </div>
                                    </div>
                                </div>
                                :
                                message.from === "AGENT" && message?.agentDetails?.isSystemMessage === undefined ?
                                    <div className={`message-row ${message?.agentDetails?.agentID === parseInt(sessionStorage.getItem('agentId')) ? 'you-message' : 'manager-message'}`} key={index} ref={setRef}>
                                        <div className="message-content">
                                            {/* {console.log('message text', message.data.text)} */}
                                            {message.data.text !== undefined ?
                                                <Linkify className="message-text" tagName="div" options={{ target: "_blank" }} >{message.data.text}</Linkify>
                                                :
                                                <Linkify className="message-text" tagName="div" options={{ target: "_blank" }} >{message.data}</Linkify>
                                            }
                                            <div className="message-time" style={{ zIndex: '-1' }}>
                                                {formatDateTimeTicket(message.date, "false")}
                                                {/* {differenceday(formatDateTimeTicket(message.date, "business"))} */}
                                                {/* {convertUTCDateToLocalDateV2(message.date, true)} */}
                                            </div>
                                        </div>
                                    </div>
                                    :
                                    message.from === "AGENT" && message?.agentDetails?.isSystemMessage &&
                                    <div style={{ display: 'flex', justifyContent: 'center' }}>
                                        <div className="message-row admin-message " key={index} ref={setRef}>
                                            <div className="message-content">
                                                <Linkify className="message-text" tagName="div" options={{ target: "_blank" }} >{message.data.text}</Linkify>
                                                <div className="message-time-admin" style={{ zIndex: '-1' }}>
                                                    {formatDateTimeTicket(message.date, "false")}
                                                    {/* {differenceday(formatDateTimeTicket(message.date, "business"))} */}
                                                    {/* {convertUTCDateToLocalDateV2(message.date, true)} */}
                                                </div>
                                            </div>
                                        </div>
                                    </div>}
                    </div>
                ))}
                {chat.newMsgSending.sending &&
                    <div className="message-row you-message" ref={setRef}>
                        <div className="message-content">
                            <Sending />
                            <div className="message-time" style={{ marginTop: '0.5rem' }}>
                            </div>
                        </div>
                    </div>}
            </div>
                :
                <div className="loading-container">

                    <Loading1 />
                </div>
            }

        </div>
    )
}

export default ChatBubble