import React from 'react'
import { IoChatbubblesSharp } from "react-icons/io5";
import { useStartDevice } from '../../../context/startDevice/startDeviceContext';
import { useTime } from '../../../context/callTime/calltimeContext'
import './botCallHistory.css'
const BotCallHistory = ({ setOpenBotConvo, openBotConvo, pause, onOpen }) => {
  const inbound = useStartDevice();
  const time = useTime();
  const handleOpenBotConvo = () => {
    onOpen()
  }
  return (
    <div className="widget-container" onClick={handleOpenBotConvo}  >
      <IoChatbubblesSharp className="chat-icon" />
    </div>
  )
}

export default BotCallHistory