import React, { useEffect, useState, useRef } from 'react';
// import Peer from "simple-peer";
import styled from "styled-components";
import { useParams } from "react-router-dom"
import { config } from '../../../config/config';
import axios from 'axios'
import Peer from 'peerjs'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMicrophone, faMicrophoneSlash, faPhoneSlash } from '@fortawesome/free-solid-svg-icons'
import { Button, Col, Row, Container } from 'react-bootstrap';
import clientCalling from "./phone.png";
import finishedCall from "./hang-up.png"
import goodDay from "../../../assets/images/sunshine.png";
import { useNotify } from '../../context/notification/notifyContext';


const Video = styled.video`
  border: 1px solid blue;
  width: 100%;
    height: 80vh;
    
    background: black;
`;
const AgentCall = styled.video`
  border: 1px solid blue;
  width: 1px;
  height: 1px;
  
  background: black;
`;

const ClientCall = ({ socketMain }) => {
    const [tglmute, setTglmute] = useState(true);
    const [agentEndedCall, setAgentEndedCall] = useState(false)
    const [agentMute, setAgentMute] = useState(false)
    const userCall = useRef();
    const remoteCall = useRef();
    const [callEnded, setCallEnded] = useState(false)
    const notify = useNotify();
    let { type, token } = useParams();

    useEffect(() => {
        socketMain.emit('agentVideo', token)
        socketMain.on('agentNotification', (res => {
            console.log('agent mute res', res)
            if (res.agentDetails.mute) {
                setAgentMute(true)
                // notify.agentMuteNotify()
            } else {
                setAgentMute(false)
                // notify.agentUnmuteNotify()
            }
            if (res.agentDetails.endCall) {
                setAgentEndedCall(true)
                // notify.agentEndedCall()
            }
        }))
    }, [token])




    useEffect(() => {
        callAgent()
    }, []);

    const sendNotification = () => {
        const body = {
            endCall: callEnded,
            mute: tglmute,
            token: token,
        }
        console.log('notification body', body)
        console.log(body.message, body.token)
        axios.post(`${config.baseUrl}/clientNotification`,
            body,
            { headers: { 'Content-Type': 'application/json' } }
        ).then((res) => {
            console.log(res)
        }).catch(err => console.log(err))
    }


    var getUserMedia = navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia || navigator.mediaDevices.mozGetUserMedia;

    let constraints = {
        audio: true,
        video: false
    }
    const callAgent = () => {
        const peer = new Peer()

        console.log(peer);
        // waiting fro agent msg

        peer.on('open', (id) => {
            console.log('connection with id', id);
            getUserMedia(constraints).then(stream => {
                console.log('client streaming', stream);
                userCall.current.srcObject = stream;
                userCall.current.srcObject.getVideoTracks().forEach(t => t.enabled = false);
                console.log(token);
                let call = peer.call(token, stream);
                console.log('after call token', token)
                console.log(stream);

                call.on('stream', remoteStream => {
                    remoteCall.current.srcObject = remoteStream;
                })

            }).catch(err => console.log(err))
        })

        peer.on('error', (err) => {
            console.log(err.type, 'peer error');
            if (err.type === "network") {
                setTimeout(function () {
                    console.log('reconnecting');
                    peer.reconnect();
                }, 1000);
            } else if (err.type === "socket-error") {
                setTimeout(function () {
                    callAgent();
                }, 1000);
            }
            // let reconnect = false;
            // let runNext = true;

            // // const FATAL_ERRORS = ['invalid-id', 'invalid-key', 'network', 'ssl-unavailable', 'server-error', 'socket-error', 'socket-closed', 'unavailable-id', 'webrtc'];
            // const FATAL_ERRORS = ['network'];
            // if (FATAL_ERRORS.includes(err.type)) {
            //     const reconnectTimer = setInterval(() => {
            //         if (!reconnect && runNext) {
            //             runNext = false;
            //             const peer = new Peer()

            //             let constraints = {
            //                 audio: true,
            //                 video: false
            //             }
            //             peer.on('open', (id) => {
            //                 console.log('reconnecting with id:', id);
            //                 // setReconnect(true);
            //                 reconnect = true;
            //                 getUserMedia(constraints).then(stream => {
            //                     console.log('reconnecting client streaming', stream);
            //                     userCall.current.srcObject = stream;
            //                     // userVideo.current.play();
            //                     console.log(token);
            //                     let call = peer.call(token, stream);
            //                     console.log(call);
            //                     call.on('stream', function (stream) {
            //                         console.log(stream);
            //                         remoteCall.current.srcObject = stream;
            //                         console.log('reconnecting remote stream', stream);
            //                     })

            //                 }).catch(err => console.log(err))
            //             })
            //             peer.on('error', (err) => {
            //                 // setReconnect(false);
            //                 reconnect = false;
            //             })
            //             if (reconnect) {
            //                 clearInterval(reconnectTimer);
            //             } else {
            //                 runNext = true;
            //             }

            //         }
            //     }, 1000)

            // } else {
            //     console.log('Non fatal error: ', err.type);
            // }
        })


    }


    var mute = () => {
        console.log('inside mute');
        console.log(userCall);
        if (userCall.current.srcObject != null && userCall.current.srcObject.getVideoTracks().length > 0) {

            userCall.current.srcObject.getAudioTracks().forEach(t => t.enabled = !t.enabled);
        }
        // if (tglmute) {
        //     notify.clientMute()
        // } else {
        //     notify.clientUnmute()
        // }
        setTglmute(!tglmute);
        sendNotification()
    }



    const endStream = () => {
        userCall.current.srcObject.getTracks().forEach((track) => {
            track.stop();
        });
        setCallEnded(true)
        const body = {
            endCall: true,
            token: token,
        }
        axios.post(`${config.baseUrl}/clientNotification`,
            body,
            { headers: { 'Content-Type': 'application/json' } }
        ).then((res) => {
            console.log(res)
        }).catch(err => console.log(err))
    }

    let UserCall;
    let RemoteCall;

    UserCall = (
        <Video playsInline
            muted
            ref={userCall}
            poster={callEnded ? finishedCall : clientCalling}
            autoPlay />

    );

    RemoteCall = (
        <AgentCall playsInline ref={remoteCall} autoPlay />
    )



    return (
        <>
            {callEnded ?
                <div style={{ textAlign: 'center', marginTop: '50px' }}>
                    <h1 >You Left The Meeting</h1>
                    <h1>Thank You</h1>
                    <p>Have A Nice Day</p>
                    <img src={goodDay} alt="no preview available" style={{ width: '280px' }} />
                </div>
                :
                <Container fluid='xs' style={{ backgroundColor: "#202124", height: "100vh" }}>
                    <Col xs={12} style={{ justifyContent: 'center', textAlign: 'center' }}>
                        {RemoteCall}
                        {agentEndedCall && <h5 style={{ paddingLeft: '27px', color: 'aliceblue' }}>Agent has ended the call</h5>}
                        {agentMute && <FontAwesomeIcon className="clientMute" icon={faMicrophoneSlash} />}
                        {UserCall}
                    </Col>
                    <Container>

                        <Row xs={12}>
                            <Col>

                                <Button
                                    style={{ marginRight: '45px' }}
                                    onClick={mute}
                                    variant={tglmute ? 'primary' : 'danger'}
                                >
                                    {tglmute ? <FontAwesomeIcon style={{ fontSize: 'xxx-large' }} icon={faMicrophone} /> : <FontAwesomeIcon style={{ fontSize: 'xxx-large' }} icon={faMicrophoneSlash} />}
                                </Button>

                            </Col>
                            <Col></Col>
                            <div>
                                <Button onClick={endStream} variant="danger" >
                                    <FontAwesomeIcon style={{ fontSize: 'xxx-large' }} icon={faPhoneSlash} />
                                </Button>

                            </div>

                        </Row>
                    </Container>


                </Container>

            }
        </>
    )
}

export default ClientCall
